/* ============================================================================
   TURNIER BEARBEITEN PAGE - Dashboard Style
   ============================================================================ */

/* =====================
   ANIMATIONS
   ===================== */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* =====================
   BASE CONTAINER
   ===================== */
.turnier-edit {
  min-height: calc(100vh - 80px);
  background: #000000;
  color: #ffffff;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  padding: 1.5rem;
  max-width: 1100px;
  margin: 0 auto;
  animation: fadeIn 0.6s ease-out;
}

/* =====================
   LOADING STATE
   ===================== */
.loading-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 50vh;
}

.loading-spinner {
  width: 60px;
  height: 60px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-top: 4px solid #ff3333;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
  box-shadow: 0 0 20px rgba(255, 51, 51, 0.3);
}

.loading-box p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
}

/* =====================
   HEADER
   ===================== */
.edit-header {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  padding: 1rem 1.5rem;
  border-radius: 12px;
  border: 1px solid rgba(255, 51, 51, 0.2);
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
}

.edit-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, rgba(255, 51, 51, 0.5), transparent);
  animation: shimmer 3s infinite;
}

.header-title {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.header-icon {
  font-size: 2rem;
  filter: drop-shadow(0 0 10px rgba(255, 51, 51, 0.5));
}

.header-text h1 {
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0;
  background: linear-gradient(135deg, #ff3333, #ff6b35);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.header-text p {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.6);
  margin: 0;
}

.header-actions {
  display: flex;
  gap: 0.75rem;
}

.btn-back {
  padding: 0.6rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 8px;
  color: #fff;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-back:hover {
  border-color: #ff3333;
  background: rgba(255, 51, 51, 0.1);
}

.btn-save {
  padding: 0.6rem 1.5rem;
  background: linear-gradient(135deg, #ff3333, #cc0000);
  border: none;
  border-radius: 8px;
  color: #ffffff;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(255, 51, 51, 0.3);
}

.btn-save:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 51, 51, 0.4);
}

.btn-save:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* =====================
   MESSAGES
   ===================== */
.msg {
  padding: 0.75rem 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  animation: fadeInUp 0.3s ease-out;
}

.msg-error {
  background: rgba(255, 51, 51, 0.15);
  border: 1px solid rgba(255, 51, 51, 0.4);
  color: #ff6b6b;
}

.msg-success {
  background: rgba(76, 175, 80, 0.15);
  border: 1px solid rgba(76, 175, 80, 0.4);
  color: #4CAF50;
}

/* =====================
   TAB NAVIGATION
   ===================== */
.tab-nav {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1rem;
  padding: 0.5rem;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  border: 1px solid rgba(255, 51, 51, 0.15);
  flex-wrap: wrap;
}

.tab-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  background: transparent;
  border: none;
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  flex: 1;
  min-width: 120px;
}

.tab-btn:hover {
  background: rgba(255, 51, 51, 0.1);
  color: rgba(255, 255, 255, 0.9);
}

.tab-btn.active {
  background: linear-gradient(135deg, rgba(255, 51, 51, 0.2), rgba(255, 107, 53, 0.2));
  color: #ff3333;
  border: 1px solid rgba(255, 51, 51, 0.4);
  box-shadow: 0 4px 15px rgba(255, 51, 51, 0.2);
  font-weight: 600;
}

.tab-icon {
  font-size: 1.1rem;
}

/* =====================
   FORM CONTAINER
   ===================== */
.form-container {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 16px;
  padding: 1.5rem;
  animation: fadeInUp 0.5s ease-out;
}

/* =====================
   FORM GROUPS
   ===================== */
.form-section {
  margin-bottom: 1.5rem;
}

.form-section h3 {
  font-size: 1.1rem;
  margin: 0 0 1rem 0;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid rgba(255, 51, 51, 0.2);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, #ff3333, #ff6b35);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.form-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  margin-bottom: 1rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: 0.75rem 1rem;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 8px;
  color: #fff;
  font-size: 0.95rem;
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #ff3333;
  box-shadow: 0 0 15px rgba(255, 51, 51, 0.2);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.form-group select option {
  background: #1a1a2e;
  color: #fff;
}

.form-group textarea {
  min-height: 100px;
  resize: vertical;
}

/* Full Width Input */
.form-group.full-width {
  grid-column: 1 / -1;
}

/* =====================
   KATEGORIEN GRID
   ===================== */
.kategorien-section h3 {
  font-size: 1.1rem;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #ff3333, #ff6b35);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.kategorien-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 0.75rem;
}

.kategorie-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.kategorie-item:hover {
  border-color: rgba(255, 51, 51, 0.4);
  background: rgba(255, 51, 51, 0.05);
}

.kategorie-item.selected {
  background: rgba(255, 51, 51, 0.15);
  border-color: #ff3333;
}

.kategorie-item input[type="checkbox"] {
  width: 18px;
  height: 18px;
  accent-color: #ff3333;
}

.kategorie-item .icon {
  font-size: 1.25rem;
}

.kategorie-item .label {
  flex: 1;
  font-size: 0.9rem;
  color: #fff;
}

/* =====================
   TOURNAMENT MODE SELECT
   ===================== */
.mode-select {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.mode-option {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.mode-option:hover {
  border-color: rgba(255, 51, 51, 0.4);
}

.mode-option.selected {
  background: rgba(255, 51, 51, 0.15);
  border-color: #ff3333;
}

.mode-option input[type="radio"] {
  margin-top: 0.25rem;
  accent-color: #ff3333;
}

.mode-info h4 {
  color: #fff;
  margin: 0 0 0.25rem 0;
  font-size: 0.95rem;
}

.mode-info p {
  color: rgba(255, 255, 255, 0.6);
  margin: 0;
  font-size: 0.85rem;
}

/* Compact Mode Options fr Grid-Layout */
.mode-option.compact {
  padding: 0.6rem 0.75rem;
  margin-bottom: 0.4rem;
}

.mode-option.compact:last-child {
  margin-bottom: 0;
}

.mode-option.compact .mode-info h4 {
  font-size: 0.8rem;
  margin-bottom: 0.15rem;
}

.mode-option.compact .mode-info p {
  font-size: 0.7rem;
}

/* =====================
   STATUS SELECT
   ===================== */
.status-options {
  display: flex;
  gap: 0.75rem;
  flex-wrap: wrap;
}

.status-btn {
  padding: 0.6rem 1.25rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.status-btn:hover {
  border-color: rgba(255, 51, 51, 0.4);
  color: #fff;
}

.status-btn.active {
  background: linear-gradient(135deg, #ff3333, #cc0000);
  border-color: #ff3333;
  color: #fff;
  font-weight: 600;
}

/* =====================
   MODE BOX
   ===================== */
.mode-box {
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 8px;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.2);
}

.mode-box h4 {
  color: #ff3333;
  margin-bottom: 0.5rem;
  font-size: 0.95rem;
}

.mode-box p {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.75rem;
  margin-bottom: 0.75rem;
}

/* =====================
   PREISE TAB STYLES
   ===================== */
.info-box {
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1.5rem;
}

.info-box p {
  margin: 0;
  color: rgba(255, 255, 255, 0.8);
}

.info-box.info-blue {
  background: rgba(59, 130, 246, 0.1);
  border: 1px solid rgba(59, 130, 246, 0.3);
}

.info-box.info-red {
  background: rgba(255, 51, 51, 0.1);
  border: 1px solid rgba(255, 51, 51, 0.3);
}

.form-hint {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.8rem;
  margin-top: 0.25rem;
}

.subsection-title {
  margin-top: 2rem;
  margin-bottom: 1rem;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.price-preview {
  background: rgba(0, 0, 0, 0.3);
  padding: 0.75rem;
  border-radius: 8px;
  font-size: 0.9rem;
  border: 1px solid rgba(255, 51, 51, 0.2);
}

.price-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.3rem;
  color: rgba(255, 255, 255, 0.8);
}

.price-row.price-total {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 0.3rem;
  margin-top: 0.3rem;
  margin-bottom: 0;
}

.price-sum {
  color: #2ed573 !important;
}

/* =====================
   RESPONSIVE
   ===================== */
@media (max-width: 768px) {
  .edit-header {
    flex-direction: column;
    align-items: stretch;
  }

  .header-actions {
    justify-content: flex-end;
  }

  .tab-nav {
    flex-direction: column;
  }

  .tab-btn {
    width: 100%;
    justify-content: center;
  }

  .form-row {
    grid-template-columns: 1fr;
  }

  .kategorien-grid {
    grid-template-columns: 1fr;
  }
}
