/* =====================
   WINNER SCREEN / SIEGEREHRUNG
   ===================== */
.winner-screen-overlay-jp {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(180deg, #0d0a07 0%, #1a1510 50%, #0d0a07 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  overflow: hidden;
  cursor: pointer;
  animation: fadeIn 0.3s ease-out;
}

/* Corner Decorations */
.winner-corner-jp {
  position: absolute;
  width: 80px;
  height: 80px;
  pointer-events: none;
  animation: cornerFadeIn 1s ease-out;
}

.winner-corner-jp.topLeft { top: 20px; left: 20px; }
.winner-corner-jp.topRight { top: 20px; right: 20px; }
.winner-corner-jp.bottomLeft { bottom: 20px; left: 20px; }
.winner-corner-jp.bottomRight { bottom: 20px; right: 20px; }

.winner-corner-jp .corner-h {
  position: absolute;
  height: 3px;
  width: 100%;
  background: linear-gradient(90deg, #d4af37, transparent);
  box-shadow: 0 0 10px rgba(212, 175, 55, 0.5);
}

.winner-corner-jp.topRight .corner-h,
.winner-corner-jp.bottomRight .corner-h {
  background: linear-gradient(270deg, #d4af37, transparent);
}

.winner-corner-jp.topLeft .corner-h,
.winner-corner-jp.topRight .corner-h { top: 0; }
.winner-corner-jp.bottomLeft .corner-h,
.winner-corner-jp.bottomRight .corner-h { bottom: 0; }

.winner-corner-jp .corner-v {
  position: absolute;
  width: 3px;
  height: 100%;
  background: linear-gradient(180deg, #d4af37, transparent);
  box-shadow: 0 0 10px rgba(212, 175, 55, 0.5);
}

.winner-corner-jp.bottomLeft .corner-v,
.winner-corner-jp.bottomRight .corner-v {
  background: linear-gradient(0deg, #d4af37, transparent);
}

.winner-corner-jp.topLeft .corner-v,
.winner-corner-jp.bottomLeft .corner-v { left: 0; }
.winner-corner-jp.topRight .corner-v,
.winner-corner-jp.bottomRight .corner-v { right: 0; }

.winner-corner-jp .corner-dot {
  position: absolute;
  width: 12px;
  height: 12px;
  background: #d4af37;
  border-radius: 2px;
  transform: rotate(45deg);
  box-shadow: 0 0 15px rgba(212, 175, 55, 0.8);
}

.winner-corner-jp.topLeft .corner-dot { top: -4px; left: -4px; }
.winner-corner-jp.topRight .corner-dot { top: -4px; right: -4px; }
.winner-corner-jp.bottomLeft .corner-dot { bottom: -4px; left: -4px; }
.winner-corner-jp.bottomRight .corner-dot { bottom: -4px; right: -4px; }

@keyframes cornerFadeIn {
  from { opacity: 0; transform: scale(0.5); }
  to { opacity: 1; transform: scale(1); }
}

/* Floating Kanji Background */
.winner-bg-kanji {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  pointer-events: none;
}

.winner-bg-kanji .floating-kanji {
  position: absolute;
  color: rgba(212, 175, 55, 0.06);
  top: 100%;
  animation: floatUpWinner 15s linear infinite;
}

@keyframes floatUpWinner {
  0% { transform: translateY(0) rotate(0deg); opacity: 0.06; }
  50% { opacity: 0.12; }
  100% { transform: translateY(-120vh) rotate(15deg); opacity: 0; }
}

/* Radial Glow */
.winner-glow-jp {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 150%;
  height: 150%;
  background: radial-gradient(ellipse at center,
    rgba(212, 175, 55, 0.15) 0%,
    transparent 60%);
  animation: pulseGlowWinner 3s ease-in-out infinite;
  pointer-events: none;
}

@keyframes pulseGlowWinner {
  0%, 100% { opacity: 0.5; transform: translate(-50%, -50%) scale(1); }
  50% { opacity: 1; transform: translate(-50%, -50%) scale(1.1); }
}

/* Winner Content */
.winner-content-jp {
  text-align: center;
  z-index: 1;
  animation: winnerContentIn 0.8s ease-out;
  cursor: default;
}

@keyframes winnerContentIn {
  from { opacity: 0; transform: translateY(30px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Winner Header */
.winner-header-jp {
  margin-bottom: 3rem;
}

.winner-kanji {
  display: block;
  font-size: 3rem;
  color: #d4af37;
  text-shadow: 0 0 30px rgba(212, 175, 55, 0.5);
  animation: kanjiPulse 2s ease-in-out infinite;
}

@keyframes kanjiPulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

.winner-title {
  display: block;
