/* Podium Container */
.podium-container-jp {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  gap: 1rem;
  margin: 2rem 0;
}

/* Podium Place */
.podium-place-jp {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 200px;
  animation: podiumRise 0.8s ease-out backwards;
}

.podium-place-jp.first { animation-delay: 0.3s; }
.podium-place-jp.second { animation-delay: 0.5s; }
.podium-place-jp.third { animation-delay: 0.7s; }

@keyframes podiumRise {
  from { opacity: 0; transform: translateY(50px); }
  to { opacity: 1; transform: translateY(0); }
}

.podium-medal {
  font-size: 3rem;
  margin-bottom: 0.5rem;
  animation: medalBounce 2s ease-in-out infinite;
}

.podium-place-jp.first .podium-medal { animation-delay: 0s; }
.podium-place-jp.second .podium-medal { animation-delay: 0.3s; }
.podium-place-jp.third .podium-medal { animation-delay: 0.6s; }

@keyframes medalBounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

.podium-rank {
  font-size: 2rem;
  font-weight: 900;
  margin-bottom: 0.5rem;
}

.podium-place-jp.first .podium-rank { color: #d4af37; text-shadow: 0 0 20px rgba(212, 175, 55, 0.8); }
.podium-place-jp.second .podium-rank { color: #C0C0C0; text-shadow: 0 0 20px rgba(192, 192, 192, 0.8); }
.podium-place-jp.third .podium-rank { color: #cd7f32; text-shadow: 0 0 20px rgba(205, 127, 50, 0.8); }

.podium-name {
  font-size: 1.3rem;
  font-weight: 700;
  color: #fff;
  margin-bottom: 0.25rem;
  max-width: 180px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.podium-place-jp.first .podium-name {
  font-size: 1.6rem;
  color: #d4af37;
  text-shadow: 0 0 20px rgba(212, 175, 55, 0.5);
}

.podium-verein {
  font-size: 0.85rem;
  color: #888;
  margin-bottom: 0.5rem;
  max-width: 180px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.podium-score {
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

