.podium-place-jp.second .podium-score { color: #C0C0C0; }
.podium-place-jp.third .podium-score { color: #cd7f32; }

.podium-empty {
  color: #555;
  font-size: 1rem;
  margin-bottom: 1rem;
}

/* Podium Blocks */
.podium-block {
  width: 100%;
  border-radius: 8px 8px 0 0;
  position: relative;
}

.podium-block::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  border-radius: 8px 8px 0 0;
}

.podium-block.first {
  height: 120px;
  background: linear-gradient(180deg, rgba(212, 175, 55, 0.4) 0%, rgba(212, 175, 55, 0.2) 100%);
  border: 2px solid #d4af37;
  border-bottom: none;
  box-shadow: 0 0 30px rgba(212, 175, 55, 0.3);
