.score-label-jp {
  font-size: 0.7rem;
  color: #888;
  letter-spacing: 2px;
}

.score-value-jp {
  font-size: 2rem;
  font-weight: 900;
  color: #d4af37;
  text-shadow: 0 0 15px rgba(212, 175, 55, 0.5);
}

/* =====================
   DISQUALIFY BUTTON
   ===================== */
.btn-disqualify-jp {
  background: rgba(244, 67, 54, 0.2);
  border: 2px solid #f44336;
  color: #f44336;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: all 0.2s;
}

.btn-disqualify-jp:hover {
  background: rgba(244, 67, 54, 0.4);
  box-shadow: 0 0 15px rgba(244, 67, 54, 0.4);
}

/* =====================
   MODAL OVERLAY & CONTENT
   ===================== */
.modal-overlay-jp {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  animation: fadeIn 0.2s ease-out;
}

.modal-content-jp {
  background: linear-gradient(180deg, #1a1510 0%, #0d0a07 100%);
  border: 2px solid #d4af37;
  border-radius: 16px;
  min-width: 450px;
  max-width: 90vw;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5), 0 0 40px rgba(212, 175, 55, 0.2);
  animation: slideIn 0.3s ease-out;
}

.modal-content-jp.disqualify-modal {
  border-color: #f44336;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5), 0 0 40px rgba(244, 67, 54, 0.2);
}

.modal-content-jp.tiebreaker-modal {
  min-width: 550px;
}

.modal-header-jp {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.25rem 1.5rem;
  background: linear-gradient(180deg, #8B0000, #5c0000);
  border-bottom: 2px solid #d4af37;
  border-radius: 14px 14px 0 0;
  font-weight: 700;
  font-size: 1.1rem;
  color: #fff;
  letter-spacing: 2px;
}

.modal-header-jp.disqualify {
  background: linear-gradient(180deg, #b71c1c, #7f0000);
  border-color: #f44336;
}

.modal-header-jp.tiebreaker {
  background: linear-gradient(180deg, #5c4d00, #3d3400);
  border-color: #d4af37;
}

.modal-icon {
  font-size: 1.5rem;
}

.modal-body-jp {
  padding: 1.5rem;
}

.modal-footer-jp {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding: 1rem 1.5rem;
  border-top: 1px solid rgba(212, 175, 55, 0.2);
}

/* Disqualify Modal Specific */
.disqualify-target {
  background: rgba(244, 67, 54, 0.1);
  border: 1px solid rgba(244, 67, 54, 0.3);
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.target-label {
  color: #888;
  font-size: 0.9rem;
}

.target-name {
  color: #f44336;
  font-weight: 700;
  font-size: 1.2rem;
}

.disqualify-reason-input label {
  display: block;
  color: #c4b89a;
  margin-bottom: 0.75rem;
}

.reason-select-jp {
  width: 100%;
  padding: 0.8rem;
  background: rgba(0, 0, 0, 0.4);
  border: 2px solid rgba(244, 67, 54, 0.4);
  border-radius: 8px;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
}

.reason-select-jp:focus {
  outline: none;
  border-color: #f44336;
}

.reason-input-jp {
  width: 100%;
  padding: 0.8rem;
  background: rgba(0, 0, 0, 0.4);
  border: 2px solid rgba(244, 67, 54, 0.4);
  border-radius: 8px;
  color: #fff;
  font-size: 1rem;
  margin-top: 0.75rem;
}

.confirm-disqualify-btn-jp {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(180deg, #f44336, #c62828);
  border: 2px solid #f44336;
  border-radius: 8px;
  color: #fff;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s;
}

.confirm-disqualify-btn-jp:hover:not(:disabled) {
  box-shadow: 0 4px 20px rgba(244, 67, 54, 0.5);
}

.confirm-disqualify-btn-jp:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Tie-Breaker Modal Specific */
.tiebreaker-info {
  text-align: center;
  margin-bottom: 1.5rem;
  color: #c4b89a;
}

.tiebreaker-info p {
  margin: 0.5rem 0;
}

.tied-score {
  font-size: 2rem;
  font-weight: 900;
  color: #d4af37;
  margin: 1rem 0 !important;
}

.tiebreaker-participants {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.tied-participant {
  background: rgba(212, 175, 55, 0.1);
  border: 2px solid #d4af37;
  border-radius: 8px;
  padding: 1rem 1.5rem;
  text-align: center;
}

.tied-name {
  display: block;
  color: #fff;
  font-weight: 700;
  font-size: 1.1rem;
}

.tied-verein {
  display: block;
  color: #888;
  font-size: 0.85rem;
  margin-top: 0.25rem;
}

.tiebreaker-question {
  text-align: center;
  color: #d4af37;
  font-weight: 700;
  margin: 1.5rem 0;
  font-size: 1.1rem;
}

.tiebreaker-options {
  display: flex;
  gap: 2rem;
  align-items: stretch;
}

.tiebreaker-method {
  flex: 1;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(212, 175, 55, 0.3);
  border-radius: 12px;
  padding: 1.25rem;
  text-align: center;
}

.tiebreaker-method h4 {
  color: #d4af37;
  margin: 0 0 0.5rem;
  font-size: 1.1rem;
}

.tiebreaker-method p {
  color: #888;
  font-size: 0.85rem;
  margin: 0 0 1rem;
}

.tiebreaker-divider {
  display: flex;
  align-items: center;
  color: #555;
  font-weight: 700;
}

.tiebreaker-select {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.tiebreaker-winner-btn {
  padding: 0.75rem 1rem;
  background: rgba(76, 175, 80, 0.2);
  border: 2px solid #4CAF50;
  border-radius: 8px;
  color: #4CAF50;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.tiebreaker-winner-btn:hover {
  background: rgba(76, 175, 80, 0.4);
  box-shadow: 0 0 15px rgba(76, 175, 80, 0.4);
}

.tiebreaker-winner-btn.extra {
  background: rgba(33, 150, 243, 0.2);
  border-color: #2196F3;
  color: #2196F3;
}

.tiebreaker-winner-btn.extra:hover {
  background: rgba(33, 150, 243, 0.4);
  box-shadow: 0 0 15px rgba(33, 150, 243, 0.4);
}

/* =====================
   BACK BUTTON
   ===================== */
.btn-back-jp {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: transparent;
  border: 2px solid #ccc;
  border-radius: 6px;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  margin-right: auto;
}

.btn-back-jp:hover {
  border-color: #d4af37;
  color: #d4af37;
}

/* Show Completed Categories Button */
.btn-show-completed-jp {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.4rem 0.8rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.85rem;
  transition: all 0.2s;
  background: rgba(100, 100, 100, 0.2);
  border: 2px solid #ccc;
  color: #fff;
  white-space: nowrap;
}

.btn-show-completed-jp:hover {
  border-color: #d4af37;
  color: #d4af37;
  background: rgba(100, 100, 100, 0.3);
}

.btn-show-completed-jp.active {
  background: rgba(76, 175, 80, 0.2);
  border-color: #4CAF50;
  color: #4CAF50;
}

.btn-show-completed-jp.active:hover {
  background: rgba(76, 175, 80, 0.3);
}

/* =====================
   SHUFFLE BUTTON
   ===================== */
.btn-shuffle-jp {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.6rem 1.2rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: all 0.2s;
  background: rgba(156, 39, 176, 0.2);
  border: 2px solid #9C27B0;
  color: #9C27B0;
}

.btn-shuffle-jp:hover {
  background: rgba(156, 39, 176, 0.3);
  box-shadow: 0 0 15px rgba(156, 39, 176, 0.4);
}
