/* ============================================================================
   STAFF MANAGER - Dashboard Style (matching MainDashboard)
   ============================================================================ */

/* =====================
   ANIMATIONS
   ===================== */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

@keyframes pulse {
  0%, 100% { opacity: 1; transform: scale(1); }
  50% { opacity: 0.7; transform: scale(1.02); }
}

/* =====================
   BASE CONTAINER
   ===================== */
.staff-manager {
  min-height: calc(100vh - 80px);
  background: #000000;
  color: #ffffff;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  padding: 1.5rem;
  animation: fadeIn 0.6s ease-out;
}

/* =====================
   LOADING STATE
   ===================== */
.staff-manager.loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 50vh;
}

.loading-spinner {
  width: 60px;
  height: 60px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-top: 4px solid #ff3333;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
  box-shadow: 0 0 20px rgba(255, 51, 51, 0.3);
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.staff-manager.loading p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
}

/* =====================
   HEADER
   ===================== */
.staff-header {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  padding: 1rem 1.5rem;
  border-radius: 12px;
  border: 1px solid rgba(255, 51, 51, 0.2);
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
}

.staff-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, rgba(255, 51, 51, 0.5), transparent);
  animation: shimmer 3s infinite;
}

.header-title {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.header-icon {
  font-size: 2rem;
  filter: drop-shadow(0 0 10px rgba(255, 51, 51, 0.5));
}

.header-text h1 {
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0;
  background: linear-gradient(135deg, #ff3333, #ff6b35);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.header-text p {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.6);
  margin: 0;
}

.btn-add {
  padding: 0.6rem 1.25rem;
  background: linear-gradient(135deg, #ff3333, #cc0000);
  border: none;
  border-radius: 8px;
  color: #ffffff;
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(255, 51, 51, 0.2);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.btn-add:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 51, 51, 0.4);
  background: linear-gradient(135deg, #ff4444, #dd0000);
}

/* =====================
   STATS CARDS (Compact)
   ===================== */
.staff-stats {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1rem;
  flex-wrap: wrap;
}

.stat-card {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.15), rgba(37, 99, 235, 0.15));
  backdrop-filter: blur(20px);
  border: 1px solid rgba(59, 130, 246, 0.3);
  border-radius: 8px;
  padding: 0.5rem 0.75rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.stat-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(59, 130, 246, 0.25);
  border-color: rgba(59, 130, 246, 0.5);
}

.stat-card .stat-icon {
  font-size: 1rem;
  filter: drop-shadow(0 0 5px rgba(255, 51, 51, 0.5));
}

.stat-card .stat-info {
  display: flex;
  align-items: center;
  gap: 0.4rem;
}

.stat-card .stat-value {
  font-size: 1.1rem;
  font-weight: 700;
  color: #ff3333;
  text-shadow: 0 0 8px rgba(255, 51, 51, 0.5);
}

.stat-card .stat-label {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.7);
}

.stat-card.verfuegbar { border-left: 3px solid #4CAF50; }
.stat-card.einsatz { border-left: 3px solid #ff3333; }
.stat-card.pause { border-left: 3px solid #f7931e; }

/* =====================
   TABS
   ===================== */
.staff-tabs {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1rem;
  padding: 0.5rem;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  border: 1px solid rgba(255, 51, 51, 0.15);
}

.tab-btn {
  flex: 1;
  padding: 0.75rem 1.25rem;
  background: transparent;
  border: none;
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.tab-btn:hover {
  background: rgba(255, 51, 51, 0.1);
  color: rgba(255, 255, 255, 0.9);
}

.tab-btn.active {
  background: linear-gradient(135deg, rgba(255, 51, 51, 0.2), rgba(255, 107, 53, 0.2));
  color: #ff3333;
  border: 1px solid rgba(255, 51, 51, 0.4);
  box-shadow: 0 4px 15px rgba(255, 51, 51, 0.2);
  font-weight: 600;
}

/* =====================
   FILTER BUTTONS
   ===================== */
.staff-filter {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
  padding: 0.75rem 1rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 12px;
  flex-wrap: wrap;
}

.filter-btn {
  padding: 0.5rem 0.85rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.8rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
}

.filter-btn:hover {
  background: rgba(255, 51, 51, 0.1);
  border-color: rgba(255, 51, 51, 0.3);
  color: rgba(255, 255, 255, 0.9);
}

.filter-btn.active {
  background: var(--filter-color, #ff3333);
  border-color: var(--filter-color, #ff3333);
  color: #fff;
  font-weight: 600;
  box-shadow: 0 2px 10px rgba(255, 51, 51, 0.3);
}

.filter-count {
  margin-left: auto;
  color: #ff3333;
  font-weight: 600;
  font-size: 0.85rem;
  padding-left: 0.75rem;
}

/* =====================
   STAFF GRID
   ===================== */
.staff-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 1.25rem;
}

/* =====================
   STAFF CARD
   ===================== */
.staff-card {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 16px;
  padding: 1.25rem;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  animation: fadeInUp 0.5s ease-out;
}

.staff-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--rolle-color, #ff3333);
}

.staff-card:hover {
  border-color: rgba(255, 51, 51, 0.4);
  transform: translateY(-4px);
  box-shadow: 0 12px 40px rgba(255, 51, 51, 0.2);
}

.staff-card.status-im_einsatz {
  border-color: rgba(255, 51, 51, 0.5);
  box-shadow: 0 0 20px rgba(255, 51, 51, 0.15);
}

.staff-card.status-pause {
  border-color: rgba(247, 147, 30, 0.4);
}

.staff-card.status-abwesend {
  opacity: 0.6;
}

/* =====================
   STAFF CARD HEADER
   ===================== */
.staff-card-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
  cursor: pointer;
}

.staff-avatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.4rem;
  font-weight: 700;
  color: #fff;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.staff-info {
  flex: 1;
}

.staff-info h3 {
  margin: 0;
  font-size: 1.05rem;
  color: #fff;
  font-weight: 600;
}

.staff-rolle {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.6);
  display: flex;
  align-items: center;
  gap: 0.3rem;
}

.staff-status {
  padding: 0.4rem 0.75rem;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 600;
  color: #fff;
  display: flex;
  align-items: center;
  gap: 0.3rem;
}

.staff-status.pulsing {
  animation: pulse 1.5s ease-in-out infinite;
}

/* Card Toggle Button */
.card-toggle {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background: rgba(255, 51, 51, 0.15);
  border: 1px solid rgba(255, 51, 51, 0.3);
  color: #ff3333;
  font-size: 0.7rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-left: 0.5rem;
  flex-shrink: 0;
}

.card-toggle:hover {
  background: rgba(255, 51, 51, 0.3);
}

/* Collapsed Card State */
.staff-card.collapsed {
  padding: 0.75rem 1rem;
}

.staff-card.collapsed .staff-card-header {
  margin-bottom: 0;
}

/* =====================
   STAFF DETAILS
   ===================== */
.staff-details {
  margin-bottom: 1rem;
  padding: 0.75rem;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  border: 1px solid rgba(255, 51, 51, 0.1);
}

.detail-item {
  display: flex;
  justify-content: space-between;
  font-size: 0.85rem;
  padding: 0.25rem 0;
}

.detail-item .detail-label {
  color: rgba(255, 255, 255, 0.6);
}

.detail-item .detail-value {
  color: #fff;
  font-weight: 500;
}

.detail-item.highlight {
  background: rgba(255, 51, 51, 0.1);
  margin: 0.25rem -0.75rem;
  padding: 0.5rem 0.75rem;
  border-radius: 4px;
}

.detail-item.highlight .detail-value {
  color: #ff3333;
}

/* =====================
   STATUS BUTTONS
   ===================== */
.status-buttons {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.status-btn {
  flex: 1;
  padding: 0.5rem;
  background: rgba(0, 0, 0, 0.3);
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.5);
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.status-btn:hover {
  border-color: var(--status-color);
  background: rgba(255, 255, 255, 0.05);
  color: var(--status-color);
}

.status-btn.active {
  background: var(--status-color);
  border-color: var(--status-color);
  color: #fff;
}

/* =====================
   ACTIONS
   ===================== */
.staff-actions {
  display: flex;
  gap: 0.5rem;
}

.btn-edit {
  flex: 1;
  padding: 0.5rem 1rem;
  background: rgba(255, 51, 51, 0.15);
  border: 1px solid rgba(255, 51, 51, 0.4);
  border-radius: 8px;
  color: #ff3333;
  font-size: 0.85rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-edit:hover {
  background: rgba(255, 51, 51, 0.25);
  border-color: #ff3333;
  transform: translateY(-1px);
}

.btn-delete {
  padding: 0.5rem 0.75rem;
  background: rgba(255, 100, 100, 0.15);
  border: 1px solid rgba(255, 100, 100, 0.4);
  border-radius: 8px;
  color: #ff6b6b;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-delete:hover {
  background: rgba(255, 100, 100, 0.25);
  border-color: #ff6b6b;
}

/* =====================
   EMPTY STATE
   ===================== */
.empty-state {
  grid-column: 1 / -1;
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px dashed rgba(255, 51, 51, 0.3);
  border-radius: 16px;
}

.empty-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
  filter: drop-shadow(0 0 15px rgba(255, 51, 51, 0.3));
}

.empty-state h3 {
  color: #ff3333;
  margin: 0 0 0.5rem 0;
  font-size: 1.2rem;
}

.empty-state p {
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 1.5rem;
}

/* =====================
   COMING SOON
   ===================== */
.coming-soon {
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 16px;
  backdrop-filter: blur(20px);
}

.coming-soon .icon {
  font-size: 4rem;
  display: block;
  margin-bottom: 1rem;
  filter: drop-shadow(0 0 15px rgba(255, 51, 51, 0.3));
}

.coming-soon h3 {
  background: linear-gradient(135deg, #ff3333, #ff6b35);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin: 0 0 0.5rem 0;
  font-size: 1.3rem;
}

.coming-soon p {
  color: rgba(255, 255, 255, 0.6);
}

/* =====================
   MATTEN ZUWEISUNG
   ===================== */
.matten-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.25rem;
}

.matte-box {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 16px;
  overflow: hidden;
  transition: all 0.3s ease;
}

.matte-box:hover {
  border-color: rgba(255, 51, 51, 0.4);
  box-shadow: 0 8px 25px rgba(255, 51, 51, 0.15);
}

.matte-header {
  background: linear-gradient(135deg, rgba(255, 51, 51, 0.2), rgba(255, 107, 53, 0.2));
  padding: 0.75rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(255, 51, 51, 0.2);
}

.matte-nummer {
  font-weight: 700;
  color: #ff3333;
  font-size: 1rem;
}

.matte-count {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.7);
}

.matte-staff {
  padding: 1rem;
  min-height: 100px;
}

.matte-staff-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0.75rem;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
  border: 1px solid rgba(255, 51, 51, 0.1);
}

.btn-remove {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: rgba(255, 100, 100, 0.2);
  border: 1px solid rgba(255, 100, 100, 0.4);
  color: #ff6b6b;
  font-size: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-remove:hover {
  background: rgba(255, 100, 100, 0.4);
}

.no-staff {
  color: rgba(255, 255, 255, 0.4);
  text-align: center;
  font-style: italic;
  padding: 1rem;
}

.matte-assign {
  padding: 0.75rem 1rem;
  border-top: 1px solid rgba(255, 51, 51, 0.2);
}

.matte-assign select {
  width: 100%;
  padding: 0.6rem 0.75rem;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 8px;
  color: #fff;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.matte-assign select:hover,
.matte-assign select:focus {
  border-color: #ff3333;
  outline: none;
  box-shadow: 0 0 10px rgba(255, 51, 51, 0.2);
}

.matte-assign select option {
  background: #1a1a2e;
  color: #fff;
}

/* =====================
   MODAL
   ===================== */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
  animation: fadeIn 0.3s ease;
}

.modal-content {
  background: linear-gradient(145deg, #1a1a2e, #0d0d1a);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 20px;
  width: 100%;
  max-width: 500px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.25rem 1.5rem;
  border-bottom: 1px solid rgba(255, 51, 51, 0.2);
}

.modal-header h2 {
  margin: 0;
  font-size: 1.2rem;
  background: linear-gradient(135deg, #ff3333, #ff6b35);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.modal-close {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: rgba(255, 100, 100, 0.2);
  border: 1px solid rgba(255, 100, 100, 0.4);
  color: #ff6b6b;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
}

.modal-close:hover {
  background: rgba(255, 100, 100, 0.4);
  transform: rotate(90deg);
}

.modal-body {
  padding: 1.5rem;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 0.75rem;
  padding: 1.25rem 1.5rem;
  border-top: 1px solid rgba(255, 51, 51, 0.2);
}

/* =====================
   FORM ELEMENTS
   ===================== */
.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-size: 0.85rem;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.7);
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 0.75rem 1rem;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 8px;
  color: #fff;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #ff3333;
  box-shadow: 0 0 15px rgba(255, 51, 51, 0.2);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.form-group select option {
  background: #1a1a2e;
  color: #fff;
}

.form-group textarea {
  resize: vertical;
  min-height: 80px;
}

.btn-cancel {
  padding: 0.75rem 1.5rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-cancel:hover {
  border-color: rgba(255, 255, 255, 0.4);
  color: #fff;
  background: rgba(255, 255, 255, 0.15);
}

.btn-save {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #ff3333, #cc0000);
  border: none;
  border-radius: 8px;
  color: #ffffff;
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(255, 51, 51, 0.3);
}

.btn-save:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 51, 51, 0.4);
}

.btn-save:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* =====================
   RESPONSIVE
   ===================== */
@media (max-width: 768px) {
  .staff-header {
    flex-direction: column;
    align-items: stretch;
    text-align: center;
  }

  .header-title {
    justify-content: center;
  }

  .btn-add {
    width: 100%;
    justify-content: center;
  }

  .staff-stats {
    justify-content: center;
  }

