/* ============================================
   TDA TOURNAMENT LOGIN - ULTRA PREMIUM DESIGN
   ============================================ */

/* Main Container */
.login-page-new {
  min-height: 100vh;
  background:
    linear-gradient(135deg, #0a0a0f 0%, #1a0f1e 50%, #0f0a1a 100%),
    radial-gradient(circle at 20% 50%, rgba(255, 51, 51, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(255, 107, 53, 0.05) 0%, transparent 50%);
  color: #fff;
  position: relative;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Inter', sans-serif;
}

/* Animated gradient overlay */
.login-page-new::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 30% 40%, rgba(255, 51, 51, 0.04) 0%, transparent 40%),
    radial-gradient(circle at 70% 70%, rgba(255, 107, 53, 0.03) 0%, transparent 40%);
  animation: gradientFloat 15s ease-in-out infinite;
  pointer-events: none;
}

@keyframes gradientFloat {
  0%, 100% { opacity: 0.3; transform: translate(0, 0); }
  50% { opacity: 0.6; transform: translate(20px, -20px); }
}

/* Premium Navigation */
.login-nav {
  background: rgba(5, 5, 10, 0.8);
  backdrop-filter: blur(40px) saturate(180%);
  border-bottom: 1px solid rgba(255, 51, 51, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.3);
}

.login-nav-content {
  max-width: 1400px;
  margin: 0 auto;
  padding: 1.5rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-logo {
  display: flex;
  align-items: center;
  gap: 1.25rem;
}

.nav-logo img {
  height: 56px;
  width: 56px;
  border-radius: 50%;
  object-fit: cover;
  border: 2.5px solid #ff3333;
  padding: 5px;
  background:
    linear-gradient(135deg, rgba(255, 51, 51, 0.15), rgba(255, 107, 53, 0.1)),
    rgba(10, 10, 15, 0.8);
  box-shadow:
    0 0 30px rgba(255, 51, 51, 0.4),
    0 0 60px rgba(255, 51, 51, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.nav-logo img:hover {
  transform: scale(1.08) rotate(5deg);
  box-shadow:
    0 0 40px rgba(255, 51, 51, 0.6),
    0 0 80px rgba(255, 51, 51, 0.3);
}

.nav-logo span {
  font-size: 1.375rem;
  font-weight: 700;
  background: linear-gradient(135deg, #ff3333, #ff6b35, #ff8c5a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -0.3px;
}

.nav-links {
  display: flex;
  gap: 2.5rem;
}

.nav-links a {
  color: rgba(255, 255, 255, 0.75);
  text-decoration: none;
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.3s ease;
  position: relative;
  padding: 0.625rem 0;
  letter-spacing: 0.2px;
}

.nav-links a::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #ff3333, #ff6b35);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  transform: translateX(-50%);
}

.nav-links a:hover {
  color: #fff;
}

.nav-links a:hover::after {
  width: 100%;
}

/* Main Content */
.login-main {
  padding: 2rem 0;
  min-height: calc(100vh - 110px);
  position: relative;
  z-index: 1;
}

.login-container-new {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: 1fr 420px;
  gap: 3rem;
  align-items: start;
}

/* Tournaments Section */
.tournaments-section-login {
  background: transparent;
}

.section-header h1 {
  font-size: 2rem;
  margin-bottom: 0.5rem;
  color: #ffffff;
  font-weight: 700;
  letter-spacing: -0.8px;
}

.section-header p {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.9375rem;
  margin-bottom: 1.5rem;
  font-weight: 400;
  letter-spacing: 0.1px;
}

/* Loading & Empty States */
.loading-state,
.empty-state {
  text-align: center;
  padding: 5rem 2rem;
  color: rgba(255, 255, 255, 0.5);
}

.spinner {
  width: 60px;
  height: 60px;
  border: 3px solid rgba(255, 255, 255, 0.05);
  border-top-color: #ff3333;
  border-radius: 50%;
  animation: spin 1s cubic-bezier(0.68, -0.55, 0.265, 1.55) infinite;
  margin: 0 auto 1.5rem;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Tournament Cards - MODERN & ELEGANT */
.tournaments-grid-login {
  display: grid;
  gap: 1rem;
  max-height: calc(100vh - 220px);
  overflow-y: auto;
  padding-right: 0.5rem;
  scrollbar-width: thin;
  scrollbar-color: rgba(255, 51, 51, 0.3) transparent;
}

.tournaments-grid-login::-webkit-scrollbar {
  width: 6px;
}

.tournaments-grid-login::-webkit-scrollbar-track {
  background: transparent;
}

.tournaments-grid-login::-webkit-scrollbar-thumb {
  background: rgba(255, 51, 51, 0.3);
  border-radius: 10px;
  transition: background 0.3s ease;
}

.tournaments-grid-login::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 51, 51, 0.5);
}

.tournament-card-login {
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.06);
  border-radius: 16px;
  padding: 1.5rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.tournament-card-login::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 100%;
  background: linear-gradient(180deg, #ff3333, #ff6b35);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.tournament-card-login::after {
  content: none;
}

.tournament-card-login:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: rgba(255, 255, 255, 0.1);
  transform: translateX(4px);
  box-shadow: -4px 0 20px rgba(255, 51, 51, 0.1);
}

.tournament-card-login:hover::before {
  opacity: 1;
}

.tournament-card-login.selected {
  background: rgba(255, 51, 51, 0.08);
  border: 1px solid rgba(255, 51, 51, 0.3);
  transform: translateX(4px);
}

.tournament-card-login.selected::before {
  opacity: 1;
  width: 3px;
}

.tournament-card-login .tournament-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
  gap: 1rem;
  background: transparent !important;
  border: none !important;
  padding: 0 !important;
}

.tournament-card-login .tournament-header h3 {
  font-size: 1.125rem;
  color: #ffffff !important;
  margin: 0;
  line-height: 1.4;
  font-weight: 600;
  letter-spacing: -0.3px;
}

.selected-badge {
  background: linear-gradient(135deg, #ff3333, #ff6b35);
  color: #fff;
  padding: 0.375rem 0.75rem;
  border-radius: 20px;
  font-size: 0.6875rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  white-space: nowrap;
  flex-shrink: 0;
  box-shadow: 0 2px 8px rgba(255, 51, 51, 0.3);
}

@keyframes pulse-glow {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.85;
  }
}

.tournament-card-login .tournament-details {
  display: flex;
  flex-direction: column;
  gap: 0.625rem;
  background: transparent !important;
}

.tournament-card-login .detail-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: rgba(255, 255, 255, 0.7) !important;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0.1px;
}

.tournament-card-login .detail-icon {
  font-size: 1.125rem;
  flex-shrink: 0;
  opacity: 0.6;
}

/* Login Section - ULTRA PREMIUM CARD */
.login-section-new {
  position: sticky;
  top: 100px;
}

.login-box-styled {
  background: rgba(255, 255, 255, 0.04);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 20px;
  padding: 2.5rem;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
}

.login-box-styled::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, #ff3333, #ff6b35);
}

@keyframes gradientShift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.login-header {
  text-align: center;
  margin-bottom: 2rem;
}

.login-logo-small {
  margin-bottom: 1.25rem;
  display: flex;
  justify-content: center;
}

.login-logo-small img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid rgba(255, 51, 51, 0.4);
  padding: 5px;
  background: rgba(255, 51, 51, 0.08);
  box-shadow: 0 8px 24px rgba(255, 51, 51, 0.2);
}

.login-header h2 {
  font-size: 1.75rem;
  margin-bottom: 0.5rem;
  color: #ffffff;
  font-weight: 600;
  letter-spacing: -0.6px;
}

.login-header p {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.9375rem;
  font-weight: 400;
  letter-spacing: 0.1px;
}

.selected-tournament-info {
  background: linear-gradient(135deg, rgba(255, 51, 51, 0.12), rgba(255, 107, 53, 0.1));
  border: 1.5px solid rgba(255, 51, 51, 0.3);
  border-radius: 16px;
  padding: 1.25rem 1.5rem;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);
}

.info-label {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.55);
  text-transform: uppercase;
  letter-spacing: 1.2px;
  font-weight: 600;
}

.info-value {
  color: #ffffff;
  font-weight: 600;
  font-size: 1.125rem;
  letter-spacing: -0.3px;
}

.login-message {
  padding: 1.125rem 1.25rem;
  border-radius: 14px;
  margin-bottom: 2rem;
  font-size: 0.9375rem;
  text-align: center;
  font-weight: 500;
  letter-spacing: 0.2px;
}

.login-message.error {
  background: rgba(239, 68, 68, 0.12);
  border: 1.5px solid rgba(239, 68, 68, 0.3);
  color: #fca5a5;
}

.login-message.success {
  background: rgba(34, 197, 94, 0.12);
  border: 1.5px solid rgba(34, 197, 94, 0.3);
  color: #86efac;
}

.login-message.info {
  background: rgba(59, 130, 246, 0.12);
  border: 1.5px solid rgba(59, 130, 246, 0.3);
  color: #93c5fd;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.input-group {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.input-group label {
  color: rgba(255, 255, 255, 0.85);
  font-size: 0.9375rem;
  font-weight: 600;
  letter-spacing: 0.3px;
}

.input-group input {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  padding: 0.875rem 1.125rem;
  color: #fff;
  font-size: 0.9375rem;
  transition: all 0.2s ease;
  font-weight: 400;
  letter-spacing: 0.1px;
}

.input-group input::placeholder {
  color: rgba(255, 255, 255, 0.3);
}

.input-group input:focus {
  outline: none;
  border-color: rgba(255, 51, 51, 0.4);
  background: rgba(255, 255, 255, 0.05);
  box-shadow: 0 0 0 3px rgba(255, 51, 51, 0.1);
}

.input-group input:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.login-button {
  background: linear-gradient(135deg, #ff3333, #ff4444);
  color: #ffffff;
  border: none;
  padding: 1rem 2rem;
  border-radius: 12px;
  font-weight: 600;
  font-size: 0.9375rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.625rem;
  margin-top: 0.5rem;
  box-shadow: 0 4px 16px rgba(255, 51, 51, 0.3);
  text-transform: uppercase;
  letter-spacing: 0.8px;
  position: relative;
  overflow: hidden;
}

.login-button::before {
  content: none;
}

.login-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 24px rgba(255, 51, 51, 0.4);
}

.login-button:hover:not(:disabled)::before {
  content: none;
}

.login-button:active:not(:disabled) {
  transform: translateY(0);
}

.login-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.login-spinner {
  width: 18px;
  height: 18px;
  border: 2.5px solid rgba(255, 255, 255, 0.25);
  border-top-color: #fff;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

.login-footer {
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
  text-align: center;
}

.login-footer p {
  color: rgba(255, 255, 255, 0.55);
  font-size: 0.9375rem;
  margin-bottom: 1rem;
  font-weight: 400;
}

.register-link {
  background: transparent;
  border: 1.5px solid rgba(255, 51, 51, 0.4);
  color: #ff6b35;
  padding: 0.875rem 1.75rem;
  border-radius: 14px;
  font-weight: 600;
  font-size: 0.9375rem;
  cursor: pointer;
  transition: all 0.3s ease;
  letter-spacing: 0.4px;
}

.register-link:hover {
  background: rgba(255, 51, 51, 0.12);
  border-color: rgba(255, 51, 51, 0.6);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 51, 51, 0.2);
}

.login-help {
  margin-top: 1.75rem;
  padding: 1.375rem 1.5rem;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.06);
}

.login-help p {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.875rem;
  margin-bottom: 0.625rem;
  line-height: 1.7;
  font-weight: 400;
}

.login-help p:last-child {
  margin-bottom: 0;
}

.login-help strong {
  color: rgba(255, 255, 255, 0.85);
  font-weight: 600;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .login-container-new {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .login-section-new {
    position: static;
    max-width: 550px;
    margin: 0 auto;
  }

  .tournaments-grid-login {
    max-height: 65vh;
  }
}

@media (max-width: 768px) {
  .login-nav-content {
    padding: 1.25rem 1.5rem;
  }

  .nav-links {
    gap: 1.5rem;
  }

  .login-container-new {
    padding: 0 1.5rem;
  }

  .section-header h1 {
    font-size: 2.25rem;
  }

  .login-box-styled {
    padding: 2.5rem;
  }

  .tournament-card-login .tournament-header h3 {
    font-size: 1.375rem;
  }
}

@media (max-width: 480px) {
  .nav-logo img {
    height: 48px;
    width: 48px;
  }

  .nav-logo span {
    font-size: 1.25rem;
  }

  .nav-links {
    gap: 1.25rem;
    font-size: 0.9375rem;
  }

  .section-header h1 {
    font-size: 2rem;
  }

  .tournament-card-login .tournament-header h3 {
    font-size: 1.25rem;
  }

  .tournament-card-login .detail-item {
    font-size: 0.9375rem;
  }

  .login-logo-small img {
    width: 80px;
    height: 80px;
  }

  .login-header h2 {
    font-size: 2rem;
  }
}
