/* TurnierDashboard.css - TDA DARK THEME */

.turnier-dashboard {
  min-height: 100vh;
  background: #000000 !important;
  padding: 2rem;
}

.turnier-dashboard * {
  box-sizing: border-box;
}

/* Loading & Error States */
.turnier-dashboard .dashboard-loading,
.turnier-dashboard .dashboard-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 300px;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
  background: transparent !important;
}

.turnier-dashboard .loading-spinner {
  font-size: 3rem;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.turnier-dashboard .dashboard-error h3 {
  color: #ff6b6b;
  margin-bottom: 10px;
}

/* Header */
.turnier-dashboard .dashboard-header {
  background: rgba(0, 0, 0, 0.6) !important;
  backdrop-filter: blur(10px);
  color: white;
  padding: 2rem;
  border-radius: 16px;
  margin-bottom: 2rem;
  box-shadow: 0 8px 32px rgba(255, 51, 51, 0.15), inset 0 0 60px rgba(255, 51, 51, 0.05);
  border: 1px solid rgba(255, 51, 51, 0.3);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 16px;
}

.turnier-dashboard .header-title h1 {
  font-size: 2.2rem;
  font-weight: 700;
  color: #ff3333 !important;
  margin: 0 0 0.5rem 0;
  text-shadow: 0 0 20px rgba(255, 51, 51, 0.5);
  background: none !important;
  -webkit-background-clip: unset !important;
  -webkit-text-fill-color: unset !important;
}

.turnier-dashboard .header-title p {
  color: rgba(255, 255, 255, 0.7) !important;
  margin: 0;
  font-size: 1.1rem;
}

.turnier-dashboard .header-actions {
  display: flex;
  gap: 12px;
  align-items: center;
  flex-wrap: wrap;
}

.turnier-dashboard .search-box input {
  padding: 10px 16px;
  border: 1px solid rgba(255, 255, 255, 0.3) !important;
  border-radius: 8px;
  font-size: 0.9rem;
  min-width: 200px;
  background: rgba(255, 255, 255, 0.1) !important;
  color: white !important;
  transition: all 0.2s;
}

.turnier-dashboard .search-box input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.turnier-dashboard .search-box input:focus {
  outline: none;
  border-color: rgba(255, 255, 255, 0.6) !important;
  background: rgba(255, 255, 255, 0.15) !important;
}

.turnier-dashboard .filter-select {
  padding: 10px 16px;
  border: 1px solid rgba(255, 255, 255, 0.3) !important;
  border-radius: 8px;
  font-size: 0.9rem;
  background: rgba(255, 255, 255, 0.1) !important;
  color: white !important;
  cursor: pointer;
}

.turnier-dashboard .filter-select option {
  background: #1a1a1a;
  color: white;
}

/* Buttons */
.turnier-dashboard .btn {
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  display: inline-flex;
  align-items: center;
  gap: 6px;
}

.turnier-dashboard .btn-primary {
  background: rgba(255, 51, 51, 0.2) !important;
  color: #ff6b6b !important;
  border: 1px solid rgba(255, 51, 51, 0.4) !important;
}

.turnier-dashboard .btn-primary:hover {
  background: rgba(255, 51, 51, 0.3) !important;
  border-color: rgba(255, 51, 51, 0.6) !important;
  transform: translateY(-1px);
}

.turnier-dashboard .btn-danger {
  background: rgba(220, 53, 69, 0.3) !important;
  color: #ff6b6b !important;
  border: 1px solid rgba(220, 53, 69, 0.5) !important;
}

.turnier-dashboard .btn-danger:hover {
  background: rgba(220, 53, 69, 0.5) !important;
}

.turnier-dashboard .btn-sm {
  padding: 8px 14px;
  font-size: 0.85rem;
}

/* Statistics Grid */
.turnier-dashboard .stats-grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 0.75rem;
  margin-bottom: 1.5rem;
}

.turnier-dashboard .stat-card {
  background: rgba(0, 0, 0, 0.6) !important;
  backdrop-filter: blur(10px);
  padding: 0.75rem;
  border-radius: 10px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 51, 51, 0.2) !important;
}

.turnier-dashboard .stat-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(255, 51, 51, 0.2);
  border-color: rgba(255, 51, 51, 0.5) !important;
}

.turnier-dashboard .stat-icon {
  font-size: 1.25rem;
  opacity: 0.9;
}

.turnier-dashboard .stat-content h3 {
  font-size: 1.25rem;
  font-weight: bold;
  color: #ffffff !important;
  margin: 0;
  background: none !important;
  -webkit-background-clip: unset !important;
  -webkit-text-fill-color: unset !important;
  line-height: 1.2;
}

.turnier-dashboard .stat-content p {
  font-size: 0.65rem;
  color: rgba(255, 255, 255, 0.7) !important;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.3px;
  font-weight: 600;
}

/* Turnier Grid */
.turnier-dashboard .turnier-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  gap: 1.5rem;
}

.turnier-dashboard .turnier-card {
  background: rgba(0, 0, 0, 0.6) !important;
  backdrop-filter: blur(10px);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 51, 51, 0.2) !important;
}

.turnier-dashboard .turnier-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 32px rgba(255, 51, 51, 0.2);
  border-color: rgba(255, 51, 51, 0.5) !important;
}

.turnier-dashboard .card-header {
  padding: 16px 20px;
  background: rgba(255, 51, 51, 0.1) !important;
  border-bottom: 1px solid rgba(255, 51, 51, 0.2) !important;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 12px;
}

.turnier-dashboard .card-title h3 {
  font-size: 1.1rem;
  font-weight: 600;
  color: #ffffff !important;
  margin: 0 0 4px 0;
}

.turnier-dashboard .card-subtitle {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.6) !important;
}

.turnier-dashboard .status-badge {
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 500;
  white-space: nowrap;
}

.turnier-dashboard .status-upcoming {
  background: rgba(33, 150, 243, 0.2) !important;
  color: #64b5f6 !important;
  border: 1px solid rgba(33, 150, 243, 0.3);
}

.turnier-dashboard .status-active {
  background: rgba(76, 175, 80, 0.2) !important;
  color: #81c784 !important;
  border: 1px solid rgba(76, 175, 80, 0.3);
}

.turnier-dashboard .status-completed {
  background: rgba(158, 158, 158, 0.2) !important;
  color: #bdbdbd !important;
  border: 1px solid rgba(158, 158, 158, 0.3);
}

.turnier-dashboard .card-body {
  padding: 16px 20px;
  background: transparent !important;
}

.turnier-dashboard .info-row {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 10px;
  font-size: 0.9rem;
}

.turnier-dashboard .info-row:last-child {
  margin-bottom: 0;
}

.turnier-dashboard .info-icon {
  font-size: 1rem;
  width: 24px;
  text-align: center;
}

.turnier-dashboard .info-label {
  color: rgba(255, 255, 255, 0.6) !important;
  min-width: 100px;
}

.turnier-dashboard .info-value {
  color: #ffffff !important;
  font-weight: 500;
}

/* Kategorien Display */
.turnier-dashboard .kategorien-row {
  margin-top: 12px;
  padding-top: 12px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.turnier-dashboard .kategorien-label {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5) !important;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 8px;
}

.turnier-dashboard .kategorien-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.turnier-dashboard .kategorie-tag {
  padding: 4px 10px;
  background: rgba(255, 51, 51, 0.15) !important;
  border: 1px solid rgba(255, 51, 51, 0.3) !important;
  border-radius: 12px;
  font-size: 0.75rem;
  color: #ff6b6b !important;
  font-weight: 500;
}

.turnier-dashboard .no-kategorien {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.4) !important;
  font-style: italic;
}

.turnier-dashboard .card-footer {
  padding: 12px 20px;
  background: rgba(0, 0, 0, 0.3) !important;
  border-top: 1px solid rgba(255, 255, 255, 0.1) !important;
  display: flex;
  justify-content: space-between;
  gap: 12px;
}

/* Verffentlichen Button */
.turnier-dashboard .btn-success {
  background: linear-gradient(135deg, #22c55e, #16a34a) !important;
  border: 1px solid rgba(34, 197, 94, 0.5) !important;
  color: #fff !important;
}

.turnier-dashboard .btn-success:hover {
  background: linear-gradient(135deg, #16a34a, #15803d) !important;
  box-shadow: 0 4px 15px rgba(34, 197, 94, 0.4);
}

.turnier-dashboard .btn-warning {
  background: linear-gradient(135deg, #f97316, #ea580c) !important;
  border: 1px solid rgba(249, 115, 22, 0.5) !important;
  color: #fff !important;
}

.turnier-dashboard .btn-warning:hover {
  background: linear-gradient(135deg, #ea580c, #c2410c) !important;
  box-shadow: 0 4px 15px rgba(249, 115, 22, 0.4);
}

/* Empty State */
.turnier-dashboard .empty-state {
  text-align: center;
  padding: 60px 20px;
  background: rgba(0, 0, 0, 0.6) !important;
  backdrop-filter: blur(10px);
  border-radius: 16px;
  border: 1px solid rgba(255, 51, 51, 0.2) !important;
}

.turnier-dashboard .empty-icon {
  font-size: 4rem;
  margin-bottom: 16px;
}

.turnier-dashboard .empty-state h3 {
  font-size: 1.25rem;
  color: #ffffff !important;
  margin: 0 0 8px 0;
}

.turnier-dashboard .empty-state p {
  color: rgba(255, 255, 255, 0.6) !important;
  margin: 0 0 20px 0;
}

/* Responsive */
@media (max-width: 768px) {
  .turnier-dashboard {
    padding: 1rem;
  }

  .turnier-dashboard .dashboard-header {
    flex-direction: column;
    text-align: center;
    padding: 1.5rem;
  }

  .turnier-dashboard .header-title h1 {
    font-size: 1.8rem;
  }

  .turnier-dashboard .header-actions {
    width: 100%;
    flex-direction: column;
  }

  .turnier-dashboard .search-box input {
    width: 100%;
  }

  .turnier-dashboard .filter-select {
    width: 100%;
  }

  .turnier-dashboard .stats-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 0.5rem;
  }

  .turnier-dashboard .turnier-grid {
    grid-template-columns: 1fr;
  }

  .turnier-dashboard .card-footer {
    flex-direction: column;
  }

  .turnier-dashboard .card-footer .btn {
    width: 100%;
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .turnier-dashboard .stats-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
