/* ============================================================================
   DIVISION BERSICHT - Dashboard Style
   ============================================================================ */

/* =====================
   ANIMATIONS
   ===================== */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

/* =====================
   BASE CONTAINER
   ===================== */
.division-uebersicht {
  min-height: calc(100vh - 100px);
  background: #000000;
  color: #ffffff;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  padding: 1.5rem;
  animation: fadeIn 0.5s ease-out;
}

.division-uebersicht h1 {
  font-size: 1.8rem;
  font-weight: 700;
  margin: 0 0 1.5rem 0;
  background: linear-gradient(135deg, #ff3333, #ff6b35);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

/* =====================
   FILTER BAR
   ===================== */
.filter-bar {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border-radius: 12px;
  border: 1px solid rgba(255, 51, 51, 0.2);
}

.filter-bar button {
  padding: 0.5rem 1rem;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
  display: flex;
  align-items: center;
  gap: 0.4rem;
}

.filter-bar button:hover {
  background: rgba(255, 51, 51, 0.15);
  border-color: rgba(255, 51, 51, 0.5);
  color: #fff;
}

.filter-bar button.active {
  background: linear-gradient(135deg, #ff3333, #cc0000);
  border-color: #ff3333;
  color: #fff;
  font-weight: 600;
  box-shadow: 0 4px 15px rgba(255, 51, 51, 0.3);
}

/* =====================
   SUB-FILTER BAR
   ===================== */
.sub-filter-bar {
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
  margin-bottom: 1rem;
  padding: 0.75rem 1rem;
  background: rgba(255, 51, 51, 0.08);
  border-radius: 10px;
  border: 1px solid rgba(255, 51, 51, 0.15);
}

.sub-filter-bar button {
  padding: 0.4rem 0.8rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 51, 51, 0.25);
  border-radius: 6px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.8rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
  display: flex;
  align-items: center;
  gap: 0.3rem;
}

.sub-filter-bar button:hover {
  background: rgba(255, 51, 51, 0.15);
  border-color: rgba(255, 51, 51, 0.4);
  color: #fff;
}

.sub-filter-bar button.active {
  background: rgba(255, 51, 51, 0.25);
  border-color: #ff3333;
  color: #ff3333;
  font-weight: 600;
}

/* =====================
   ACTIVE FILTERS
   ===================== */
.active-filters {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 1rem;
  padding: 0.75rem 1rem;
  background: rgba(255, 51, 51, 0.1);
  border-radius: 8px;
  border: 1px solid rgba(255, 51, 51, 0.2);
  flex-wrap: wrap;
}

.filter-label {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.85rem;
}

.filter-tag {
  padding: 0.4rem 0.8rem;
  background: linear-gradient(135deg, rgba(255, 51, 51, 0.2), rgba(255, 107, 53, 0.2));
  border: 1px solid rgba(255, 51, 51, 0.4);
  border-radius: 20px;
  color: #ff3333;
  font-size: 0.85rem;
  font-weight: 600;
}

.clear-filters {
  margin-left: auto;
  padding: 0.4rem 0.8rem;
  background: rgba(255, 100, 100, 0.15);
  border: 1px solid rgba(255, 100, 100, 0.3);
  border-radius: 6px;
  color: #ff6b6b;
  font-size: 0.8rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.clear-filters:hover {
  background: rgba(255, 100, 100, 0.25);
  border-color: #ff6b6b;
}

/* =====================
   RESULTS INFO
   ===================== */
.results-info {
  margin-bottom: 1rem;
  padding: 0.5rem 0;
}

.results-info p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  margin: 0;
}

.results-info .filter-info {
  color: #ff3333;
  font-weight: 500;
}

/* =====================
   DIVISION LIST / GRID
   ===================== */
.division-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 1rem;
  animation: fadeInUp 0.5s ease-out;
}

/* =====================
   NO RESULTS
   ===================== */
.no-results {
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px dashed rgba(255, 51, 51, 0.3);
  border-radius: 16px;
  margin-top: 2rem;
}

.no-results h3 {
  color: #ff3333;
  font-size: 1.3rem;
  margin: 0 0 0.5rem 0;
}

.no-results p {
  color: rgba(255, 255, 255, 0.6);
  font-size: 1rem;
  margin-bottom: 1.5rem;
}

.show-all-btn {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #ff3333, #cc0000);
  border: none;
  border-radius: 8px;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(255, 51, 51, 0.3);
}

.show-all-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 51, 51, 0.4);
}

/* =====================
   LOADING STATE
   ===================== */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 50vh;
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-top: 4px solid #ff3333;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
  box-shadow: 0 0 20px rgba(255, 51, 51, 0.3);
}

.loading-container p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
}

/* =====================
   ERROR STATE
   ===================== */
.error-container {
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(255, 51, 51, 0.1);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 16px;
}

.error-container h2 {
  color: #ff6b6b;
  margin: 0 0 1rem 0;
}

.error-container p {
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 1.5rem;
}

.retry-button {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #ff3333, #cc0000);
  border: none;
  border-radius: 8px;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.retry-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 51, 51, 0.4);
}

/* =====================
   FILTER TOGGLE BUTTON
   ===================== */
.filter-toggle {
  padding: 0.5rem 1rem;
  background: rgba(255, 51, 51, 0.15);
  border: 1px solid rgba(255, 51, 51, 0.4);
  border-radius: 8px;
  color: #ff3333;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-left: auto;
}

.filter-toggle:hover {
  background: rgba(255, 51, 51, 0.25);
  border-color: #ff3333;
}

.filter-toggle .filter-count {
  background: linear-gradient(135deg, #ff3333, #cc0000);
  color: #fff;
  padding: 0.15rem 0.5rem;
  border-radius: 10px;
  font-size: 0.75rem;
  min-width: 20px;
  text-align: center;
}

/* =====================
   ADVANCED FILTERS PANEL
   ===================== */
.advanced-filters {
  margin-bottom: 1rem;
  padding: 1.25rem;
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(20px);
  border-radius: 12px;
  border: 1px solid rgba(255, 51, 51, 0.2);
  animation: fadeInUp 0.3s ease-out;
}

.filter-section {
  margin-bottom: 1rem;
}

.filter-section:last-child {
  margin-bottom: 0;
}

.filter-section h4 {
  font-size: 0.8rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.5);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin: 0 0 0.5rem 0;
  display: flex;
  align-items: center;
  gap: 0.4rem;
}

.filter-section h4::before {
  content: '';
  width: 3px;
  height: 12px;
  background: linear-gradient(135deg, #ff3333, #ff6b35);
  border-radius: 2px;
}

.filter-options {
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
}

.filter-options button {
  padding: 0.4rem 0.8rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 6px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.8rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
}

.filter-options button:hover {
  background: rgba(255, 51, 51, 0.15);
  border-color: rgba(255, 51, 51, 0.4);
  color: #fff;
}

.filter-options button.active {
  background: rgba(255, 51, 51, 0.25);
  border-color: #ff3333;
  color: #ff3333;
  font-weight: 600;
}

/* =====================
   RESPONSIVE
   ===================== */
@media (max-width: 768px) {
  .division-uebersicht {
    padding: 1rem;
  }

  .division-uebersicht h1 {
    font-size: 1.4rem;
    margin-bottom: 1rem;
  }

  .filter-bar {
    padding: 0.75rem;
  }

  .filter-bar button {
    padding: 0.4rem 0.8rem;
    font-size: 0.8rem;
  }

  .division-list {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }

  .active-filters {
    flex-direction: column;
    align-items: flex-start;
  }

  .clear-filters {
    margin-left: 0;
    margin-top: 0.5rem;
  }

  .filter-toggle {
    width: 100%;
    justify-content: center;
    margin-left: 0;
    margin-top: 0.5rem;
  }

  .advanced-filters {
    padding: 1rem;
  }

  .filter-section h4 {
    font-size: 0.75rem;
  }

  .filter-options button {
    padding: 0.35rem 0.6rem;
    font-size: 0.75rem;
  }
}
