/* Zeitplan Dashboard */
.zeitplan-dashboard {
  padding: 1rem;
  color: #fff;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
}

/* Header */
.zeitplan-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(255, 51, 51, 0.2);
  flex-wrap: wrap;
  gap: 1rem;
}

.header-title {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.header-title h2 {
  margin: 0;
  font-size: 1.5rem;
  color: #fff;
}

.header-title .subtitle {
  margin: 0;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.9rem;
}

.btn-back {
  background: none;
  border: 1px solid rgba(255, 51, 51, 0.4);
  color: #ff6b6b;
  padding: 0.4rem 0.8rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.85rem;
  margin-bottom: 0.5rem;
  transition: all 0.2s;
}

.btn-back:hover {
  background: rgba(255, 51, 51, 0.1);
  border-color: #ff6b6b;
}

.header-stats {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.stat-badge {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.4rem 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 6px;
  min-width: 60px;
}

.stat-badge .stat-value {
  font-size: 1.1rem;
  font-weight: 700;
  color: #fff;
}

.stat-badge .stat-label {
  font-size: 0.65rem;
  color: rgba(255, 255, 255, 0.5);
  text-transform: uppercase;
}

.stat-badge.warning .stat-value { color: #f59e0b; }
.stat-badge.time .stat-value { color: #22c55e; }

/* Actions Bar */
.zeitplan-actions {
  display: flex;
  gap: 0.75rem;
  margin-bottom: 1rem;
  flex-wrap: wrap;
}

.zeitplan-actions button {
  padding: 0.6rem 1rem;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-settings {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2) !important;
  color: #fff;
}

.btn-settings:hover {
  background: rgba(255, 255, 255, 0.15);
}

.btn-add-ring {
  background: linear-gradient(135deg, #3b82f6, #2563eb);
  color: #fff;
}

.btn-add-ring:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.btn-generate {
  background: linear-gradient(135deg, #ff3333, #cc0000);
  color: #fff;
}

.btn-generate:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(255, 51, 51, 0.3);
}

.btn-generate:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-conflicts {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2) !important;
  color: #fff;
}

.btn-conflicts.has-warning {
  background: rgba(245, 158, 11, 0.2);
  border-color: rgba(245, 158, 11, 0.4) !important;
  color: #f59e0b;
}

.btn-save {
  background: linear-gradient(135deg, #22c55e, #16a34a);
  color: #fff;
}

.btn-save:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(34, 197, 94, 0.3);
}

/* Detail-Ansicht Buttons */
.btn-scoreboard {
  background: linear-gradient(135deg, #8b5cf6, #7c3aed);
  color: #fff;
}

.btn-scoreboard:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(139, 92, 246, 0.3);
}

.filter-buttons {
  display: flex;
  gap: 0;
  border-radius: 6px;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-filter {
  background: rgba(255, 255, 255, 0.05);
  border: none !important;
  border-right: 1px solid rgba(255, 255, 255, 0.1) !important;
  color: rgba(255, 255, 255, 0.6);
  padding: 0.5rem 0.75rem;
  font-size: 0.8rem;
}

.btn-filter:last-child {
  border-right: none !important;
}

.btn-filter.active {
  background: rgba(255, 51, 51, 0.3);
  color: #fff;
}

.btn-filter:hover:not(.active) {
  background: rgba(255, 255, 255, 0.1);
}

.btn-pause {
  background: rgba(245, 158, 11, 0.2);
  border: 1px solid rgba(245, 158, 11, 0.4) !important;
  color: #f59e0b;
}

.btn-pause:hover {
  background: rgba(245, 158, 11, 0.3);
}

.btn-free-content {
  background: rgba(59, 130, 246, 0.2);
  border: 1px solid rgba(59, 130, 246, 0.4) !important;
  color: #60a5fa;
}

.btn-free-content:hover {
  background: rgba(59, 130, 246, 0.3);
}

.btn-double-starters {
  background: rgba(168, 85, 247, 0.2);
  border: 1px solid rgba(168, 85, 247, 0.4) !important;
  color: #c084fc;
}

.btn-double-starters:hover {
  background: rgba(168, 85, 247, 0.3);
}

.btn-double-starters.active {
  background: rgba(168, 85, 247, 0.4);
  border-color: rgba(168, 85, 247, 0.6) !important;
}

.ring-double-starters {
  margin-bottom: 1rem;
}

.btn-drag-mode {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2) !important;
  color: #fff;
}

.btn-drag-mode.active {
  background: rgba(34, 197, 94, 0.3);
  border-color: rgba(34, 197, 94, 0.5) !important;
  color: #22c55e;
}

/* Free Content Modal */
.free-content-modal {
  max-width: 400px;
}

.free-content-modal .form-group {
  margin-bottom: 1rem;
}

.free-content-modal label {
  display: block;
  margin-bottom: 0.5rem;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
}

.free-content-modal input,
.free-content-modal textarea {
  width: 100%;
  padding: 0.6rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  color: #fff;
  font-size: 0.9rem;
}

.free-content-modal input:focus,
.free-content-modal textarea:focus {
  outline: none;
  border-color: rgba(255, 51, 51, 0.5);
}

/* Slot Styles */
.free-content-slot {
  background: rgba(59, 130, 246, 0.1) !important;
  border-color: rgba(59, 130, 246, 0.3) !important;
}

.free-content-display {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex: 1;
}

.free-content-icon {
  font-size: 1.5rem;
}

.free-content-info {
  display: flex;
  flex-direction: column;
}

.free-content-title {
  font-weight: 600;
  color: #60a5fa;
}

.free-content-desc {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5);
}

.free-content-time {
  margin-left: auto;
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.6);
}

.completed-slot {
  opacity: 0.6;
  background: rgba(34, 197, 94, 0.1) !important;
}

.status-completed {
  color: #22c55e;
  font-weight: bold;
  margin-left: 0.5rem;
}

.drag-enabled {
  cursor: grab;
  border: 2px dashed rgba(255, 255, 255, 0.3) !important;
}

.drag-enabled:active {
  cursor: grabbing;
}

/* Settings Panel */
.settings-panel {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
}

.settings-header-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.settings-panel h3 {
  margin: 0;
  font-size: 1rem;
  color: #ff6b6b;
}

.settings-link {
  font-size: 0.8rem;
  color: #ff6b6b;
  text-decoration: none;
  padding: 0.3rem 0.6rem;
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 4px;
  transition: all 0.2s;
}

.settings-link:hover {
  background: rgba(255, 51, 51, 0.1);
  border-color: #ff6b6b;
}

.settings-note {
  margin: 0 0 1rem 0;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5);
  font-style: italic;
}

.settings-panel h4 {
  margin: 1.5rem 0 0.75rem 0;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.8);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 1rem;
}

.settings-grid {
  display: flex;
  gap: 1.5rem;
  flex-wrap: wrap;
}

.setting-group {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

.setting-group label {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6);
}

.setting-group input {
  padding: 0.5rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 4px;
  color: #fff;
  width: 100px;
}

.setting-group input:focus {
  outline: none;
  border-color: #ff3333;
}

/* Conflicts Panel */
.conflicts-panel {
  background: rgba(245, 158, 11, 0.05);
  border: 1px solid rgba(245, 158, 11, 0.2);
  border-radius: 8px;
  margin-bottom: 1rem;
  overflow: hidden;
}

.conflicts-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 1rem;
  background: rgba(245, 158, 11, 0.1);
  border-bottom: 1px solid rgba(245, 158, 11, 0.2);
}

.conflicts-header h3 {
  margin: 0;
  font-size: 0.95rem;
  color: #f59e0b;
}

.close-btn {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.6);
  font-size: 1.25rem;
  cursor: pointer;
}

.double-starters-list {
  padding: 0.75rem;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 0.5rem;
}

.no-conflicts {
  padding: 1rem;
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
}

.double-starter-item {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  padding: 0.5rem 0.75rem;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  align-items: center;
}

.double-starter-item .name {
  font-weight: 600;
  color: #fff;
}

.double-starter-item .verein {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.8rem;
}

.double-starter-item .divisions {
  display: flex;
  gap: 0.3rem;
  margin-left: auto;
}

.division-tag {
  padding: 0.15rem 0.4rem;
  background: rgba(99, 102, 241, 0.2);
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: 3px;
  font-size: 0.7rem;
  color: #818cf8;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.division-tag .slot-time {
  font-size: 0.6rem;
  color: rgba(255, 255, 255, 0.5);
}

.division-tag.conflict {
  background: rgba(239, 68, 68, 0.3);
  border-color: rgba(239, 68, 68, 0.5);
  color: #f87171;
}

/* Doppelstarts mit Konflikt */
.double-starter-item.has-conflict {
  background: rgba(239, 68, 68, 0.15);
  border: 1px solid rgba(239, 68, 68, 0.3);
  flex-direction: column;
  align-items: flex-start;
}

.double-starter-item .starter-info {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  width: 100%;
  margin-bottom: 0.5rem;
}

.conflict-details {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex-wrap: wrap;
  width: 100%;
}

.conflict-arrow {
  color: #f87171;
  font-size: 1rem;
}

.conflict-warning {
  width: 100%;
  padding: 0.4rem 0.6rem;
  background: rgba(239, 68, 68, 0.2);
  border-radius: 4px;
  font-size: 0.75rem;
  color: #f87171;
  margin-top: 0.3rem;
}

.conflict-badge {
  margin-left: 0.75rem;
  padding: 0.2rem 0.5rem;
  background: rgba(239, 68, 68, 0.3);
  border-radius: 4px;
  font-size: 0.75rem;
  color: #f87171;
  font-weight: normal;
}

.no-conflicts {
  color: #22c55e !important;
  font-weight: 500;
}

/* Empty State */
.empty-zeitplan {
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(255, 255, 255, 0.02);
  border: 1px dashed rgba(255, 51, 51, 0.2);
  border-radius: 8px;
}

.empty-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.empty-zeitplan h3 {
  margin: 0 0 0.5rem 0;
  color: rgba(255, 255, 255, 0.8);
}

.empty-zeitplan p {
  color: rgba(255, 255, 255, 0.5);
  margin: 0;
}

/* ==================== TATAMI OVERVIEW ==================== */
.tatami-overview {
  margin-top: 1rem;
}

.tatami-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 1rem;
}

/* Tatami Card */
.tatami-card {
  background: linear-gradient(145deg, rgba(30, 25, 25, 0.9), rgba(20, 15, 15, 0.95));
  border: 2px solid rgba(255, 51, 51, 0.2);
  border-radius: 12px;
  padding: 1.25rem;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.tatami-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #ff3333, #cc0000);
  opacity: 0.8;
}

.tatami-card:hover {
  transform: translateY(-4px);
  border-color: rgba(255, 51, 51, 0.5);
  box-shadow: 0 8px 25px rgba(255, 51, 51, 0.15);
}

.tatami-card.active {
  border-color: rgba(255, 51, 51, 0.4);
}

.tatami-card.complete {
  border-color: rgba(34, 197, 94, 0.4);
}

.tatami-card.complete::before {
  background: linear-gradient(90deg, #22c55e, #16a34a);
}

.tatami-card.empty {
  opacity: 0.6;
  border-style: dashed;
}

.tatami-card.unassigned {
  background: linear-gradient(145deg, rgba(40, 35, 30, 0.9), rgba(30, 25, 20, 0.95));
  border-color: rgba(245, 158, 11, 0.3);
  cursor: default;
}

.tatami-card.unassigned::before {
  background: linear-gradient(90deg, #f59e0b, #d97706);
}

.tatami-card.unassigned.has-items {
  border-color: rgba(245, 158, 11, 0.5);
}

/* Tatami Header */
.tatami-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.tatami-header h3 {
  margin: 0;
  font-size: 1.15rem;
  font-weight: 700;
  color: #fff;
}

.status-icon {
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  font-weight: 700;
}

.status-icon.complete {
  background: linear-gradient(135deg, #22c55e, #16a34a);
  color: #fff;
  font-size: 1rem;
}

.status-icon.in-progress {
  background: rgba(255, 51, 51, 0.2);
  border: 2px solid #ff6b6b;
  color: #ff6b6b;
  font-size: 0.6rem;
  animation: pulse-ring 2s infinite;
}

@keyframes pulse-ring {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

/* Tatami Stats */
.tatami-stats {
  margin-bottom: 0.75rem;
}

.stat-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem 0;
}

.stat-label-small {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5);
}

.stat-value-small {
  font-size: 0.85rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.8);
}

.stat-value-small.highlight {
  color: #22c55e;
}

/* Tatami Time */
.tatami-time {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem 0.75rem;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 6px;
  margin-bottom: 0.75rem;
}

.time-range {
  font-size: 0.9rem;
  font-weight: 600;
  color: #fff;
}

.duration {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.5);
}

/* Tatami Current */
.tatami-current {
  padding-top: 0.5rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.current-division {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.7);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.current-division.completed {
  color: #22c55e;
  font-style: italic;
}

/* Tatami Empty State */
.tatami-empty {
  padding: 1.5rem 0;
  text-align: center;
}

.tatami-empty span {
  color: rgba(255, 255, 255, 0.4);
  font-size: 0.85rem;
}

/* Unassigned Card */
.unassigned-count {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0;
}

.count-value {
  font-size: 2.5rem;
  font-weight: 700;
  color: #f59e0b;
}

.count-label {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5);
  text-transform: uppercase;
}

.unassigned-list {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
  padding-top: 0.75rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.unassigned-item {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.6);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.more-items {
  font-size: 0.75rem;
  color: #f59e0b;
  font-style: italic;
}

/* ==================== RING DETAIL VIEW ==================== */
.ring-detail-view {
  margin-top: 1rem;
}

.ring-slots-list-detail {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.ring-empty-detail {
  text-align: center;
  padding: 3rem;
  background: rgba(255, 255, 255, 0.02);
  border: 1px dashed rgba(255, 51, 51, 0.2);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.5);
}

/* Detail Slot Card */
.ring-slot-card-detail {
  background: linear-gradient(145deg, rgba(30, 25, 25, 0.9), rgba(20, 15, 15, 0.95));
  border: 2px solid rgba(255, 51, 51, 0.2);
  border-radius: 10px;
  padding: 1rem 1.25rem;
  cursor: grab;
  transition: all 0.2s;
  display: grid;
  grid-template-columns: 120px 1fr auto auto;
  gap: 1rem;
  align-items: center;
}

.ring-slot-card-detail:hover {
  border-color: rgba(255, 51, 51, 0.4);
  background: linear-gradient(145deg, rgba(40, 30, 30, 0.9), rgba(25, 20, 20, 0.95));
}

.ring-slot-card-detail:active {
  cursor: grabbing;
}

.ring-slot-card-detail.pause-slot {
  background: rgba(34, 197, 94, 0.1);
  border-color: rgba(34, 197, 94, 0.3);
  cursor: default;
  display: flex;
  justify-content: space-between;
  padding: 0.75rem 1.25rem;
}

/* Time Block Detail */
.slot-time-block-detail {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.slot-time-block-detail .start-time {
  font-size: 1.25rem;
  font-weight: 700;
  color: #22c55e;
}

.slot-time-block-detail .time-dash {
  display: none;
}

.slot-time-block-detail .end-time {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.5);
}

/* Main Detail */
.slot-main-detail {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

.slot-main-detail .slot-division {
  font-size: 1.05rem;
  font-weight: 600;
  color: #fff;
}

.slot-discipline {
  display: inline-block;
  padding: 0.2rem 0.5rem;
  border-radius: 4px;
  font-size: 0.75rem;
  font-weight: 500;
  width: fit-content;
}

.slot-discipline.formen {
  background: rgba(147, 51, 234, 0.2);
  color: #a78bfa;
  border: 1px solid rgba(147, 51, 234, 0.3);
}

.slot-discipline.selbstverteidigung {
  background: rgba(236, 72, 153, 0.2);
  color: #f472b6;
  border: 1px solid rgba(236, 72, 153, 0.3);
}

.slot-discipline.kickboxen,
.slot-discipline.freefight,
.slot-discipline.pointfighting {
  background: rgba(239, 68, 68, 0.2);
  color: #f87171;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

.slot-discipline.default {
  background: rgba(107, 114, 128, 0.2);
  color: #9ca3af;
  border: 1px solid rgba(107, 114, 128, 0.3);
}

/* Info Detail */
.slot-info-detail {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5);
}

/* Actions Detail */
.slot-actions-detail {
  display: flex;
  gap: 0.4rem;
  align-items: center;
}

.slot-actions-detail .action-btn {
  width: 32px;
  height: 32px;
  border: none;
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  transition: all 0.2s;
}

.slot-actions-detail .action-btn:hover {
  background: rgba(255, 51, 51, 0.2);
  color: #ff6b6b;
}

.slot-actions-detail .action-btn.pause:hover {
  background: rgba(34, 197, 94, 0.2);
  color: #22c55e;
}

.slot-actions-detail .action-btn.delete {
  color: rgba(255, 100, 100, 0.8);
}

.slot-actions-detail .action-btn.delete:hover {
  background: rgba(255, 51, 51, 0.3);
  color: #ff4444;
}

.btn-delete-slot {
  padding: 0.4rem 0.8rem;
  background: rgba(255, 51, 51, 0.2);
  border: 1px solid rgba(255, 51, 51, 0.4);
  border-radius: 6px;
  color: #ff6b6b;
  font-size: 0.8rem;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-delete-slot:hover {
  background: rgba(255, 51, 51, 0.4);
  border-color: #ff4444;
  color: #fff;
}

.btn-delete-all {
  padding: 0.6rem 1rem;
  background: rgba(255, 51, 51, 0.15);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 8px;
  color: #ff6b6b;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-delete-all:hover {
  background: rgba(255, 51, 51, 0.3);
  border-color: #ff4444;
  color: #fff;
}

/* Verfgbare Kategorien Card */
.available-categories {
  min-width: 300px;
}

.available-categories .hint-text,
.tatami-card .hint-text {
  margin-top: 1rem;
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.5);
  text-align: center;
}

.tatami-card.clickable {
  cursor: pointer;
}

.tatami-card.clickable:hover {
  border-color: #f59e0b;
}

/* Drag & Drop Target */
.tatami-card.drop-target {
  border-style: dashed;
}

.tatami-card.drag-over {
  background: rgba(34, 197, 94, 0.2) !important;
  border-color: #22c55e !important;
  transform: scale(1.02);
}

/* Nicht zugeordnet Panel */
.unassigned-panel {
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  max-height: 400px;
  overflow-y: auto;
}

.unassigned-panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.unassigned-panel-header h3 {
  margin: 0;
  color: #f59e0b;
}

.unassigned-panel .panel-hint {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 1rem;
}

.unassigned-divisions-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.unassigned-division-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  padding: 0.75rem 1rem;
  cursor: grab;
  transition: all 0.2s;
}

.unassigned-division-item:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.2);
}

.unassigned-division-item:active {
  cursor: grabbing;
}

.division-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.division-code {
  font-weight: 600;
  color: #fff;
}

.division-meta {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5);
}

.ring-assign-select {
  padding: 0.5rem 0.75rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.85rem;
  cursor: pointer;
}

.ring-assign-select:hover {
  border-color: #22c55e;
}

.ring-assign-select option {
  background: #1a1a1a;
}

.all-assigned {
  text-align: center;
  color: #22c55e;
  padding: 2rem;
}

/* Tatami Modal - Kompakt */
.tatami-modal {
  max-width: 600px;
  width: 95%;
  max-height: 80vh;
  overflow-y: auto;
}

.tatami-modal .modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  position: sticky;
  top: 0;
  background: #1e293b;
  z-index: 10;
}

.tatami-modal .modal-header h3 {
  margin: 0;
  color: #fff;
  font-size: 1rem;
}

.tatami-form {
  padding: 1rem;
}

.form-section {
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.form-section:last-child {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}

.form-section h4 {
  margin: 0 0 0.6rem 0;
  color: #f59e0b;
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.form-hint {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 0.5rem;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.75rem;
  margin-bottom: 0.75rem;
}

.form-row:last-child {
  margin-bottom: 0;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.form-group label {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.7);
}

.form-group input[type="text"],
.form-group input[type="number"],
.form-group select,
.form-group textarea {
  padding: 0.5rem 0.65rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  color: #fff;
  font-size: 0.85rem;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #f59e0b;
}

.form-group input[type="color"] {
  width: 50px;
  height: 32px;
  padding: 2px;
  border-radius: 6px;
  cursor: pointer;
}

.form-group select option {
  background: #1a1a1a;
}

.form-group textarea {
  resize: vertical;
  min-height: 50px;
}

.checkbox-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
  gap: 0.4rem;
}

.checkbox-item {
  display: flex;
  align-items: center;
  gap: 0.4rem;
  padding: 0.35rem 0.5rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
}

.checkbox-item:hover {
  background: rgba(255, 255, 255, 0.1);
}

.checkbox-item input[type="checkbox"] {
  width: 14px;
  height: 14px;
  accent-color: #22c55e;
}

.checkbox-item span {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.8rem;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 0.75rem;
  padding: 0.75rem 1rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  position: sticky;
  bottom: 0;
  background: #1e293b;
}

.btn-cancel {
  padding: 0.5rem 1rem;
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 6px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-cancel:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.5);
}

.btn-primary {
  padding: 0.5rem 1rem;
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  border: none;
  border-radius: 6px;
  color: #fff;
  font-weight: 600;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-primary:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(245, 158, 11, 0.3);
}

@media (max-width: 600px) {
  .form-row {
    grid-template-columns: 1fr;
  }

  .checkbox-grid {
    grid-template-columns: 1fr 1fr;
  }

  .tatami-modal {
    max-height: 90vh;
  }
}

/* Frage-Modal */
.question-modal {
  max-width: 420px;
  width: 90%;
}

.question-text {
  font-size: 1rem;
  color: #fff;
  margin-bottom: 1rem;
}

.default-preview {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  padding: 0.75rem 1rem;
}

.preview-item {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.7);
  padding: 0.25rem 0;
}

.preview-item strong {
  color: rgba(255, 255, 255, 0.9);
}

.question-actions {
  display: flex;
  gap: 0.5rem;
}

.btn-secondary {
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 6px;
  color: #fff;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.2);
}

/* Erweitertes Einstellungen-Panel */
.settings-panel.extended {
  max-width: 700px;
  max-height: 70vh;
  overflow-y: auto;
}

.settings-section {
  margin-bottom: 1.25rem;
  padding-bottom: 1.25rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.settings-section:last-child {
  border-bottom: none;
  margin-bottom: 0;
}

.settings-section h4 {
  margin: 0 0 0.5rem 0;
  color: #f59e0b;
  font-size: 0.85rem;
  text-transform: uppercase;
}

.settings-hint {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 0.75rem;
}

.setting-group.full-width {
  grid-column: 1 / -1;
}

.checkbox-grid.compact {
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: 0.35rem;
}

.checkbox-grid.compact .checkbox-item {
  padding: 0.3rem 0.5rem;
}

.checkbox-grid.compact .checkbox-item span {
  font-size: 0.75rem;
}

.settings-actions {
  display: flex;
  justify-content: flex-end;
  padding-top: 1rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  margin-top: 1rem;
}

/* Tatami Header mit Lschen-Button */
.tatami-header-actions {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  z-index: 10;
}

.btn-delete-tatami {
  display: flex !important;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  min-width: 28px;
  border: 2px solid rgba(255, 80, 80, 0.6) !important;
  border-radius: 6px;
  background: rgba(255, 50, 50, 0.4) !important;
  color: #ff6b6b !important;
  font-size: 1.4rem;
  font-weight: bold;
  line-height: 1;
  cursor: pointer;
  transition: all 0.2s;
  z-index: 20;
  position: relative;
}

.btn-delete-tatami:hover {
  background: rgba(255, 50, 50, 0.8) !important;
  border-color: #ff4444 !important;
  color: #fff !important;
  transform: scale(1.1);
}

.slot-actions-detail .ring-select {
  padding: 0.4rem 0.6rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.8rem;
  cursor: pointer;
}

.slot-actions-detail .ring-select option {
  background: #1a1a1a;
}

/* Pause Content */
.pause-content {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.pause-icon {
  font-size: 1.25rem;
}

.pause-label {
  font-weight: 600;
  color: #22c55e;
  font-size: 1rem;
}

.pause-time {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.5);
}

.delete-pause {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.4);
  font-size: 1.25rem;
  cursor: pointer;
  padding: 0.3rem 0.5rem;
}

.delete-pause:hover {
  color: #ef4444;
}

/* Loading */
.loading-state {
  text-align: center;
  padding: 3rem;
  color: rgba(255, 255, 255, 0.6);
}

/* Responsive */
@media (max-width: 1024px) {
  .tatami-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }

  .ring-slot-card-detail {
    grid-template-columns: 100px 1fr auto;
  }

  .slot-info-detail {
    display: none;
  }
}

@media (max-width: 768px) {
  .zeitplan-header {
    flex-direction: column;
  }

  .tatami-grid {
    grid-template-columns: 1fr;
  }

  .ring-slot-card-detail {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }

  .slot-time-block-detail {
    flex-direction: row;
    align-items: center;
    gap: 0.5rem;
  }

  .slot-time-block-detail .time-dash {
    display: inline;
    color: rgba(255, 255, 255, 0.3);
  }

  .slot-actions-detail {
    justify-content: flex-end;
  }

  .settings-grid {
    flex-direction: column;
  }
}
