/* ============================================================================
   DIVISION CARD - Dashboard Style
   ============================================================================ */

/* =====================
   BASE CARD
   ===================== */
.division-card {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 12px;
  padding: 1rem;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.division-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #ff3333, #ff6b35);
}

.division-card:hover {
  transform: translateY(-4px);
  border-color: rgba(255, 51, 51, 0.4);
  box-shadow: 0 8px 30px rgba(255, 51, 51, 0.2);
}

/* =====================
   CARD HEADER
   ===================== */
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid rgba(255, 51, 51, 0.2);
}

.card-header h3 {
  font-size: 1.1rem;
  font-weight: 700;
  margin: 0;
  background: linear-gradient(135deg, #ff3333, #ff6b35);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* =====================
   CARD ACTIONS
   ===================== */
.card-actions {
  display: flex;
  gap: 0.5rem;
  flex-shrink: 0;
}

.card-actions .btn {
  padding: 0.4rem 0.75rem;
  font-size: 0.75rem;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.3rem;
}

.btn-edit,
.card-actions .btn-secondary {
  background: rgba(255, 51, 51, 0.15);
  border: 1px solid rgba(255, 51, 51, 0.3);
  color: #ff3333;
}

.btn-edit:hover,
.card-actions .btn-secondary:hover {
  background: rgba(255, 51, 51, 0.25);
  border-color: #ff3333;
}

.btn-delete,
.card-actions .btn-danger {
  background: rgba(255, 100, 100, 0.15);
  border: 1px solid rgba(255, 100, 100, 0.3);
  color: #ff6b6b;
}

.btn-delete:hover,
.card-actions .btn-danger:hover {
  background: rgba(255, 100, 100, 0.25);
  border-color: #ff6b6b;
}

/* =====================
   CARD CONTENT
   ===================== */
.card-content {
  line-height: 1.5;
}

.view-content p {
  margin: 0.4rem 0;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.85rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.view-content p strong {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 500;
  min-width: 90px;
}

/* =====================
   EDIT FORM
   ===================== */
.edit-form {
  margin-top: 0.5rem;
}

.form-group {
  margin-bottom: 0.75rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.4rem;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
  font-size: 0.8rem;
}

.form-input,
.form-select {
  width: 100%;
  padding: 0.6rem 0.75rem;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 6px;
  color: #fff;
  font-size: 0.85rem;
  transition: all 0.3s ease;
}

.form-input:focus,
.form-select:focus {
  outline: none;
  border-color: #ff3333;
  box-shadow: 0 0 10px rgba(255, 51, 51, 0.2);
}

.form-input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.form-select option {
  background: #1a1a2e;
  color: #fff;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.75rem;
}

/* =====================
   FORM ACTIONS
   ===================== */
.form-actions {
  display: flex;
  gap: 0.5rem;
  margin-top: 1rem;
  padding-top: 0.75rem;
  border-top: 1px solid rgba(255, 51, 51, 0.2);
}

.form-actions .btn {
  flex: 1;
  padding: 0.6rem 1rem;
  font-size: 0.85rem;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.4rem;
}

.btn-save,
.form-actions .btn-primary {
  background: linear-gradient(135deg, #ff3333, #cc0000);
  border: none;
  color: #fff;
  box-shadow: 0 4px 12px rgba(255, 51, 51, 0.3);
}

.btn-save:hover,
.form-actions .btn-primary:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 16px rgba(255, 51, 51, 0.4);
}

.btn-cancel,
.form-actions .btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.7);
}

.btn-cancel:hover,
.form-actions .btn-secondary:hover {
  border-color: rgba(255, 255, 255, 0.4);
  color: #fff;
  background: rgba(255, 255, 255, 0.15);
}

/* =====================
   LOADING OVERLAY
   ===================== */
.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 12px;
  z-index: 10;
}

.loading-spinner {
  width: 30px;
  height: 30px;
  border: 3px solid rgba(255, 255, 255, 0.1);
  border-top: 3px solid #ff3333;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* =====================
   RESPONSIVE
   ===================== */
@media (max-width: 768px) {
  .division-card {
    padding: 0.75rem;
  }

  .card-header {
    flex-direction: column;
    gap: 0.75rem;
  }

  .card-actions {
    width: 100%;
    justify-content: flex-end;
  }

  .form-row {
    grid-template-columns: 1fr;
  }

  .form-actions {
    flex-direction: column;
  }

  .card-header h3 {
    font-size: 1rem;
  }
}
