/* BruchtestDashboard.css - Bruchtest Wertungssystem */

.bruchtest-dashboard {
  min-height: 100vh;
  background: #000;
  padding: 0.5rem 1rem 1rem 1rem;
  color: #fff;
}

/* Header */
.bruchtest-dashboard .bruchtest-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding: 1rem 1.5rem;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 12px;
  border: 1px solid rgba(255, 51, 51, 0.3);
  flex-wrap: wrap;
  gap: 1rem;
}

.bruchtest-dashboard .header-title {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.bruchtest-dashboard .header-title h1 {
  font-size: 1.25rem;
  margin: 0;
  color: #ff3333;
  white-space: nowrap;
}

.bruchtest-dashboard .header-kategorie {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.bruchtest-dashboard .header-kategorie select {
  padding: 0.4rem 0.75rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  color: #fff;
  font-size: 0.85rem;
  cursor: pointer;
}

.bruchtest-dashboard .header-kategorie select:focus {
  outline: none;
  border-color: #ff3333;
}

.bruchtest-dashboard .modus-badge {
  padding: 0.3rem 0.6rem;
  background: rgba(255, 51, 51, 0.25);
  border: 1px solid #ff3333;
  border-radius: 4px;
  font-size: 0.75rem;
  font-weight: 600;
  color: #ff3333;
}

/* Header Settings */
.bruchtest-dashboard .header-settings {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  flex-wrap: wrap;
}

.bruchtest-dashboard .setting-group {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.bruchtest-dashboard .setting-group label {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.8rem;
}

.bruchtest-dashboard .setting-group select {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #fff;
  padding: 0.4rem 0.75rem;
  border-radius: 6px;
  font-size: 0.85rem;
}

.bruchtest-dashboard .modus-buttons {
  display: flex;
  gap: 0.25rem;
}

.bruchtest-dashboard .modus-btn {
  padding: 0.4rem 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 6px;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  font-size: 0.8rem;
  transition: all 0.15s;
}

.bruchtest-dashboard .modus-btn:hover {
  background: rgba(255, 51, 51, 0.15);
}

.bruchtest-dashboard .modus-btn.active {
  background: rgba(255, 51, 51, 0.25);
  border-color: #ff3333;
  color: #fff;
}

.bruchtest-dashboard .max-techniken-input {
  display: flex;
  align-items: center;
  gap: 0.35rem;
}

.bruchtest-dashboard .max-techniken-input button {
  width: 28px;
  height: 28px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  font-size: 1rem;
}

.bruchtest-dashboard .max-techniken-input span {
  min-width: 30px;
  text-align: center;
  font-weight: bold;
  color: #ff3333;
}

/* Loading & Empty States */
.bruchtest-dashboard .loading-state,
.bruchtest-dashboard .empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem;
  text-align: center;
}

.bruchtest-dashboard .loading-spinner,
.bruchtest-dashboard .empty-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.bruchtest-dashboard .loading-spinner {
  animation: pulse 1s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { transform: scale(1); opacity: 1; }
  50% { transform: scale(1.1); opacity: 0.7; }
}

/* Kategorie-Dropdown Section */
.bruchtest-dashboard .kategorie-dropdown-section {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.75rem 1rem;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 8px;
  border: 1px solid rgba(255, 51, 51, 0.3);
  margin-bottom: 1rem;
}

.bruchtest-dashboard .kategorie-dropdown-section label {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
  white-space: nowrap;
}

.bruchtest-dashboard .kategorie-dropdown-section select {
  flex: 1;
  max-width: 400px;
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  color: #fff;
  font-size: 0.9rem;
  cursor: pointer;
}

.bruchtest-dashboard .kategorie-dropdown-section select:focus {
  outline: none;
  border-color: #ff3333;
}

.bruchtest-dashboard .modus-anzeige {
  padding: 0.4rem 0.8rem;
  background: rgba(255, 51, 51, 0.15);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 6px;
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.8);
}

.bruchtest-dashboard .modus-anzeige strong {
  color: #ff3333;
}

/* Content Layout - NEU */
.bruchtest-dashboard .bruchtest-content {
  display: grid;
  grid-template-columns: 280px 1fr;
  gap: 1.5rem;
}

.bruchtest-dashboard .bruchtest-content.new-layout {
  grid-template-columns: 1fr 260px;
}

/* Sidebar */
.bruchtest-dashboard .bruchtest-sidebar {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.bruchtest-dashboard .sidebar-section {
  background: rgba(0, 0, 0, 0.6);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  overflow: hidden;
}

.bruchtest-dashboard .sidebar-section h3 {
  margin: 0;
  padding: 0.75rem 1rem;
  background: rgba(255, 51, 51, 0.15);
  border-bottom: 1px solid rgba(255, 51, 51, 0.3);
  font-size: 0.9rem;
  color: #ff3333;
}

.bruchtest-dashboard .kategorie-list,
.bruchtest-dashboard .teilnehmer-list {
  max-height: 250px;
  overflow-y: auto;
}

.bruchtest-dashboard .kategorie-item,
.bruchtest-dashboard .teilnehmer-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 1rem;
  cursor: pointer;
  transition: all 0.2s;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.bruchtest-dashboard .kategorie-item:hover,
.bruchtest-dashboard .teilnehmer-item:hover {
  background: rgba(255, 51, 51, 0.1);
}

.bruchtest-dashboard .kategorie-item.active,
.bruchtest-dashboard .teilnehmer-item.active {
  background: rgba(255, 51, 51, 0.2);
  border-left: 3px solid #ff3333;
}

.bruchtest-dashboard .kategorie-count {
  background: rgba(255, 255, 255, 0.1);
  padding: 0.2rem 0.5rem;
  border-radius: 10px;
  font-size: 0.75rem;
}

.bruchtest-dashboard .teilnehmer-name {
  font-weight: 500;
}

.bruchtest-dashboard .teilnehmer-verein {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.5);
}

.bruchtest-dashboard .teilnehmer-item .teilnehmer-count {
  font-size: 0.7rem;
  padding: 0.15rem 0.4rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  color: rgba(255, 255, 255, 0.7);
  margin-left: auto;
}

.bruchtest-dashboard .teilnehmer-item.complete {
  border-left: 3px solid #00c853;
}

.bruchtest-dashboard .teilnehmer-item.complete .teilnehmer-count {
  background: rgba(0, 200, 83, 0.2);
  color: #00c853;
}

/* Main Content */
.bruchtest-dashboard .bruchtest-main {
  background: rgba(0, 0, 0, 0.6);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 1.5rem;
}

.bruchtest-dashboard .teilnehmer-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.bruchtest-dashboard .teilnehmer-header h2 {
  margin: 0 0 0.15rem 0;
  font-size: 1.5rem;
}

.bruchtest-dashboard .teilnehmer-header .verein {
  color: rgba(255, 255, 255, 0.6);
}

.bruchtest-dashboard .technik-counter {
  text-align: right;
}

.bruchtest-dashboard .technik-counter .counter-label {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.5);
  display: block;
}

.bruchtest-dashboard .technik-counter .counter-value {
  font-size: 1.5rem;
  font-weight: bold;
  color: #ff3333;
}

/* Bereits durchgefhrte Techniken */
.bruchtest-dashboard .performed-techniken {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  padding: 0.5rem 0.75rem;
  margin-bottom: 0.75rem;
}

.bruchtest-dashboard .performed-label {
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.5);
  text-transform: uppercase;
  display: block;
  margin-bottom: 0.35rem;
}

.bruchtest-dashboard .performed-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.bruchtest-dashboard .performed-item {
  display: flex;
  align-items: center;
  gap: 0.35rem;
  padding: 0.3rem 0.6rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  font-size: 0.8rem;
}

.bruchtest-dashboard .performed-item.mit-sprung {
  border-color: rgba(0, 200, 83, 0.4);
  background: rgba(0, 200, 83, 0.1);
}

.bruchtest-dashboard .performed-num {
  color: rgba(255, 255, 255, 0.4);
  font-weight: bold;
}

.bruchtest-dashboard .performed-name {
  color: #fff;
}

.bruchtest-dashboard .sprung-badge {
  font-size: 0.65rem;
  padding: 0.1rem 0.3rem;
  background: #00c853;
  color: #000;
  border-radius: 3px;
  font-weight: 600;
}

.bruchtest-dashboard .performed-punkte {
  color: #ff3333;
  font-weight: 600;
  margin-left: auto;
}

/* Bewertungsformular */
.bruchtest-dashboard .bewertung-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.bruchtest-dashboard .form-section {
  background: rgba(255, 255, 255, 0.03);
  padding: 1rem;
  border-radius: 8px;
}

.bruchtest-dashboard .form-section h3 {
  margin: 0 0 1rem 0;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Technik Grid */
.bruchtest-dashboard .technik-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: 0.5rem;
}

.bruchtest-dashboard .technik-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  color: #fff;
}

.bruchtest-dashboard .technik-btn:hover {
  background: rgba(255, 51, 51, 0.1);
  border-color: rgba(255, 51, 51, 0.3);
}

.bruchtest-dashboard .technik-btn.active {
  background: rgba(255, 51, 51, 0.2);
  border-color: #ff3333;
}

.bruchtest-dashboard .technik-icon {
  font-size: 1.5rem;
  margin-bottom: 0.25rem;
}

.bruchtest-dashboard .technik-name {
  font-size: 0.75rem;
  text-align: center;
}

.bruchtest-dashboard .technik-mult {
  font-size: 0.65rem;
  color: rgba(255, 255, 255, 0.5);
  margin-top: 0.25rem;
}

/* Number Input */
.bruchtest-dashboard .number-input {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.bruchtest-dashboard .number-input button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid rgba(255, 51, 51, 0.5);
  background: rgba(255, 51, 51, 0.1);
  color: #ff3333;
  font-size: 1.25rem;
  cursor: pointer;
  transition: all 0.2s;
}

.bruchtest-dashboard .number-input button:hover {
  background: rgba(255, 51, 51, 0.3);
}

.bruchtest-dashboard .number-display {
  font-size: 2.5rem;
  font-weight: bold;
  min-width: 80px;
  text-align: center;
}

.bruchtest-dashboard .number-input.small button {
  width: 30px;
  height: 30px;
  font-size: 1rem;
}

.bruchtest-dashboard .number-input.small span {
  font-size: 1.25rem;
  min-width: 40px;
}

/* Sliders */
.bruchtest-dashboard .sliders {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}

.bruchtest-dashboard .slider-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.bruchtest-dashboard .slider-group label {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.7);
}

.bruchtest-dashboard .slider-group input[type="range"] {
  width: 100%;
  height: 6px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
  -webkit-appearance: none;
}

.bruchtest-dashboard .slider-group input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: #ff3333;
  cursor: pointer;
}

/* Inline Section */
.bruchtest-dashboard .form-section.inline {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.bruchtest-dashboard .versuche-group,
.bruchtest-dashboard .erfolg-group {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.bruchtest-dashboard .erfolg-toggle {
  display: flex;
  gap: 0.5rem;
}

.bruchtest-dashboard .erfolg-btn {
  padding: 0.5rem 1rem;
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  transition: all 0.2s;
}

.bruchtest-dashboard .erfolg-btn.active.success {
  background: rgba(34, 197, 94, 0.2);
  border-color: #22c55e;
  color: #22c55e;
}

.bruchtest-dashboard .erfolg-btn.active.fail {
  background: rgba(239, 68, 68, 0.2);
  border-color: #ef4444;
  color: #ef4444;
}

/* Textarea */
.bruchtest-dashboard textarea {
  width: 100%;
  min-height: 60px;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  color: #fff;
  resize: vertical;
  margin-top: 0.5rem;
}

/* Footer */
.bruchtest-dashboard .bewertung-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5rem;
  padding-top: 1.5rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.bruchtest-dashboard .punkte-display {
  display: flex;
  align-items: baseline;
  gap: 0.75rem;
}

.bruchtest-dashboard .punkte-label {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.7);
}

.bruchtest-dashboard .punkte-value {
  font-size: 2.5rem;
  font-weight: bold;
  color: #ff3333;
}

.bruchtest-dashboard .save-btn {
  padding: 0.75rem 2rem;
  background: linear-gradient(135deg, #ff3333, #cc0000);
  border: none;
  border-radius: 8px;
  color: #fff;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.bruchtest-dashboard .save-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(255, 51, 51, 0.4);
}

.bruchtest-dashboard .no-teilnehmer {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 300px;
  color: rgba(255, 255, 255, 0.5);
}

/* Responsive */
@media (max-width: 900px) {
  .bruchtest-dashboard .bruchtest-content {
    grid-template-columns: 1fr;
  }

  .bruchtest-dashboard .bruchtest-sidebar {
    flex-direction: row;
  }

  .bruchtest-dashboard .sidebar-section {
    flex: 1;
  }

  .bruchtest-dashboard .sliders {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 600px) {
  .bruchtest-dashboard .bruchtest-header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }

  .bruchtest-dashboard .technik-grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .bruchtest-dashboard .form-section.inline {
    flex-direction: column;
    align-items: flex-start;
  }

  .bruchtest-dashboard .bewertung-footer {
    flex-direction: column;
    gap: 1rem;
  }

  .bruchtest-dashboard .save-btn {
    width: 100%;
  }
}

/* ====== KOMPAKTES LAYOUT ====== */

/* Form Rows */
.bruchtest-dashboard .bewertung-form.compact {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.bruchtest-dashboard .form-row {
  display: flex;
  gap: 1rem;
  background: rgba(255, 255, 255, 0.03);
  padding: 0.75rem;
  border-radius: 8px;
}

/* Top Row: Material + Anzahl + Sprung */
.bruchtest-dashboard .top-row {
  display: grid;
  grid-template-columns: 1fr 150px 1fr;
  align-items: center;
  gap: 1rem;
}

.bruchtest-dashboard .anzahl-cell {
  min-width: 140px;
  text-align: center;
}

.bruchtest-dashboard .form-cell label {
  display: block;
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.5);
  text-transform: uppercase;
  margin-bottom: 0.35rem;
}

.bruchtest-dashboard .material-buttons,
.bruchtest-dashboard .sprung-buttons {
  display: flex;
  gap: 0.5rem;
}

.bruchtest-dashboard .mat-btn,
.bruchtest-dashboard .spr-btn {
  padding: 0.5rem 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 6px;
  color: #fff;
  cursor: pointer;
  font-size: 0.8rem;
  transition: all 0.15s;
}

.bruchtest-dashboard .mat-btn:hover,
.bruchtest-dashboard .spr-btn:hover {
  background: rgba(255, 51, 51, 0.15);
}

.bruchtest-dashboard .mat-btn.active,
.bruchtest-dashboard .spr-btn.active {
  background: rgba(255, 51, 51, 0.25);
  border-color: #ff3333;
}

.bruchtest-dashboard .spr-btn.bonus.active {
  background: rgba(0, 200, 83, 0.25);
  border-color: #00c853;
  color: #00c853;
}

/* Anzahl compact */
.bruchtest-dashboard .number-input.compact {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.bruchtest-dashboard .number-input.compact button {
  width: 36px;
  height: 36px;
  font-size: 1.2rem;
}

.bruchtest-dashboard .number-input.compact span {
  font-size: 1.5rem;
  font-weight: bold;
  min-width: 40px;
  text-align: center;
}

/* Technik Compact */
.bruchtest-dashboard .technik-row {
  padding: 0.5rem;
}

.bruchtest-dashboard .technik-compact {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.75rem;
  width: 100%;
}

.bruchtest-dashboard .technik-col {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.bruchtest-dashboard .col-label {
  font-size: 0.7rem;
  color: #ff3333;
  text-transform: uppercase;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.bruchtest-dashboard .tech-btn {
  padding: 0.4rem 0.5rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  font-size: 0.75rem;
  text-align: left;
  transition: all 0.15s;
}

.bruchtest-dashboard .tech-btn:hover {
  background: rgba(255, 51, 51, 0.15);
}

.bruchtest-dashboard .tech-btn.active {
  background: rgba(255, 51, 51, 0.3);
  border-color: #ff3333;
}

.bruchtest-dashboard .tech-btn small {
  color: rgba(255, 255, 255, 0.4);
  margin-left: 0.25rem;
}

/* Slider Row */
.bruchtest-dashboard .slider-row {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr auto;
  gap: 1rem;
  align-items: end;
}

.bruchtest-dashboard .slider-compact {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.bruchtest-dashboard .slider-compact label {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6);
}

.bruchtest-dashboard .slider-compact label strong {
  color: #ff3333;
  font-size: 1rem;
  margin-left: 0.5rem;
}

.bruchtest-dashboard .slider-compact input[type="range"] {
  width: 100%;
  height: 6px;
  -webkit-appearance: none;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

.bruchtest-dashboard .slider-compact input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 18px;
  height: 18px;
  background: #ff3333;
  border-radius: 50%;
  cursor: pointer;
}

/* Zusatzpunkte */
.bruchtest-dashboard .zusatz-compact {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  min-width: 100px;
}

.bruchtest-dashboard .zusatz-compact label {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6);
}

.bruchtest-dashboard .zusatz-compact input {
  width: 100%;
  padding: 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  color: #fff;
  font-size: 1rem;
  text-align: center;
}

.bruchtest-dashboard .zusatz-compact input:focus {
  outline: none;
  border-color: #ff3333;
}

/* ====== TRADITIONELL MODUS ====== */

.bruchtest-dashboard .traditionell-section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* Zugewiesene Techniken */
.bruchtest-dashboard .assigned-techniken {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  padding: 0.75rem;
}

.bruchtest-dashboard .assigned-header {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 0.5rem;
  text-transform: uppercase;
}

.bruchtest-dashboard .assigned-list {
  display: flex;
  flex-direction: column;
  gap: 0.35rem;
  margin-bottom: 0.75rem;
}

.bruchtest-dashboard .assigned-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.15s;
}

.bruchtest-dashboard .assigned-item:hover:not(.done) {
  background: rgba(255, 51, 51, 0.1);
  border-color: rgba(255, 51, 51, 0.3);
}

.bruchtest-dashboard .assigned-item.selected {
  background: rgba(255, 51, 51, 0.2);
  border-color: #ff3333;
  box-shadow: 0 0 0 2px rgba(255, 51, 51, 0.3);
}

.bruchtest-dashboard .assigned-item.done {
  background: rgba(0, 200, 83, 0.1);
  border-color: rgba(0, 200, 83, 0.3);
  cursor: default;
  opacity: 0.7;
}

.bruchtest-dashboard .assigned-num {
  font-weight: bold;
  color: rgba(255, 255, 255, 0.5);
  min-width: 20px;
}

.bruchtest-dashboard .assigned-name {
  font-weight: 500;
  min-width: 120px;
}

/* Anzahl Bretter pro Technik */
.bruchtest-dashboard .assigned-anzahl {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  margin-left: auto;
  margin-right: 1rem;
}

.bruchtest-dashboard .assigned-anzahl button {
  width: 24px;
  height: 24px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bruchtest-dashboard .assigned-anzahl button:hover:not(:disabled) {
  background: rgba(255, 51, 51, 0.2);
}

.bruchtest-dashboard .assigned-anzahl button:disabled {
  opacity: 0.4;
  cursor: default;
}

.bruchtest-dashboard .assigned-anzahl span {
  min-width: 24px;
  text-align: center;
  font-weight: bold;
  font-size: 1rem;
}

.bruchtest-dashboard .assigned-anzahl .anzahl-label {
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.5);
  font-weight: normal;
}

.bruchtest-dashboard .assigned-actions {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.bruchtest-dashboard .sprung-label {
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.5);
}

.bruchtest-dashboard .sprung-toggle {
  padding: 0.25rem 0.6rem;
  border-radius: 4px;
  font-size: 0.75rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.15s;
  min-width: 45px;
  text-align: center;
}

.bruchtest-dashboard .sprung-toggle.yes {
  background: rgba(0, 200, 83, 0.25);
  border: 1px solid #00c853;
  color: #00c853;
}

.bruchtest-dashboard .sprung-toggle.yes:hover:not(:disabled) {
  background: rgba(0, 200, 83, 0.4);
}

.bruchtest-dashboard .sprung-toggle.no {
  background: rgba(239, 68, 68, 0.2);
  border: 1px solid #ef4444;
  color: #ef4444;
}

.bruchtest-dashboard .sprung-toggle.no:hover:not(:disabled) {
  background: rgba(239, 68, 68, 0.35);
}

.bruchtest-dashboard .sprung-toggle:disabled {
  opacity: 0.5;
  cursor: default;
}

.bruchtest-dashboard .remove-btn {
  width: 24px;
  height: 24px;
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  border-radius: 4px;
  color: #ef4444;
  cursor: pointer;
  font-size: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.15s;
}

.bruchtest-dashboard .remove-btn:hover {
  background: rgba(239, 68, 68, 0.25);
}

.bruchtest-dashboard .done-badge {
  padding: 0.2rem 0.5rem;
  background: rgba(0, 200, 83, 0.2);
  border-radius: 4px;
  color: #00c853;
  font-size: 0.75rem;
  font-weight: 600;
}

/* Technik hinzufgen */
.bruchtest-dashboard .add-technik {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 0.75rem;
}

.bruchtest-dashboard .add-label {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.6);
  display: block;
  margin-bottom: 0.75rem;
  text-transform: uppercase;
  font-weight: 600;
}

.bruchtest-dashboard .technik-kategorie {
  margin-bottom: 0.75rem;
}

.bruchtest-dashboard .kat-label {
  display: block;
  font-size: 0.75rem;
  color: #ff3333;
  text-transform: uppercase;
  font-weight: 600;
  margin-bottom: 0.4rem;
}

.bruchtest-dashboard .technik-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
}

.bruchtest-dashboard .technik-buttons .tech-btn {
  padding: 0.4rem 0.7rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 4px;
  color: #fff;
  font-size: 0.8rem;
  cursor: pointer;
  transition: all 0.15s;
}

.bruchtest-dashboard .technik-buttons .tech-btn:hover {
  background: rgba(255, 51, 51, 0.2);
  border-color: rgba(255, 51, 51, 0.4);
}

.bruchtest-dashboard .add-tech-btn {
  padding: 0.35rem 0.6rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 4px;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.75rem;
  cursor: pointer;
  transition: all 0.15s;
}

.bruchtest-dashboard .add-tech-btn:hover {
  background: rgba(255, 51, 51, 0.15);
  border-color: rgba(255, 51, 51, 0.3);
}

.bruchtest-dashboard .add-tech-btn:disabled,
.bruchtest-dashboard .add-tech-btn.assigned {
  opacity: 0.4;
  cursor: default;
}

/* Bewertungsformular Traditionell */
.bruchtest-dashboard .trad-bewertung-form {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.bruchtest-dashboard .selected-technik-info {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.bruchtest-dashboard .selected-label {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.5);
}

.bruchtest-dashboard .selected-technik {
  font-size: 1.1rem;
  font-weight: 600;
  color: #ff3333;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.bruchtest-dashboard .selected-technik .mult-badge {
  font-size: 0.75rem;
  padding: 0.15rem 0.5rem;
  background: rgba(255, 51, 51, 0.3);
  color: #ff3333;
  border-radius: 3px;
  font-weight: 600;
}

.bruchtest-dashboard .selected-technik .sprung-badge {
  font-size: 0.75rem;
  padding: 0.15rem 0.5rem;
  background: #00c853;
  color: #000;
  border-radius: 3px;
  font-weight: 600;
}

/* ====== TECHNIK ITEM FULL ====== */
.bruchtest-dashboard .assigned-item-full {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  padding: 0.75rem;
  margin-bottom: 0.75rem;
}

.bruchtest-dashboard .assigned-item-full.done {
  background: rgba(0, 200, 83, 0.1);
  border-color: rgba(0, 200, 83, 0.3);
  opacity: 0.7;
}

.bruchtest-dashboard .item-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.bruchtest-dashboard .item-header .assigned-num {
  font-weight: bold;
  color: #ff3333;
}

.bruchtest-dashboard .item-header .assigned-name {
  font-weight: 600;
  font-size: 1rem;
}

.bruchtest-dashboard .item-header .mult-badge {
  font-size: 0.7rem;
  padding: 0.1rem 0.4rem;
  background: rgba(255, 51, 51, 0.3);
  color: #ff3333;
  border-radius: 3px;
  font-weight: 600;
}

.bruchtest-dashboard .item-header .sprung-mult-badge {
  font-size: 0.7rem;
  padding: 0.1rem 0.4rem;
  background: #00c853;
  color: #000;
  border-radius: 3px;
  font-weight: 600;
}

.bruchtest-dashboard .item-header .remove-btn {
  margin-left: auto;
}

.bruchtest-dashboard .item-header .done-badge {
  margin-left: auto;
  padding: 0.25rem 0.75rem;
  background: rgba(0, 200, 83, 0.2);
  border-radius: 4px;
  color: #00c853;
  font-weight: 600;
}

.bruchtest-dashboard .item-header .expand-icon {
  margin-left: 0.5rem;
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.5);
}

.bruchtest-dashboard .assigned-item-full.done .item-header:hover {
  background: rgba(0, 200, 83, 0.15);
}

.bruchtest-dashboard .assigned-item-full.done.collapsed {
  opacity: 0.8;
}

.bruchtest-dashboard .assigned-item-full.done.expanded {
  opacity: 1;
}

/* Control Rows */
.bruchtest-dashboard .item-controls {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.bruchtest-dashboard .control-row {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  align-items: flex-end;
}

.bruchtest-dashboard .control-group {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.bruchtest-dashboard .control-group label {
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.5);
  text-transform: uppercase;
}

.bruchtest-dashboard .control-group.material-group {
  flex: 0 0 auto;
}

.bruchtest-dashboard .material-btns {
  display: flex;
  gap: 0.25rem;
}

.bruchtest-dashboard .mat-btn-sm {
  width: 32px;
  height: 32px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.15s;
}

.bruchtest-dashboard .mat-btn-sm:hover {
  background: rgba(255, 51, 51, 0.15);
}

.bruchtest-dashboard .mat-btn-sm.active {
  background: rgba(255, 51, 51, 0.3);
  border-color: #ff3333;
}

/* Number Controls */
.bruchtest-dashboard .num-ctrl {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.bruchtest-dashboard .num-ctrl button {
  width: 28px;
  height: 28px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
}

.bruchtest-dashboard .num-ctrl button:hover {
  background: rgba(255, 51, 51, 0.2);
}

.bruchtest-dashboard .num-ctrl span {
  min-width: 28px;
  text-align: center;
  font-weight: bold;
}

/* Toggle Buttons */
.bruchtest-dashboard .toggle-btn {
  padding: 0.35rem 0.6rem;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 600;
  cursor: pointer;
  min-width: 50px;
}

.bruchtest-dashboard .toggle-btn.yes {
  background: rgba(0, 200, 83, 0.25);
  border: 1px solid #00c853;
  color: #00c853;
}

.bruchtest-dashboard .toggle-btn.no {
  background: rgba(239, 68, 68, 0.2);
  border: 1px solid #ef4444;
  color: #ef4444;
}

.bruchtest-dashboard .toggle-btn.success {
  background: rgba(0, 200, 83, 0.25);
  border: 1px solid #00c853;
  color: #00c853;
}

.bruchtest-dashboard .toggle-btn.fail {
  background: rgba(239, 68, 68, 0.2);
  border: 1px solid #ef4444;
  color: #ef4444;
}

/* Slider Row */
.bruchtest-dashboard .control-row.sliders {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr auto;
  gap: 0.75rem;
}

.bruchtest-dashboard .slider-group {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
}

.bruchtest-dashboard .slider-group label {
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.6);
}

.bruchtest-dashboard .slider-group label strong {
  color: #ff3333;
  margin-left: 0.25rem;
}

.bruchtest-dashboard .slider-group input[type="range"] {
  width: 100%;
  height: 6px;
  -webkit-appearance: none;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

.bruchtest-dashboard .slider-group input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 16px;
  height: 16px;
  background: #ff3333;
  border-radius: 50%;
  cursor: pointer;
}

.bruchtest-dashboard .zusatz-group {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
}

.bruchtest-dashboard .zusatz-group label {
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.6);
}

.bruchtest-dashboard .zusatz-group input {
  width: 60px;
  padding: 0.3rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  color: #fff;
  text-align: center;
}

/* Save Row */
.bruchtest-dashboard .save-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 0.5rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  margin-top: 0.5rem;
}

.bruchtest-dashboard .auto-save-hint {
  font-size: 0.7rem;
  color: rgba(0, 200, 83, 0.7);
  font-style: italic;
}

.bruchtest-dashboard .item-punkte {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.bruchtest-dashboard .item-punkte .punkte-label {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.6);
}

.bruchtest-dashboard .item-punkte .punkte-value {
  font-size: 1.5rem;
  font-weight: bold;
  color: #ff3333;
}

.bruchtest-dashboard .item-punkte .punkte-value.zero {
  color: #ef4444;
}

.bruchtest-dashboard .save-item-btn {
  padding: 0.5rem 1.25rem;
  background: linear-gradient(135deg, #00c853, #009624);
  border: none;
  border-radius: 6px;
  color: #fff;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.bruchtest-dashboard .save-item-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(0, 200, 83, 0.4);
}

/* ====== RECHTES PANEL - Teilnehmer mit Platzierung ====== */
.bruchtest-dashboard .bruchtest-right-panel {
  background: rgba(0, 0, 0, 0.6);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  overflow: hidden;
  height: fit-content;
  max-height: calc(100vh - 200px);
  display: flex;
  flex-direction: column;
}

.bruchtest-dashboard .bruchtest-right-panel .panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 1rem;
  background: rgba(255, 51, 51, 0.15);
  border-bottom: 1px solid rgba(255, 51, 51, 0.3);
}

.bruchtest-dashboard .bruchtest-right-panel .panel-header h3 {
  margin: 0;
  font-size: 0.9rem;
  color: #ff3333;
}

.bruchtest-dashboard .bruchtest-right-panel .teilnehmer-anzahl {
  background: rgba(255, 255, 255, 0.1);
  padding: 0.2rem 0.6rem;
  border-radius: 10px;
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.7);
}

.bruchtest-dashboard .teilnehmer-ranking-list {
  overflow-y: auto;
  flex: 1;
}

.bruchtest-dashboard .ranking-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem 0.75rem;
  cursor: pointer;
  transition: all 0.15s;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.bruchtest-dashboard .ranking-item:hover {
  background: rgba(255, 51, 51, 0.1);
}

.bruchtest-dashboard .ranking-item.active {
  background: rgba(255, 51, 51, 0.2);
  border-left: 3px solid #ff3333;
}

.bruchtest-dashboard .ranking-item.complete {
  border-left: 3px solid #00c853;
}

.bruchtest-dashboard .ranking-item .platzierung {
  font-weight: bold;
  font-size: 0.85rem;
  min-width: 24px;
  color: rgba(255, 255, 255, 0.5);
}

.bruchtest-dashboard .ranking-item .platzierung.top-1 {
  color: #ffd700;
}

.bruchtest-dashboard .ranking-item .platzierung.top-2 {
  color: #c0c0c0;
}

.bruchtest-dashboard .ranking-item .platzierung.top-3 {
  color: #cd7f32;
}

.bruchtest-dashboard .ranking-item .ranking-name {
  flex: 1;
  font-size: 0.85rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.bruchtest-dashboard .ranking-item .ranking-punkte {
  font-size: 0.75rem;
  color: #ff3333;
  font-weight: 600;
  white-space: nowrap;
}

/* Responsive fr neues Layout */
@media (max-width: 1100px) {
  .bruchtest-dashboard .bruchtest-content.new-layout {
    grid-template-columns: 1fr;
  }

  .bruchtest-dashboard .bruchtest-right-panel {
    max-height: 300px;
  }
}

@media (max-width: 600px) {
  .bruchtest-dashboard .kategorie-dropdown-section {
    flex-direction: column;
    align-items: stretch;
    gap: 0.5rem;
  }

  .bruchtest-dashboard .kategorie-dropdown-section select {
    max-width: 100%;
  }
}
