/* Vereine List Page - Using Central CSS Variables */

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.vereine-page {
  padding: 0;
  background: transparent;
  min-height: auto;
  animation: fadeIn 0.6s ease-out;
}

/* Header - Using CSS Variables */
.vereine-page .page-header {
  text-align: center;
  margin-bottom: var(--tda-spacing-lg);
  padding: var(--tda-spacing-md) var(--tda-spacing-lg);
  background: var(--tda-bg-card-light);
  backdrop-filter: var(--tda-blur-medium);
  border-radius: var(--tda-radius-large);
  border: 1px solid var(--tda-border-primary);
  box-shadow: var(--tda-shadow-heavy);
  position: relative;
  overflow: hidden;
}

.vereine-page .page-header h1 {
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
  font-weight: 700;
  color: var(--tda-primary);
  text-shadow: var(--tda-text-glow-red-strong);
}

.vereine-page .page-header p {
  font-size: 0.95rem;
  color: var(--tda-white-70);
  margin: 0;
}

/* Statistik-Cards - Using CSS Variables */
.vereine-page .stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: var(--tda-spacing-sm);
  margin-bottom: var(--tda-spacing-lg);
}

.vereine-page .stat-card {
  background: var(--tda-blue);
  backdrop-filter: var(--tda-blur-medium);
  border-radius: var(--tda-radius-medium);
  padding: var(--tda-spacing-sm);
  display: flex;
  align-items: center;
  gap: var(--tda-spacing-sm);
  border: 1px solid var(--tda-blue-border);
  box-shadow: var(--tda-shadow-medium);
  transition: all var(--tda-transition-normal);
}

.vereine-page .stat-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--tda-shadow-red-hover);
}

.vereine-page .stat-icon {
  font-size: 2rem;
  filter: drop-shadow(0 0 10px rgba(255, 51, 51, 0.5));
}

.vereine-page .stat-content h3 {
  font-size: 1.8rem;
  margin: 0;
  font-weight: 700;
  color: var(--tda-primary);
  text-shadow: var(--tda-text-glow-red);
}

.vereine-page .stat-content p {
  margin: 0;
  color: var(--tda-white-80);
  font-size: 0.85rem;
}

/* Such- und Filter-Bereich - Using CSS Variables */
.vereine-page .controls-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--tda-spacing-lg);
  gap: var(--tda-spacing-md);
  flex-wrap: wrap;
  background: var(--tda-bg-card-light);
  backdrop-filter: var(--tda-blur-medium);
  padding: var(--tda-spacing-md);
  border-radius: var(--tda-radius-large);
  border: 1px solid var(--tda-border-primary);
  box-shadow: var(--tda-shadow-medium);
}

.vereine-page .search-container {
  flex: 1;
  max-width: 400px;
}

.vereine-page .sort-container {
  min-width: 200px;
}

.vereine-page .sort-select {
  width: 100%;
  padding: 0.9rem 1.2rem;
  border-radius: var(--tda-radius-medium);
  font-size: 0.85rem;
  transition: all var(--tda-transition-normal);
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: var(--tda-blur-medium);
  color: rgba(255, 51, 51, 0.95);
  box-shadow: var(--tda-shadow-medium);
  cursor: pointer;
  font-weight: 500;
  border: 1px solid rgba(255, 51, 51, 0.3);
}

.vereine-page .sort-select:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 51, 51, 0.5);
  color: var(--tda-primary);
}

.vereine-page .sort-select:focus {
  outline: none;
  border-color: var(--tda-primary);
  box-shadow: var(--tda-shadow-red);
  background: rgba(255, 255, 255, 0.1);
  color: var(--tda-primary);
}

.vereine-page .sort-select option {
  background: #1a1a1a;
  color: var(--tda-white);
  padding: 0.5rem;
}

.vereine-page .search-input {
  width: 100%;
  padding: 0.9rem 1.5rem;
  border: 1px solid var(--tda-border-primary);
  border-radius: var(--tda-radius-medium);
  font-size: 0.95rem;
  transition: all var(--tda-transition-normal);
  background: var(--tda-black-40);
  backdrop-filter: var(--tda-blur-light);
  color: var(--tda-white);
  box-shadow: var(--tda-shadow-medium);
}

.vereine-page .search-input::placeholder {
  color: var(--tda-white-50);
}

.vereine-page .search-input:focus {
  outline: none;
  border-color: var(--tda-primary);
  box-shadow: var(--tda-shadow-red);
  background: var(--tda-black-60);
}

.vereine-page .actions-container {
  display: flex;
  gap: var(--tda-spacing-sm);
}

.vereine-page .action-button {
  padding: 0.9rem 1.5rem;
  border: none;
  border-radius: var(--tda-radius-medium);
  font-weight: 600;
  cursor: pointer;
  transition: all var(--tda-transition-normal);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.9rem;
  box-shadow: var(--tda-shadow-medium);
}

.vereine-page .action-button.primary {
  background: var(--tda-gradient-primary);
  color: var(--tda-white);
  border: 1px solid var(--tda-border-primary-hover);
}

.vereine-page .action-button.primary:hover {
  background: var(--tda-gradient-reverse);
  transform: translateY(-2px);
  box-shadow: var(--tda-shadow-red-strong);
}

.vereine-page .action-button.secondary {
  background: var(--tda-gray-30);
  backdrop-filter: var(--tda-blur-light);
  color: var(--tda-white-80);
  border: 1px solid var(--tda-gray-50);
}

.vereine-page .action-button.secondary:hover {
  background: var(--tda-gray-50);
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(108, 117, 125, 0.3);
}

/* Vereine-Grid - Using CSS Variables */
.vereine-page .vereine-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: var(--tda-spacing-md);
  margin-bottom: var(--tda-spacing-lg);
}

.vereine-page .verein-card {
  background: var(--tda-bg-card);
  backdrop-filter: var(--tda-blur-medium);
  border-radius: var(--tda-radius-large);
  border: 1px solid var(--tda-border-primary);
  box-shadow: var(--tda-shadow-heavy);
  overflow: hidden;
  transition: all var(--tda-transition-normal);
  position: relative;
  display: flex;
  flex-direction: column;
}

.vereine-page .verein-card:hover {
  border-color: var(--tda-border-primary-hover);
}

.vereine-page .verein-card.expanded {
  box-shadow: var(--tda-shadow-red-hover);
}

/* Kompakter Header - immer sichtbar, klickbar zum Ein-/Ausklappen */
.vereine-page .verein-card-header-compact {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem;
  cursor: pointer;
  transition: background 0.2s ease;
}

.vereine-page .verein-card.expanded .verein-card-header-compact {
  border-bottom: 1px solid rgba(255, 51, 51, 0.2);
}

.vereine-page .verein-card-header-compact:hover {
  background: rgba(255, 51, 51, 0.05);
}

.vereine-page .expand-icon {
  font-size: 0.8rem;
  color: #ff3333;
  width: 20px;
  text-align: center;
  flex-shrink: 0;
}

.vereine-page .verein-name {
  font-size: 1rem;
  font-weight: 600;
  color: #ff3333;
  margin: 0;
  flex: 1;
  min-width: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.vereine-page .verein-kaempfer {
  font-size: 0.8rem;
  color: #ff6b35;
  font-weight: 600;
  flex-shrink: 0;
  padding: 0.2rem 0.5rem;
  background: rgba(255, 107, 53, 0.15);
  border-radius: 4px;
}

.vereine-page .verein-ort {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5);
  flex-shrink: 0;
}

.vereine-page .quick-actions {
  display: flex;
  gap: 0.3rem;
  flex-shrink: 0;
}

.vereine-page .quick-btn {
  padding: 0.35rem 0.5rem;
  background: rgba(255, 51, 51, 0.15) !important;
  border: 1px solid rgba(255, 51, 51, 0.3) !important;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.8rem;
  transition: all 0.2s ease;
  color: #fff !important;
}

.vereine-page .quick-btn:hover {
  background: rgba(255, 51, 51, 0.25) !important;
  border-color: #ff3333 !important;
}

/* Expandierter Inhalt - UNTERHALB des Headers */
.vereine-page .verein-card-expanded {
  padding: 1rem;
  animation: fadeIn 0.2s ease-out;
  background: transparent !important;
}

.vereine-page .verein-info-grid {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 1rem;
  background: transparent !important;
}

.vereine-page .verein-info-grid .info-item {
  display: flex !important;
  align-items: center !important;
  gap: 0.6rem !important;
  padding: 0.3rem 0 !important;
  color: rgba(255, 255, 255, 0.8) !important;
  font-size: 0.9rem !important;
  background: transparent !important;
  border: none !important;
}

.vereine-page .verein-info-grid .info-icon {
  font-size: 1rem !important;
  width: 24px !important;
  text-align: center !important;
  background: transparent !important;
  filter: none !important;
  flex-shrink: 0 !important;
}

.vereine-page .verein-info-grid .info-text {
  flex: 1 !important;
  color: rgba(255, 255, 255, 0.8) !important;
  background: transparent !important;
  word-break: break-word !important;
  white-space: normal !important;
}

.vereine-page .verein-card-actions {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.5rem;
  padding-top: 1rem;
  border-top: 1px solid rgba(255, 51, 51, 0.2);
  background: transparent !important;
}

/* Toggle Button Style */
.vereine-page .action-button.toggle {
  background: rgba(255, 51, 51, 0.15);
  color: var(--tda-primary);
  border: 1px solid rgba(255, 51, 51, 0.4);
}

.vereine-page .action-button.toggle:hover {
  background: rgba(255, 51, 51, 0.25);
  border-color: var(--tda-primary);
  transform: translateY(-2px);
}

.vereine-page .card-action-button {
  padding: 0.7rem 0.8rem;
  border: none;
  border-radius: var(--tda-radius-small);
  font-weight: 600;
  cursor: pointer;
  transition: all var(--tda-transition-normal);
  font-size: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.4rem;
  white-space: nowrap;
}

.vereine-page .card-action-button.primary {
  background: linear-gradient(135deg, #ff3333, #cc0000) !important;
  color: #fff !important;
  border: 1px solid rgba(255, 51, 51, 0.6) !important;
}

.vereine-page .card-action-button.primary:hover {
  background: linear-gradient(135deg, #ff4444, #dd1111) !important;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 51, 51, 0.4);
}

.vereine-page .card-action-button.secondary {
  background: rgba(108, 117, 125, 0.3) !important;
  color: rgba(255, 255, 255, 0.9) !important;
  border: 1px solid rgba(108, 117, 125, 0.5) !important;
}

.vereine-page .card-action-button.secondary:hover {
  background: rgba(108, 117, 125, 0.5) !important;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(108, 117, 125, 0.3);
}

.vereine-page .card-action-button.archive {
  background: rgba(108, 117, 125, 0.25) !important;
  color: rgba(255, 255, 255, 0.9) !important;
  border: 1px solid rgba(108, 117, 125, 0.5) !important;
}

.vereine-page .card-action-button.archive:hover {
  background: rgba(108, 117, 125, 0.4) !important;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(108, 117, 125, 0.4);
}

.vereine-page .card-action-button.danger {
  background: rgba(220, 53, 69, 0.25) !important;
  color: #ff6b6b !important;
  border: 1px solid rgba(220, 53, 69, 0.5) !important;
}

.vereine-page .card-action-button.danger:hover {
  background: rgba(220, 53, 69, 0.4) !important;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(220, 53, 69, 0.5);
  color: #ff8888 !important;
}

.vereine-page .card-action-button.restore {
  background: rgba(40, 167, 69, 0.25) !important;
  color: #6bff6b !important;
  border: 1px solid rgba(40, 167, 69, 0.5) !important;
}

.vereine-page .card-action-button.restore:hover {
  background: rgba(40, 167, 69, 0.4) !important;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(40, 167, 69, 0.5);
  color: #88ff88 !important;
}


/* Keine Ergebnisse - Using CSS Variables */
.vereine-page .no-results {
  text-align: center;
  padding: 4rem 2rem;
  background: var(--tda-bg-card);
  backdrop-filter: var(--tda-blur-medium);
  border-radius: var(--tda-radius-large);
  border: 1px solid var(--tda-border-primary);
  box-shadow: var(--tda-shadow-heavy);
  margin-bottom: var(--tda-spacing-lg);
}

.vereine-page .no-results-content h3 {
  color: var(--tda-primary);
  font-size: 1.6rem;
  margin-bottom: var(--tda-spacing-sm);
  text-shadow: var(--tda-text-glow-red-strong);
}

.vereine-page .no-results-content p {
  color: var(--tda-white-70);
  font-size: 1rem;
  margin: 0.8rem 0;
  line-height: 1.6;
}

.vereine-page .clear-search-button {
  background: var(--tda-gradient-primary);
  color: var(--tda-white);
  border: 1px solid var(--tda-border-primary-hover);
  padding: 0.75rem 1.5rem;
  border-radius: var(--tda-radius-small);
  cursor: pointer;
  font-weight: 600;
  margin-top: var(--tda-spacing-md);
  transition: all var(--tda-transition-normal);
  box-shadow: var(--tda-shadow-medium);
}

.vereine-page .clear-search-button:hover {
  background: var(--tda-gradient-reverse);
  transform: translateY(-2px);
  box-shadow: var(--tda-shadow-red-strong);
}

/* Loading und Error States - Using CSS Variables */
.vereine-page .loading-container,
.vereine-page .error-container {
  text-align: center;
  padding: 4rem 2rem;
  background: var(--tda-bg-card);
  backdrop-filter: var(--tda-blur-medium);
  border-radius: var(--tda-radius-large);
  border: 1px solid var(--tda-border-primary);
  box-shadow: var(--tda-shadow-heavy);
}

.vereine-page .loading-spinner {
  width: 50px;
  height: 50px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-top: 4px solid var(--tda-primary);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto var(--tda-spacing-md);
  filter: drop-shadow(0 0 10px rgba(255, 51, 51, 0.5));
}

.vereine-page .loading-container p {
  color: var(--tda-white-80);
  font-size: 1rem;
}

.vereine-page .error-container h2 {
  color: var(--tda-primary);
  margin-bottom: var(--tda-spacing-sm);
  text-shadow: var(--tda-text-glow-red-strong);
}

.vereine-page .error-container p {
  color: var(--tda-white-70);
  margin-bottom: var(--tda-spacing-md);
}

.vereine-page .retry-button {
  background: var(--tda-gradient-primary);
  color: var(--tda-white);
  border: 1px solid var(--tda-border-primary-hover);
  padding: 0.9rem 1.8rem;
  border-radius: var(--tda-radius-medium);
  cursor: pointer;
  font-weight: 600;
  transition: all var(--tda-transition-normal);
  box-shadow: var(--tda-shadow-medium);
}

.vereine-page .retry-button:hover {
  background: var(--tda-gradient-reverse);
  transform: translateY(-2px);
  box-shadow: var(--tda-shadow-red-strong);
}

/* Alphabetische Filter-Buttons im Controls-Section */
.vereine-page .letter-filter-container {
  display: flex;
  gap: 0.4rem;
  flex-wrap: wrap;
  align-items: center;
}

.vereine-page .letter-filter-btn {
  min-width: 40px;
  padding: 0.5rem 0.6rem;
  border: 1px solid rgba(108, 117, 125, 0.3);
  border-radius: var(--tda-radius-small);
  background: rgba(52, 58, 64, 0.4);
  backdrop-filter: var(--tda-blur-light);
  color: var(--tda-white-80);
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all var(--tda-transition-normal);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.vereine-page .letter-filter-btn:hover {
  background: rgba(108, 117, 125, 0.5);
  border-color: rgba(108, 117, 125, 0.5);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(108, 117, 125, 0.3);
  color: var(--tda-white);
}

.vereine-page .letter-filter-btn.active {
  background: linear-gradient(135deg, rgba(220, 53, 69, 0.8), rgba(176, 42, 55, 0.8));
  border-color: rgba(220, 53, 69, 0.9);
  color: var(--tda-white);
  box-shadow: 0 4px 16px rgba(220, 53, 69, 0.5);
  transform: translateY(-2px);
}

.vereine-page .letter-filter-btn.active:hover {
  background: linear-gradient(135deg, rgba(220, 53, 69, 0.9), rgba(176, 42, 55, 0.9));
  box-shadow: 0 6px 20px rgba(220, 53, 69, 0.6);
}


/* Archived Section Header */
.vereine-page .archived-section-header {
  text-align: center;
  margin: 3rem 0 var(--tda-spacing-lg) 0;
  padding: var(--tda-spacing-lg);
  background: rgba(108, 117, 125, 0.15);
  backdrop-filter: var(--tda-blur-medium);
  border-radius: var(--tda-radius-large);
  border: 1px solid rgba(108, 117, 125, 0.3);
  box-shadow: var(--tda-shadow-medium);
}

.vereine-page .archived-section-header h2 {
  font-size: 1.6rem;
  margin-bottom: 0.5rem;
  font-weight: 700;
  color: rgba(108, 117, 125, 1);
  text-shadow: 0 0 20px rgba(108, 117, 125, 0.5);
}

.vereine-page .archived-section-header p {
  font-size: 0.95rem;
  color: var(--tda-white-70);
  margin: 0;
}

/* Archived Cards */
.vereine-page .verein-card.archived-card {
  border: 1px solid rgba(108, 117, 125, 0.4);
  background: rgba(108, 117, 125, 0.1);
  opacity: 0.85;
}

.vereine-page .verein-card.archived-card:hover {
  opacity: 1;
  border-color: rgba(108, 117, 125, 0.6);
  box-shadow: 0 8px 32px rgba(108, 117, 125, 0.3);
}

.vereine-page .verein-card.archived-card .verein-name {
  color: rgba(108, 117, 125, 1);
  text-shadow: 0 0 15px rgba(108, 117, 125, 0.4);
}

.vereine-page .verein-card-header-compact.archived {
  background: rgba(108, 117, 125, 0.05);
}

.vereine-page .archived-date {
  font-size: 0.8rem;
  color: rgba(108, 117, 125, 0.8);
  margin-left: auto;
}

.vereine-page .quick-btn.restore {
  background: rgba(40, 167, 69, 0.15);
  border-color: rgba(40, 167, 69, 0.4);
  color: #6bff6b;
}

.vereine-page .quick-btn.restore:hover {
  background: rgba(40, 167, 69, 0.25);
  border-color: rgba(40, 167, 69, 0.6);
}

.vereine-page .quick-btn.danger {
  background: rgba(220, 53, 69, 0.15);
  border-color: rgba(220, 53, 69, 0.4);
  color: #ff6b6b;
}

.vereine-page .quick-btn.danger:hover {
  background: rgba(220, 53, 69, 0.25);
  border-color: rgba(220, 53, 69, 0.6);
}

/* Responsive Design */
@media (max-width: 768px) {
  .vereine-page {
    padding: 0;
  }

  .vereine-page .page-header {
    padding: 1rem;
  }

  .vereine-page .page-header h1 {
    font-size: 1.4rem;
  }

  .vereine-page .controls-section {
    flex-direction: column;
    align-items: stretch;
    padding: 0.75rem;
  }

  .vereine-page .search-container {
    max-width: none;
  }

  .vereine-page .actions-container {
    justify-content: center;
  }

  .vereine-page .stats-bar {
    flex-wrap: wrap;
    justify-content: center;
    gap: 0.5rem;
  }

  .vereine-page .stat-divider {
    display: none;
  }

  .vereine-page .vereine-grid {
    grid-template-columns: 1fr;
  }

  .vereine-page .verein-card-header-compact {
    flex-wrap: wrap;
  }

  .vereine-page .verein-info-grid {
    grid-template-columns: 1fr;
  }

  .vereine-page .verein-card-actions {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .vereine-page .page-header h1 {
    font-size: 1.2rem;
  }

  .vereine-page .verein-name {
    font-size: 1rem;
  }

  .vereine-page .card-action-button {
    font-size: 0.75rem;
    padding: 0.5rem;
  }
}
