/* Anmelde Dashboard - Clean Table Design */
.anmelde-dashboard {
  padding: 1rem;
  color: #ffffff;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
}

/* Stats Row */
.stats-row {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
  flex-wrap: wrap;
}

.stat-item {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 8px;
  padding: 0.75rem 1.25rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  min-width: 100px;
}

.stat-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: #fff;
}

.stat-label {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6);
  text-transform: uppercase;
}

.stat-pending .stat-value { color: #f59e0b; }
.stat-confirmed .stat-value { color: #22c55e; }
.stat-cancelled .stat-value { color: #ef4444; }

/* Filter Bar */
.filter-bar {
  display: flex;
  gap: 0.75rem;
  margin-bottom: 1rem;
  align-items: center;
  flex-wrap: wrap;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  border: 1px solid rgba(255, 51, 51, 0.15);
}

.search-input {
  flex: 1;
  min-width: 200px;
  padding: 0.6rem 1rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 6px;
  color: #fff;
  font-size: 0.9rem;
}

.search-input:focus {
  outline: none;
  border-color: #ff3333;
}

.search-input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.filter-select {
  padding: 0.6rem 1rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 6px;
  color: #fff;
  font-size: 0.85rem;
  min-width: 140px;
  cursor: pointer;
}

.filter-select:focus {
  outline: none;
  border-color: #ff3333;
}

.filter-select option {
  background: #1a1a1a;
  color: #fff;
}

.batch-btn {
  padding: 0.6rem 1rem;
  background: linear-gradient(135deg, #22c55e, #16a34a);
  border: none;
  border-radius: 6px;
  color: #fff;
  font-weight: 600;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.2s;
}

.batch-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(34, 197, 94, 0.3);
}

.result-count {
  margin-left: auto;
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.85rem;
}

/* Table Container */
.table-container {
  background: rgba(255, 255, 255, 0.02);
  border-radius: 8px;
  border: 1px solid rgba(255, 51, 51, 0.15);
  overflow: hidden;
}

/* Table */
.anmeldungen-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.9rem;
}

.anmeldungen-table thead {
  background: rgba(255, 51, 51, 0.1);
}

.anmeldungen-table th {
  padding: 0.75rem 1rem;
  text-align: left;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.9);
  border-bottom: 1px solid rgba(255, 51, 51, 0.2);
  white-space: nowrap;
}

.anmeldungen-table th.sortable {
  cursor: pointer;
  user-select: none;
}

.anmeldungen-table th.sortable:hover {
  color: #ff3333;
}

.anmeldungen-table td {
  padding: 0.65rem 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.85);
}

.anmeldungen-table tbody tr:hover {
  background: rgba(255, 51, 51, 0.05);
}

.anmeldungen-table tbody tr.selected {
  background: rgba(255, 51, 51, 0.1);
}

/* Column Widths */
.col-checkbox { width: 40px; text-align: center; }
.col-expand { width: 30px; text-align: center; }
.col-name { min-width: 150px; }
.col-verein { min-width: 120px; }
.col-kategorien { width: 140px; }
.col-gewicht { width: 80px; }
.col-level { width: 100px; }
.col-status { width: 100px; }
.col-actions { width: 80px; }

/* Expand Button */
.expand-btn {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  font-size: 0.8rem;
  padding: 0.2rem;
}

.expand-btn:hover {
  color: #ff3333;
}

/* Kategorien */
.kategorie-count {
  display: inline-block;
  background: rgba(255, 51, 51, 0.2);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 4px;
  padding: 0.15rem 0.5rem;
  font-size: 0.85rem;
  font-weight: 600;
  color: #ff6b6b;
  margin-right: 0.5rem;
}

.kategorie-preview {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.8rem;
}

/* Sub-Rows (expanded categories) */
.sub-row {
  background: rgba(255, 51, 51, 0.03);
}

.sub-row td {
  padding: 0.4rem 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.03);
}

.sub-division {
  padding-left: 2rem !important;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.kampfstil {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.8rem;
}

/* Expanded row styling */
.anmeldungen-table tbody tr.expanded {
  background: rgba(255, 51, 51, 0.08);
  border-left: 2px solid #ff3333;
}

/* Status Mixed */
.status-mixed {
  background: rgba(168, 85, 247, 0.2);
  color: #a855f7;
  border: 1px solid rgba(168, 85, 247, 0.3);
}

/* Checkbox styling */
.anmeldungen-table input[type="checkbox"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
  accent-color: #ff3333;
}

/* Name Column */
.col-name strong {
  color: #fff;
}

/* Division Badge */
.division-badge {
  display: inline-block;
  padding: 0.2rem 0.5rem;
  background: rgba(99, 102, 241, 0.2);
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: 4px;
  font-size: 0.8rem;
  color: #818cf8;
}

/* Status Badge */
.status-badge {
  display: inline-block;
  padding: 0.2rem 0.6rem;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 500;
}

.status-pending {
  background: rgba(245, 158, 11, 0.2);
  color: #f59e0b;
  border: 1px solid rgba(245, 158, 11, 0.3);
}

.status-confirmed {
  background: rgba(34, 197, 94, 0.2);
  color: #22c55e;
  border: 1px solid rgba(34, 197, 94, 0.3);
}

.status-cancelled {
  background: rgba(239, 68, 68, 0.2);
  color: #ef4444;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

/* Action Buttons */
.action-buttons {
  display: flex;
  gap: 0.3rem;
}

.action-btn {
  width: 28px;
  height: 28px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.action-btn.confirm {
  background: rgba(34, 197, 94, 0.2);
  color: #22c55e;
  border: 1px solid rgba(34, 197, 94, 0.3);
}

.action-btn.confirm:hover {
  background: rgba(34, 197, 94, 0.3);
}

.action-btn.delete {
  background: rgba(239, 68, 68, 0.2);
  color: #ef4444;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

.action-btn.delete:hover {
  background: rgba(239, 68, 68, 0.3);
}

.action-btn.small {
  width: 24px;
  height: 24px;
  font-size: 0.85rem;
}

/* Empty Row */
.empty-row {
  text-align: center;
  padding: 3rem !important;
  color: rgba(255, 255, 255, 0.5);
}

/* Loading & Error States */
.loading-state,
.error-state {
  text-align: center;
  padding: 3rem;
  color: rgba(255, 255, 255, 0.7);
}

.error-state button {
  margin-top: 1rem;
  padding: 0.6rem 1.2rem;
  background: rgba(255, 51, 51, 0.2);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 6px;
  color: #fff;
  cursor: pointer;
}

.error-state button:hover {
  background: rgba(255, 51, 51, 0.3);
}

/* Responsive */
@media (max-width: 1024px) {
  .table-container {
    overflow-x: auto;
  }

  .anmeldungen-table {
    min-width: 800px;
  }
}

@media (max-width: 768px) {
  .stats-row {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .filter-bar {
    flex-direction: column;
    align-items: stretch;
  }

  .search-input,
  .filter-select {
    width: 100%;
    min-width: auto;
  }

  .result-count {
    margin-left: 0;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .stats-row {
    grid-template-columns: 1fr;
  }

  .stat-item {
    justify-content: space-between;
  }
}
