/* Bracket Dashboard Styles - Dojo Software Design */
.bracket-dashboard {
  max-width: none;
  margin: 0;
  background: transparent;
  color: #ffffff;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  animation: fadeIn 0.6s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInUp {
  from { opacity: 0; transform: translateY(30px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

/* Overview Styles */
.bracket-overview {
  padding: 2rem;
}

.overview-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  border: 1px solid rgba(255, 215, 0, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
  animation: fadeInUp 0.8s ease-out;
}

.overview-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 215, 0, 0.1), transparent);
  animation: shimmer 3s infinite;
}

.overview-header h2 {
  margin: 0;
  font-size: 1.8rem;
  font-weight: 700;
  background: linear-gradient(135deg, #ffd700, #ff6b35, #f7931e);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 0 25px rgba(255, 215, 0, 0.8);
  position: relative;
  z-index: 2;
}

.create-bracket-btn {
  background: linear-gradient(135deg, #ffd700, #ff6b35);
  color: #1a1a2e;
  border: none;
  padding: 0.8rem 1.5rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);
  position: relative;
  z-index: 2;
}

.create-bracket-btn:hover {
  transform: translateY(-2px) scale(1.05);
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.5);
}

/* Bracket Stats */
.bracket-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

/* ========================================
   Expandable Bracket Card Styles
   ======================================== */

.brackets-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 1.5rem;
}

.bracket-card-expandable {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  border: 1px solid rgba(255, 215, 0, 0.2);
  overflow: hidden;
  transition: all 0.3s ease;
}

.bracket-card-expandable:hover {
  border-color: rgba(255, 215, 0, 0.4);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}

.bracket-card-expandable.expanded {
  border-color: rgba(255, 215, 0, 0.5);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.bracket-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem;
  cursor: pointer;
  transition: background 0.2s;
}

.bracket-card-header:hover {
  background: rgba(255, 255, 255, 0.05);
}

.bracket-card-main {
  flex: 1;
}

.bracket-card-title {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 0.5rem;
}

.bracket-card-title .expand-icon {
  color: rgba(255, 215, 0, 0.8);
  font-size: 0.8rem;
  transition: transform 0.2s;
}

.bracket-card-expandable.expanded .expand-icon {
  transform: rotate(0deg);
}

.bracket-card-title h4 {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.95);
}

.bracket-card-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.bracket-card-meta .meta-item {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.7);
}

.bracket-card-actions {
  display: flex;
  gap: 0.5rem;
}

.bracket-card-actions .btn-view {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-size: 0.85rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.bracket-card-actions .btn-view:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

/* Bracket Card Body - Teilnehmer-Tabelle */
.bracket-card-body {
  border-top: 1px solid rgba(255, 215, 0, 0.2);
  padding: 1rem 1.5rem;
  background: rgba(0, 0, 0, 0.2);
  animation: slideDown 0.3s ease-out;
}

@keyframes slideDown {
  from {
    opacity: 0;
    max-height: 0;
  }
  to {
    opacity: 1;
    max-height: 1000px;
  }
}

.loading-teilnehmer,
.no-teilnehmer {
  text-align: center;
  padding: 1.5rem;
  color: rgba(255, 255, 255, 0.6);
  font-style: italic;
}

.teilnehmer-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.9rem;
}

.teilnehmer-table th {
  text-align: left;
  padding: 0.75rem 0.5rem;
  border-bottom: 2px solid rgba(255, 215, 0, 0.3);
  color: rgba(255, 215, 0, 0.9);
  font-weight: 600;
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.teilnehmer-table td {
  padding: 0.75rem 0.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.9);
}

.teilnehmer-table tbody tr:hover {
  background: rgba(255, 255, 255, 0.05);
}

.teilnehmer-table .seed-col {
  width: 40px;
  text-align: center;
  font-weight: 700;
  color: rgba(255, 215, 0, 0.8);
}

.teilnehmer-table .name-col {
  font-weight: 600;
}

.teilnehmer-table .age-col {
  width: 80px;
  text-align: center;
}

.teilnehmer-table .date-col {
  width: 110px;
  color: rgba(255, 255, 255, 0.7);
}

.teilnehmer-table .weight-col {
  width: 80px;
  text-align: center;
  font-weight: 500;
}

.teilnehmer-table .verein-col {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
}

/* Responsive Teilnehmer-Tabelle */
@media (max-width: 768px) {
  .teilnehmer-table {
    font-size: 0.8rem;
  }

  .teilnehmer-table th,
  .teilnehmer-table td {
    padding: 0.5rem 0.25rem;
  }

  .teilnehmer-table .date-col,
  .teilnehmer-table .verein-col {
    display: none;
  }
}

/* Brackets Grid */
.brackets-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 1.5rem;
}

.bracket-card {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  transition: all 0.3s;
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.bracket-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
}

.bracket-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.bracket-header h3 {
  margin: 0;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.3rem;
  font-weight: 700;
}

.status-badge {
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  color: white;
  font-size: 0.8rem;
  font-weight: 600;
}

.bracket-info {
  margin-bottom: 1.5rem;
}

.info-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  padding: 0.3rem 0;
}

.label {
  color: rgba(255, 215, 0, 0.8);
  font-weight: 600;
}

.value {
  color: rgba(255, 255, 255, 0.9);
  font-weight: 500;
}

.bracket-actions {
  display: flex;
  gap: 0.5rem;
}

.view-btn {
  flex: 1;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  padding: 0.6rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  font-size: 0.85rem;
}

.view-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

/* Empty State */
.empty-state {
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.empty-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
  opacity: 0.6;
}

.empty-state h3 {
  color: #2d3748;
  margin: 0 0 1rem 0;
  font-size: 1.5rem;
}

.empty-state p {
  color: #718096;
  margin: 0 0 2rem 0;
}

.create-first-bracket {
  background: linear-gradient(135deg, #ffd700, #ff6b35);
  color: #1a1a2e;
  border: none;
  padding: 1rem 2rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1rem;
  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);
}

.create-first-bracket:hover {
  transform: translateY(-2px) scale(1.05);
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.5);
}

/* Bracket Details Styles */
.bracket-details {
  padding: 0.5rem;
}

.details-header {
  display: none; /* Replaced by inline styled header */
}

.back-btn {
  background: rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(255, 215, 0, 0.3);
  padding: 0.8rem 1.2rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  position: relative;
  z-index: 2;
}

.back-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  color: rgba(255, 255, 255, 0.9);
  border-color: rgba(255, 215, 0, 0.5);
}

.bracket-title h2 {
  margin: 0;
  font-size: 1.6rem;
  font-weight: 700;
  background: linear-gradient(135deg, #ffd700, #ff6b35, #f7931e);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 0 25px rgba(255, 215, 0, 0.8);
  position: relative;
  z-index: 2;
}

.bracket-type {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
  font-weight: 500;
  position: relative;
  z-index: 2;
}

.header-actions {
  display: flex;
  gap: 1rem;
  position: relative;
  z-index: 2;
}

.add-participants-btn,
.generate-btn {
  background: linear-gradient(135deg, #ffd700, #ff6b35);
  color: #1a1a2e;
  border: none;
  padding: 0.8rem 1.5rem;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);
}

.add-participants-btn:hover,
.generate-btn:hover {
  transform: translateY(-1px) scale(1.05);
  box-shadow: 0 6px 20px rgba(255, 215, 0, 0.5);
}

/* Participants Section */
.participants-section {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 2rem;
  margin-bottom: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.participants-section h3 {
  color: rgba(255, 255, 255, 0.9);
  margin: 0 0 1.5rem 0;
  font-size: 1.3rem;
}

.participants-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem;
}

.participant-item {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  padding: 1rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  transition: all 0.2s;
  border: 1px solid rgba(255, 215, 0, 0.2);
}

.participant-item:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 215, 0, 0.4);
}

.seed-number {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  padding: 0.3rem 0.6rem;
  border-radius: 6px;
  font-weight: bold;
  font-size: 0.8rem;
}

.participant-name {
  flex: 1;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.9);
}

.participant-info {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
}

/* ========================================
   ALL VALLEY KARATE CHAMPIONSHIP BRACKET
   Cobra Kai Style Tournament Tree
   ======================================== */

.allvalley-bracket {
  background: #0a0a0a !important;
  border-radius: 0 !important;
  min-height: 500px !important;
}

/* Header - Gold Banner */
.allvalley-header {
  background: linear-gradient(180deg,
    #8b7355 0%,
    #d4af37 15%,
    #f4e4bc 30%,
    #d4af37 50%,
    #b8942d 70%,
    #8b7355 100%
  );
  padding: 0.8rem 2rem 1rem;
  text-align: center;
  border-bottom: 4px solid #654321;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
}

.allvalley-header .header-subtitle {
  font-size: 0.7rem;
  letter-spacing: 6px;
  color: #654321;
  margin-bottom: 0.2rem;
  font-weight: 600;
}

.allvalley-header h1 {
  margin: 0;
  font-size: 2rem;
  font-weight: 900;
  color: #1a1a1a;
  letter-spacing: 8px;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.3);
  font-family: 'Times New Roman', serif;
}

/* Bracket Container */
.allvalley-container {
  display: flex;
  padding: 2rem;
  gap: 0;
  overflow-x: auto;
  align-items: flex-start;
}

/* Each Round Column */
.allvalley-round {
  display: flex;
  flex-direction: column;
  min-width: 180px;
}

.round-label {
  color: #d4af37;
  font-size: 0.7rem;
  font-weight: 700;
  letter-spacing: 3px;
  margin-bottom: 1.5rem;
  text-transform: uppercase;
}

.round-slots {
  display: flex;
  flex-direction: column;
}

/* Spacing between match pairs per round */
.allvalley-round[data-round="1"] .match-pair { margin-bottom: 0; }
.allvalley-round[data-round="2"] .match-pair { margin-bottom: 40px; }
.allvalley-round[data-round="3"] .match-pair { margin-bottom: 120px; }
.allvalley-round[data-round="4"] .match-pair { margin-bottom: 280px; }

/* Match Pair (2 fighters) */
.match-pair {
  cursor: pointer;
  position: relative;
}

.match-pair:hover .slot-name {
  color: #fff;
}

/* Individual Slot (1 fighter) */
.slot {
  display: flex;
  align-items: center;
  padding: 6px 0;
  position: relative;
}

/* White dot */
.slot .dot {
  width: 12px;
  height: 12px;
  background: #f5f5f5;
  border-radius: 50%;
  margin-right: 10px;
  flex-shrink: 0;
  box-shadow: 0 0 6px rgba(255, 255, 255, 0.5);
  z-index: 2;
}

.slot.winner .dot {
  background: #ffd700;
  box-shadow: 0 0 10px rgba(255, 215, 0, 0.8);
}

/* Fighter name */
.slot-name {
  font-size: 0.85rem;
  font-weight: 700;
  color: #888;
  letter-spacing: 1px;
  white-space: nowrap;
  transition: color 0.2s;
}

.slot.winner .slot-name {
  color: #fff;
}

/* The line extending from the slot */
.slot-line {
  position: absolute;
  left: 6px;
  width: calc(100% - 6px + 60px);
  height: 8px;
  display: flex;
  align-items: center;
  z-index: 1;
}

.slot-line .line-inner {
  width: 100%;
  height: 6px;
  background: linear-gradient(180deg,
    #8b4513 0%,
    #cd853f 25%,
    #daa520 50%,
    #cd853f 75%,
    #8b4513 100%
  );
  border-top: 1px solid #b22222;
  border-bottom: 1px solid #b22222;
}

/* Vertical connector between 2 fighters in a match */
.connector-v {
  position: absolute;
  right: -30px;
  top: 50%;
  transform: translateY(-50%);
  width: 6px;
  height: 60px;
  background: linear-gradient(90deg,
    #8b4513 0%,
    #cd853f 25%,
    #daa520 50%,
    #cd853f 75%,
    #8b4513 100%
  );
  border-left: 1px solid #b22222;
  border-right: 1px solid #b22222;
}

/* Champion Section */
.allvalley-champion {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 2rem;
  min-width: 180px;
}

.champion-label {
  color: #d4af37;
  font-size: 0.8rem;
  font-weight: 700;
  letter-spacing: 4px;
  margin-bottom: 1rem;
}

.champion-name {
  background: #1a1a1a;
  border: 3px solid #333;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  font-weight: 900;
  color: #555;
  letter-spacing: 2px;
  min-width: 140px;
  text-align: center;
}

.champion-name.has-winner {
  background: linear-gradient(180deg,
    #8b7355 0%,
    #d4af37 20%,
    #f4e4bc 50%,
    #d4af37 80%,
    #8b7355 100%
  );
  border-color: #d4af37;
  color: #1a1a1a;
  box-shadow: 0 0 30px rgba(212, 175, 55, 0.6);
}

/* Responsive */
@media (max-width: 768px) {
  .allvalley-container {
    flex-direction: column;
    padding: 1rem;
  }

  .allvalley-round {
    min-width: 100%;
    margin-bottom: 2rem;
  }

  .allvalley-round[data-round="1"] .match-pair,
  .allvalley-round[data-round="2"] .match-pair,
  .allvalley-round[data-round="3"] .match-pair,
  .allvalley-round[data-round="4"] .match-pair {
    margin-bottom: 20px;
  }

  .slot-line {
    display: none;
  }

  .connector-v {
    display: none;
  }

  .slot {
    background: #1a1a1a;
    padding: 10px 15px;
    border-radius: 4px;
    margin: 2px 0;
  }
}

/* Legacy Bracket Visualization (keeping for compatibility) */
.bracket-visualization {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.bracket-visualization h3 {
  color: rgba(255, 255, 255, 0.9);
  margin: 0 0 2rem 0;
  font-size: 1.4rem;
  text-align: center;
}

.bracket-tree {
  display: flex;
  gap: 2rem;
  overflow-x: auto;
  padding-bottom: 1rem;
}

.bracket-round {
  min-width: 300px;
  flex-shrink: 0;
}

.bracket-round h4 {
  color: rgba(255, 215, 0, 0.8);
  text-align: center;
  margin: 0 0 1rem 0;
  font-size: 1.1rem;
  font-weight: 600;
}

.fights-column {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.fight-box {
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 215, 0, 0.3);
  border-radius: 12px;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.3s;
  position: relative;
}

.fight-box:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 215, 0, 0.6);
  transform: scale(1.02);
}

.fight-box.completed {
  border-color: #48bb78;
  background: linear-gradient(135deg, #f0fff4, #e6fffa);
}

.fight-box.pending {
  border-color: #fbbf24;
  background: linear-gradient(135deg, #fffbeb, #fef3c7);
}

.fight-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.8rem;
}

.fight-number {
  font-weight: 600;
  color: rgba(255, 215, 0, 0.8);
  font-size: 0.9rem;
}

.completed-badge {
  font-size: 0.8rem;
}

.fight-participants {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.fighter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  font-weight: 500;
  transition: all 0.2s;
  color: rgba(255, 255, 255, 0.9);
}

.fighter.winner {
  background: linear-gradient(135deg, #48bb78, #38a169);
  color: white;
  font-weight: 700;
}

.vs {
  text-align: center;
  font-weight: bold;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.8rem;
  margin: 0.2rem 0;
}

.score {
  background: #4a5568;
  color: white;
  padding: 0.2rem 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: bold;
}

.fighter.winner .score {
  background: rgba(255, 255, 255, 0.3);
}

.winner-info {
  text-align: center;
  margin-top: 0.8rem;
  padding-top: 0.8rem;
  border-top: 1px solid rgba(72, 187, 120, 0.3);
  font-weight: 600;
  color: #48bb78;
  font-size: 0.9rem;
}

/* Modal Styles */
.modal-overlay {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  width: 100vw !important;
  height: 100vh !important;
  background: rgba(0, 0, 0, 0.85) !important;
  backdrop-filter: blur(4px);
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  z-index: 99999 !important;
  padding: 1rem;
}

.modal-content {
  background: rgba(26, 26, 46, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 215, 0, 0.3);
  border-radius: 16px;
  max-width: 600px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
}

.modal-content.large {
  max-width: 900px;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid rgba(255, 215, 0, 0.2);
}

.modal-header h3 {
  margin: 0;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.4rem;
  font-weight: 700;
}

.close-btn {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 6px;
  transition: all 0.2s;
}

.close-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.9);
}

/* Form Styles */
.create-bracket-form,
.fight-result-form {
  padding: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.form-group label {
  color: rgba(255, 215, 0, 0.8);
  font-weight: 600;
  font-size: 0.9rem;
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: 0.8rem;
  border: 2px solid rgba(255, 215, 0, 0.3);
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s;
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.9);
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.form-group textarea {
  min-height: 100px;
  resize: vertical;
}

.form-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  margin-top: 2rem;
  padding-top: 1rem;
  border-top: 1px solid rgba(255, 215, 0, 0.2);
}

.form-actions button {
  padding: 0.8rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.form-actions button[type="button"] {
  background: rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.8);
  border: none;
}

.form-actions button[type="button"]:hover {
  background: rgba(255, 255, 255, 0.3);
  color: rgba(255, 255, 255, 0.9);
}

.save-btn {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
}

.save-btn:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.save-btn:disabled {
  background: #cbd5e0;
  cursor: not-allowed;
}

/* Add Participants Modal */
.participants-modal-body {
  padding: 1.5rem;
}

.search-section {
  margin-bottom: 1.5rem;
}

.search-section input {
  width: 100%;
  padding: 0.8rem 1rem;
  border: 2px solid rgba(255, 215, 0, 0.3);
  border-radius: 10px;
  font-size: 1rem;
  transition: all 0.3s;
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.9);
}

.search-section input:focus {
  outline: none;
  border-color: rgba(255, 215, 0, 0.6);
  box-shadow: 0 0 0 3px rgba(255, 215, 0, 0.2);
  background: rgba(255, 255, 255, 0.15);
}

.search-section input::placeholder,
.form-group input::placeholder,
.form-group textarea::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.fighters-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
  max-height: 400px;
  overflow-y: auto;
}

.fighter-item {
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 215, 0, 0.3);
  border-radius: 8px;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.2s;
  text-align: center;
  color: rgba(255, 255, 255, 0.9);
}

.fighter-item:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 215, 0, 0.6);
}

.fighter-item.selected {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border-color: #667eea;
}

.fighter-name {
  font-weight: 600;
  margin-bottom: 0.3rem;
}

.fighter-info {
  font-size: 0.85rem;
  opacity: 0.8;
}

/* Fight Result Modal */
.fighters-info {
  margin-bottom: 1.5rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  border: 1px solid rgba(255, 215, 0, 0.2);
}

.fighter-option {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  margin-bottom: 1rem;
  padding: 0.8rem;
  border-radius: 6px;
  transition: all 0.2s;
}

.fighter-option:hover {
  background: rgba(255, 255, 255, 0.1);
}

.fighter-option input[type="radio"] {
  width: 18px;
  height: 18px;
  margin: 0;
}

.fighter-option label {
  cursor: pointer;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
}

.score-section,
.time-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 1rem;
}

/* Loading States */
.bracket-loading,
.bracket-error,
.loading-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.loading-spinner {
  font-size: 3rem;
  animation: spin 2s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Responsive Design */
@media (max-width: 1200px) {
  .brackets-grid {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
  
  .bracket-tree {
    gap: 1.5rem;
  }
  
  .bracket-round {
    min-width: 250px;
  }
}

@media (max-width: 768px) {
  .bracket-dashboard {
    padding: 1rem;
  }
  
  .overview-header,
  .details-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }
  
  .overview-header h2 {
    text-align: center;
    font-size: 1.5rem;
  }
  
  .bracket-stats {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  
  .brackets-grid {
    grid-template-columns: 1fr;
  }
  
  .participants-list {
    grid-template-columns: 1fr;
  }
  
  .bracket-tree {
    flex-direction: column;
    align-items: center;
  }
  
  .bracket-round {
    width: 100%;
    max-width: 400px;
  }
  
  .fighters-grid {
    grid-template-columns: 1fr;
  }
  
  .score-section,
  .time-section {
    grid-template-columns: 1fr;
  }
  
  .form-actions {
    flex-direction: column-reverse;
  }
}

@media (max-width: 480px) {
  .bracket-stats {
    grid-template-columns: 1fr;
  }
  
  .modal-content {
    margin: 1rem;
    max-height: calc(100vh - 2rem);
  }
}