/* ============================================================================
   TURNIERE VERWALTEN - Dashboard Style
   ============================================================================ */

/* =====================
   ANIMATIONS
   ===================== */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* =====================
   BASE CONTAINER
   ===================== */
.turniere-page {
  min-height: calc(100vh - 100px);
  background: #000000;
  color: #ffffff;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  padding: 1.5rem;
  animation: fadeIn 0.5s ease-out;
}

/* =====================
   PAGE HEADER
   ===================== */
.turniere-page .page-header {
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(255, 51, 51, 0.2);
}

.turniere-page .page-header h1 {
  font-size: 1.8rem;
  font-weight: 700;
  margin: 0 0 0.5rem 0;
  background: linear-gradient(135deg, #ff3333, #ff6b35);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.turniere-page .page-header p {
  color: rgba(255, 255, 255, 0.6);
  margin: 0;
  font-size: 0.95rem;
}

/* =====================
   STATS GRID
   ===================== */
.turniere-page .stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.turniere-page .stat-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 12px;
  padding: 1rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  transition: all 0.3s ease;
}

.turniere-page .stat-card:hover {
  border-color: rgba(255, 51, 51, 0.4);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 51, 51, 0.15);
}

.turniere-page .stat-icon {
  font-size: 1.5rem;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, rgba(255, 51, 51, 0.2), rgba(255, 107, 53, 0.2));
  border-radius: 10px;
}

.turniere-page .stat-content h3 {
  font-size: 1.4rem;
  font-weight: 700;
  color: #fff;
  margin: 0;
  line-height: 1.2;
}

.turniere-page .stat-content p {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6);
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* =====================
   CONTROLS SECTION
   ===================== */
.turniere-page .controls-section {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1.5rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(20px);
  border-radius: 12px;
  border: 1px solid rgba(255, 51, 51, 0.15);
  align-items: center;
}

.turniere-page .search-container {
  flex: 1;
  min-width: 200px;
}

.turniere-page .search-input {
  width: 100%;
  padding: 0.75rem 1rem;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 8px;
  color: #fff;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.turniere-page .search-input:focus {
  outline: none;
  border-color: #ff3333;
  box-shadow: 0 0 0 3px rgba(255, 51, 51, 0.1);
}

.turniere-page .search-input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.turniere-page .filter-container {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.turniere-page .filter-btn {
  padding: 0.5rem 1rem;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.turniere-page .filter-btn:hover {
  background: rgba(255, 51, 51, 0.15);
  border-color: rgba(255, 51, 51, 0.5);
  color: #fff;
}

.turniere-page .filter-btn.active {
  background: linear-gradient(135deg, #ff3333, #cc0000);
  border-color: #ff3333;
  color: #fff;
  font-weight: 600;
}

.turniere-page .actions-container {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.turniere-page .action-button {
  padding: 0.6rem 1.2rem;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.4rem;
  white-space: nowrap;
}

.turniere-page .action-button.primary {
  background: linear-gradient(135deg, #ff3333, #cc0000);
  border: 1px solid rgba(255, 51, 51, 0.5);
  color: #fff;
  box-shadow: 0 4px 15px rgba(255, 51, 51, 0.3);
}

.turniere-page .action-button.primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 51, 51, 0.4);
}

.turniere-page .action-button.secondary {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.9);
}

.turniere-page .action-button.secondary:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.3);
}

.turniere-page .action-button.danger {
  background: rgba(239, 68, 68, 0.15);
  border: 1px solid rgba(239, 68, 68, 0.3);
  color: #ef4444;
}

.turniere-page .action-button.danger:hover {
  background: rgba(239, 68, 68, 0.25);
  border-color: rgba(239, 68, 68, 0.5);
}

/* =====================
   TURNIERE GRID
   ===================== */
.turniere-page .turniere-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 1rem;
  animation: fadeInUp 0.5s ease-out;
}

/* =====================
   TURNIER CARD
   ===================== */
.turniere-page .turnier-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 12px;
  overflow: hidden;
  transition: all 0.3s ease;
}

.turniere-page .turnier-card:hover {
  border-color: rgba(255, 51, 51, 0.4);
  transform: translateY(-4px);
  box-shadow: 0 12px 30px rgba(255, 51, 51, 0.2);
}

.turniere-page .turnier-card.collapsed {
  padding: 0;
}

.turniere-page .turnier-card.expanded {
  padding: 0;
}

.turniere-page .turnier-card-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 1.25rem;
  cursor: pointer;
  transition: background 0.2s ease;
  border-bottom: 1px solid transparent;
}

.turniere-page .turnier-card-header:hover {
  background: rgba(255, 51, 51, 0.08);
}

.turniere-page .turnier-card.expanded .turnier-card-header {
  border-bottom: 1px solid rgba(255, 51, 51, 0.15);
}

.turniere-page .turnier-card-header .expand-icon {
  color: #ff3333;
  font-size: 0.8rem;
  transition: transform 0.2s ease;
}

.turniere-page .turnier-card-header .turnier-name {
  flex: 1;
  font-size: 1.05rem;
  font-weight: 600;
  color: #fff;
  margin: 0;
}

.turniere-page .turnier-card-header .turnier-datum {
  background: linear-gradient(135deg, #ff3333, #cc0000);
  color: white;
  padding: 0.25rem 0.6rem;
  border-radius: 6px;
  font-size: 0.75rem;
  font-weight: 600;
}

.turniere-page .turnier-card-header .status-badge {
  padding: 0.2rem 0.5rem;
  border-radius: 4px;
  font-size: 0.7rem;
  font-weight: 600;
  text-transform: uppercase;
}

.turniere-page .status-badge.geplant,
.turniere-page .status-badge.aktiv {
  background: rgba(34, 197, 94, 0.2);
  color: #22c55e;
  border: 1px solid rgba(34, 197, 94, 0.3);
}

.turniere-page .status-badge.laufend {
  background: rgba(59, 130, 246, 0.2);
  color: #3b82f6;
  border: 1px solid rgba(59, 130, 246, 0.3);
}

.turniere-page .status-badge.abgeschlossen,
.turniere-page .status-badge.beendet {
  background: rgba(156, 163, 175, 0.2);
  color: #9ca3af;
  border: 1px solid rgba(156, 163, 175, 0.3);
}

.turniere-page .status-badge.abgesagt {
  background: rgba(239, 68, 68, 0.2);
  color: #ef4444;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

/* =====================
   TURNIER CARD EXPANDED
   ===================== */
.turniere-page .turnier-card-body {
  padding: 1rem 1.25rem;
}

.turniere-page .turnier-info-grid {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
  margin-bottom: 1rem;
}

.turniere-page .turnier-info-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
}

.turniere-page .turnier-info-item .icon {
  font-size: 1rem;
  width: 24px;
  text-align: center;
}

.turniere-page .turnier-card-actions {
  display: flex;
  gap: 0.5rem;
  padding-top: 0.75rem;
  border-top: 1px solid rgba(255, 51, 51, 0.15);
  flex-wrap: wrap;
}

.turniere-page .card-action-btn {
  flex: 1;
  min-width: 100px;
  padding: 0.5rem 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.05);
  color: white;
  font-size: 0.85rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.4rem;
}

.turniere-page .card-action-btn:hover {
  background: rgba(255, 51, 51, 0.15);
  border-color: rgba(255, 51, 51, 0.4);
  transform: translateY(-1px);
}

.turniere-page .card-action-btn.edit:hover {
  background: rgba(168, 85, 247, 0.15);
  border-color: rgba(168, 85, 247, 0.4);
}

.turniere-page .card-action-btn.delete:hover {
  background: rgba(239, 68, 68, 0.15);
  border-color: rgba(239, 68, 68, 0.4);
  color: #ef4444;
}

/* =====================
   QUICK ACTIONS
   ===================== */
.turniere-page .quick-actions {
  display: flex;
  gap: 0.3rem;
}

.turniere-page .quick-btn {
  width: 32px;
  height: 32px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.05);
  color: white;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.turniere-page .quick-btn:hover {
  background: rgba(255, 51, 51, 0.15);
  border-color: rgba(255, 51, 51, 0.4);
  transform: scale(1.05);
}

/* =====================
   PUBLISH BADGE & BUTTON
   ===================== */
.turniere-page .publish-badge {
  padding: 0.2rem 0.5rem;
  border-radius: 4px;
  font-size: 0.7rem;
  font-weight: 600;
  text-transform: uppercase;
  white-space: nowrap;
}

.turniere-page .publish-badge.published {
  background: rgba(34, 197, 94, 0.2);
  color: #22c55e;
  border: 1px solid rgba(34, 197, 94, 0.3);
}

.turniere-page .publish-badge.unpublished {
  background: rgba(249, 115, 22, 0.2);
  color: #f97316;
  border: 1px solid rgba(249, 115, 22, 0.3);
}

.turniere-page .card-action-btn.published {
  background: rgba(34, 197, 94, 0.15);
  border-color: rgba(34, 197, 94, 0.4);
  color: #22c55e;
}

.turniere-page .card-action-btn.published:hover {
  background: rgba(249, 115, 22, 0.15);
  border-color: rgba(249, 115, 22, 0.4);
  color: #f97316;
}

.turniere-page .card-action-btn.unpublished {
  background: rgba(249, 115, 22, 0.15);
  border-color: rgba(249, 115, 22, 0.4);
  color: #f97316;
}

.turniere-page .card-action-btn.unpublished:hover {
  background: rgba(34, 197, 94, 0.15);
  border-color: rgba(34, 197, 94, 0.4);
  color: #22c55e;
}

/* =====================
   NO RESULTS
   ===================== */
.turniere-page .no-results {
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(255, 255, 255, 0.03);
  border: 1px dashed rgba(255, 51, 51, 0.3);
  border-radius: 16px;
}

.turniere-page .no-results h3 {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.3rem;
  margin: 0 0 0.5rem 0;
}

.turniere-page .no-results p {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.95rem;
  margin-bottom: 1.5rem;
}

/* =====================
   LOADING STATE
   ===================== */
.turniere-page .loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 40vh;
  padding: 3rem;
}

.turniere-page .loading-spinner {
  width: 50px;
  height: 50px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-top: 4px solid #ff3333;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

.turniere-page .loading-container p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.95rem;
}

/* =====================
   ERROR STATE
   ===================== */
.turniere-page .error-container {
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(255, 51, 51, 0.1);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 16px;
}

.turniere-page .error-container h2 {
  color: #ff6b6b;
  margin: 0 0 1rem 0;
}

.turniere-page .error-container p {
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 1.5rem;
}

.turniere-page .retry-button {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #ff3333, #cc0000);
  border: none;
  border-radius: 8px;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.turniere-page .retry-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 51, 51, 0.4);
}

/* =====================
   RESPONSIVE
   ===================== */
@media (max-width: 768px) {
  .turniere-page {
    padding: 1rem;
  }

  .turniere-page .page-header h1 {
    font-size: 1.4rem;
  }

  .turniere-page .stats-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .turniere-page .controls-section {
    flex-direction: column;
  }

  .turniere-page .search-container {
    width: 100%;
  }

  .turniere-page .turniere-grid {
    grid-template-columns: 1fr;
  }

  .turniere-page .actions-container {
    width: 100%;
    justify-content: stretch;
  }

  .turniere-page .action-button {
    flex: 1;
    justify-content: center;
  }
}
