/* ============================================================================
   WETTKAMPFER AUSWAHL MODAL - Dashboard Style
   ============================================================================ */

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 10px;
  backdrop-filter: blur(8px);
}

.modal-content {
  background: #0a0a0a;
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 16px;
  width: 100%;
  max-width: 900px;
  max-height: 90vh;
  overflow: hidden;
  box-shadow: 0 25px 50px rgba(255, 51, 51, 0.2);
  animation: modalSlideIn 0.3s ease-out;
  display: flex;
  flex-direction: column;
}

.modal-content.large {
  max-width: 1200px;
}

/* Vollbild-Modal fr Schritt 3 - volle Breite */
.modal-content.fullscreen {
  max-width: 100vw;
  max-height: 100vh;
  width: 100vw;
  height: 100vh;
  border-radius: 0;
  border: none;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-50px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* =====================
   MODAL HEADER
   ===================== */
.modal-header {
  padding: 1rem 1.5rem;
  border-bottom: 1px solid rgba(255, 51, 51, 0.2);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(135deg, rgba(255, 51, 51, 0.15), rgba(0, 0, 0, 0.5));
  gap: 20px;
  flex-wrap: wrap;
}

.modal-header.compact {
  padding: 0.75rem 1.5rem;
}

.header-left {
  flex-shrink: 0;
}

.modal-title h2 {
  margin: 0 0 2px 0;
  font-size: 1.25rem;
  font-weight: 700;
  background: linear-gradient(135deg, #ff3333, #ff6b35);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.modal-header.compact .modal-title h2 {
  font-size: 1.1rem;
}

.modal-title p {
  margin: 0;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
}

.modal-header.compact .modal-title p {
  font-size: 0.75rem;
}

.modal-close {
  background: rgba(255, 51, 51, 0.2);
  color: #ff3333;
  border: 1px solid rgba(255, 51, 51, 0.3);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.modal-close:hover {
  background: rgba(255, 51, 51, 0.4);
  border-color: #ff3333;
  transform: rotate(90deg);
}

/* =====================
   KOMPAKTER PROGRESS INDICATOR IM HEADER
   ===================== */
.progress-indicator-compact {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
  justify-content: center;
}

.step-compact {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.step-compact.active {
  border-color: rgba(255, 51, 51, 0.4);
}

.step-compact.current {
  background: rgba(255, 51, 51, 0.2);
  border-color: #ff3333;
}

.step-dot {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.7rem;
  font-weight: 700;
}

.step-compact.active .step-dot {
  background: #ff3333;
  color: #fff;
}

.step-compact.current .step-dot {
  box-shadow: 0 0 10px rgba(255, 51, 51, 0.5);
}

.step-text {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5);
  font-weight: 500;
}

.step-compact.active .step-text {
  color: rgba(255, 255, 255, 0.9);
}

.step-compact.current .step-text {
  color: #ff3333;
}

.step-line-compact {
  width: 20px;
  height: 2px;
  background: rgba(255, 51, 51, 0.3);
  border-radius: 1px;
}

/* Alte Progress Indicator ausblenden */
.progress-indicator {
  display: none;
}

.progress-step, .step-number, .step-label, .progress-line {
  display: none;
}

/* =====================
   MODAL BODY
   ===================== */
.modal-body {
  padding: 1.5rem;
  overflow-y: auto;
  flex: 1;
  background: #000;
}

.fullscreen .modal-body {
  padding: 0.75rem 1rem;
  display: flex;
  flex-direction: column;
}

.step-content h3 {
  margin: 0 0 10px 0;
  color: #fff;
  font-size: 1.3rem;
  font-weight: 600;
}

.step-content p {
  margin: 0 0 20px 0;
  color: rgba(255, 255, 255, 0.6);
  line-height: 1.5;
}

.step-content.fullscreen-step {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* =====================
   ERROR MESSAGE
   ===================== */
.error-message {
  background: rgba(239, 68, 68, 0.15);
  color: #ef4444;
  padding: 12px 16px;
  border-radius: 8px;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

.error-message button {
  background: none;
  border: none;
  color: #ef4444;
  font-weight: bold;
  cursor: pointer;
  padding: 4px;
}

/* =====================
   VEREINE GRID
   ===================== */
.vereine-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 1rem;
  margin-top: 20px;
}

.verein-card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 12px;
  padding: 1.25rem;
  transition: all 0.3s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.verein-card.selectable {
  cursor: pointer;
}

.verein-card.selectable:hover {
  border-color: rgba(255, 51, 51, 0.6);
  background: rgba(255, 51, 51, 0.1);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 51, 51, 0.2);
}

.verein-card.current {
  border-color: rgba(34, 197, 94, 0.5);
  background: rgba(34, 197, 94, 0.1);
}

.verein-info h4 {
  margin: 0 0 8px 0;
  color: #fff;
  font-size: 1.1rem;
}

.verein-info p {
  margin: 4px 0;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.9rem;
}

.select-indicator {
  font-size: 1.5rem;
  color: #ff3333;
  font-weight: bold;
}

.current-verein-info {
  margin-bottom: 30px;
}

.current-verein-info h3 {
  margin-bottom: 15px;
  color: #22c55e;
}

/* =====================
   WETTKAMPFER CONTROLS
   ===================== */
.wettkampfer-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.25rem;
  gap: 20px;
  flex-wrap: wrap;
}

.search-container {
  flex: 1;
  max-width: 350px;
}

.search-input {
  width: 100%;
  padding: 0.75rem 1rem;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 8px;
  color: #fff;
  font-size: 0.95rem;
  transition: all 0.3s ease;
}

.search-input:focus {
  outline: none;
  border-color: #ff3333;
  box-shadow: 0 0 0 3px rgba(255, 51, 51, 0.1);
}

.search-input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.selection-info {
  display: flex;
  align-items: center;
  gap: 15px;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}

.select-all-button {
  background: rgba(255, 51, 51, 0.15);
  color: #ff3333;
  border: 1px solid rgba(255, 51, 51, 0.3);
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.85rem;
  font-weight: 600;
  transition: all 0.3s ease;
}

.select-all-button:hover {
  background: rgba(255, 51, 51, 0.25);
  border-color: #ff3333;
  transform: translateY(-1px);
}

/* =====================
   WETTKAMPFER GRID
   ===================== */
.wettkampfer-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 1rem;
  max-height: 400px;
  overflow-y: auto;
  padding-right: 8px;
}

.wettkampfer-card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 12px;
  padding: 1rem;
  transition: all 0.3s ease;
  cursor: pointer;
  display: flex;
  gap: 1rem;
  align-items: flex-start;
}

.wettkampfer-card:hover {
  border-color: rgba(255, 51, 51, 0.5);
  background: rgba(255, 51, 51, 0.08);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 51, 51, 0.15);
}

.wettkampfer-card.selected {
  border-color: rgba(34, 197, 94, 0.6);
  background: rgba(34, 197, 94, 0.1);
}

.wettkampfer-checkbox {
  flex-shrink: 0;
  margin-top: 2px;
}

.wettkampfer-checkbox input[type="checkbox"] {
  width: 20px;
  height: 20px;
  accent-color: #ff3333;
  cursor: pointer;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 4px;
}

.wettkampfer-info {
  flex: 1;
}

.wettkampfer-info h4 {
  margin: 0 0 10px 0;
  color: #fff;
  font-size: 1.1rem;
  font-weight: 600;
}

.wettkampfer-details {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.detail-item {
  background: rgba(255, 51, 51, 0.1);
  border: 1px solid rgba(255, 51, 51, 0.2);
  padding: 0.3rem 0.6rem;
  border-radius: 6px;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.8);
}

/* =====================
   LOADING STATES
   ===================== */
.loading-spinner-container {
  text-align: center;
  padding: 3rem;
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-top: 4px solid #ff3333;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 15px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-spinner-container p {
  color: rgba(255, 255, 255, 0.7);
}

.no-wettkampfer {
  text-align: center;
  padding: 3rem;
  color: rgba(255, 255, 255, 0.6);
}

.no-wettkampfer p {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.no-wettkampfer button {
  background: linear-gradient(135deg, #ff3333, #cc0000);
  color: white;
  border: none;
  padding: 0.6rem 1.2rem;
  border-radius: 6px;
  cursor: pointer;
  margin-top: 10px;
  font-weight: 600;
  transition: all 0.3s ease;
}

.no-wettkampfer button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(255, 51, 51, 0.3);
}

/* =====================
   MODAL FOOTER
   ===================== */
.modal-footer {
  padding: 0.75rem 1.5rem;
  border-top: 1px solid rgba(255, 51, 51, 0.2);
  display: flex;
  justify-content: space-between;
  gap: 12px;
  background: rgba(255, 255, 255, 0.03);
}

.fullscreen .modal-footer {
  padding: 0.5rem 1rem;
}

.btn-secondary, .btn-primary {
  padding: 0.6rem 1.25rem;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 6px;
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-secondary:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
}

.btn-primary {
  background: linear-gradient(135deg, #ff3333, #cc0000);
  color: white;
  border: 1px solid rgba(255, 51, 51, 0.5);
  box-shadow: 0 4px 15px rgba(255, 51, 51, 0.3);
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 51, 51, 0.4);
}

.btn-primary:disabled, .btn-secondary:disabled {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.4);
  border-color: rgba(255, 255, 255, 0.1);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* =====================
   RESPONSIVE
   ===================== */
@media (max-width: 900px) {
  .progress-indicator-compact {
    order: 3;
    width: 100%;
    justify-content: center;
    padding-top: 8px;
    border-top: 1px solid rgba(255, 51, 51, 0.1);
    margin-top: 8px;
  }

  .step-text {
    display: none;
  }

  .step-compact {
    padding: 4px 8px;
  }
}

@media (max-width: 768px) {
  .modal-overlay {
    padding: 0;
  }

  .modal-content {
    max-height: 95vh;
  }

  .modal-header, .modal-body, .modal-footer {
    padding: 1rem;
  }

  .progress-indicator {
    padding: 1rem;
  }

  .progress-step {
    gap: 6px;
  }

  .step-number {
    width: 30px;
    height: 30px;
    font-size: 0.9rem;
  }

  .step-label {
    font-size: 0.8rem;
  }

  .progress-line {
    width: 40px;
  }

  .vereine-grid {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }

  .wettkampfer-grid {
    grid-template-columns: 1fr;
    gap: 0.75rem;
    max-height: 300px;
  }

  .wettkampfer-controls {
    flex-direction: column;
    align-items: stretch;
    gap: 1rem;
  }

  .search-container {
    max-width: none;
  }

  .selection-info {
    justify-content: space-between;
  }

  .modal-footer {
    flex-direction: column-reverse;
  }

  .btn-secondary, .btn-primary {
    width: 100%;
    justify-content: center;
  }

  .wettkampfer-details {
    flex-direction: column;
    gap: 0.4rem;
  }
}

@media (max-width: 480px) {
  .modal-title h2 {
    font-size: 1.3rem;
  }

  .modal-title p {
    font-size: 0.9rem;
  }

  .wettkampfer-card {
    padding: 0.75rem;
    gap: 0.75rem;
  }

  .wettkampfer-info h4 {
    font-size: 1rem;
  }

  .detail-item {
    font-size: 0.75rem;
    padding: 0.25rem 0.5rem;
  }
}

/* =====================
   SCROLLBAR STYLING
   ===================== */
.wettkampfer-grid::-webkit-scrollbar,
.modal-body::-webkit-scrollbar {
  width: 6px;
}

.wettkampfer-grid::-webkit-scrollbar-track,
.modal-body::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 3px;
}

.wettkampfer-grid::-webkit-scrollbar-thumb,
.modal-body::-webkit-scrollbar-thumb {
  background: rgba(255, 51, 51, 0.5);
  border-radius: 3px;
}

.wettkampfer-grid::-webkit-scrollbar-thumb:hover,
.modal-body::-webkit-scrollbar-thumb:hover {
  background: #ff3333;
}

/* =====================
   ANIMATION
   ===================== */
.wettkampfer-card.selected {
  animation: cardSelect 0.3s ease-out;
}

@keyframes cardSelect {
  0% { transform: scale(1); }
  50% { transform: scale(1.02); }
  100% { transform: scale(1); }
}

/* =====================
   STEP 3: KATEGORIEN AUSWAHL
   ===================== */
.step-description {
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 1.5rem;
  font-size: 0.95rem;
}

.kategorien-quick-select {
  background: rgba(255, 51, 51, 0.1);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 10px;
  padding: 1rem;
  margin-bottom: 1.5rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
}

.kategorien-quick-select > span {
  color: rgba(255, 255, 255, 0.8);
  font-weight: 600;
  font-size: 0.9rem;
}

.quick-select-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.quick-select-btn {
  background: rgba(255, 51, 51, 0.2);
  color: #fff;
  border: 1px solid rgba(255, 51, 51, 0.3);
  padding: 0.4rem 0.8rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.85rem;
  font-weight: 600;
  transition: all 0.3s ease;
}

.quick-select-btn:hover {
  background: rgba(255, 51, 51, 0.3);
  border-color: #ff3333;
  transform: translateY(-1px);
}

/* Wettkmpfer Kategorien Liste */
.wettkampfer-kategorien-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  max-height: 400px;
  overflow-y: auto;
  padding-right: 8px;
}

.wettkampfer-kategorie-card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 12px;
  padding: 1rem;
  transition: all 0.3s ease;
}

.wettkampfer-kategorie-card:hover {
  border-color: rgba(255, 51, 51, 0.4);
}

.wettkampfer-kategorie-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  gap: 1rem;
  flex-wrap: wrap;
}

.wettkampfer-name h4 {
  margin: 0 0 4px 0;
  color: #fff;
  font-size: 1.1rem;
  font-weight: 600;
}

.wettkampfer-meta {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.85rem;
}

.select-all-kat-btn {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 0.35rem 0.8rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: 500;
  transition: all 0.3s ease;
  white-space: nowrap;
}

.select-all-kat-btn:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.3);
}

/* Kategorien Grid */
.kategorien-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.kategorie-chip {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 8px;
  padding: 0.6rem 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 10px;
  min-width: 120px;
}

.kategorie-chip:hover {
  border-color: #ff3333;
  background: rgba(255, 51, 51, 0.15);
  transform: translateY(-1px);
}

.kategorie-chip.selected {
  background: rgba(34, 197, 94, 0.25);
  border-color: #22c55e;
}

.kategorie-chip.selected .kategorie-checkbox {
  background: #22c55e;
  border-color: #22c55e;
  color: #000;
}

.kategorie-chip.selected .kategorie-name {
  color: #22c55e;
}

.kategorie-checkbox {
  width: 22px;
  height: 22px;
  border-radius: 4px;
  background: rgba(0, 0, 0, 0.4);
  border: 2px solid rgba(255, 51, 51, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.85rem;
  font-weight: bold;
  color: #22c55e;
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.kategorie-name {
  color: #ffffff;
  font-size: 0.9rem;
  font-weight: 600;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
}

/* Kategorie Warnung */
.kategorie-warning {
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  color: #ef4444;
  padding: 0.5rem 0.75rem;
  border-radius: 6px;
  margin-top: 0.75rem;
  font-size: 0.85rem;
  display: flex;
  align-items: center;
  gap: 6px;
}

/* Division Type Gruppen */
.divisionen-by-type {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.division-type-group {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  padding: 0.75rem;
}

.division-type-header {
  color: #ff3333;
  font-size: 0.9rem;
  font-weight: 600;
  margin: 0 0 0.75rem 0;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid rgba(255, 51, 51, 0.2);
}

/* Header Actions */
.header-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.division-count {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.85rem;
}

/* Selected Summary */
.selected-divisions-summary {
  background: rgba(34, 197, 94, 0.15);
  border: 1px solid rgba(34, 197, 94, 0.3);
  color: #22c55e;
  padding: 0.5rem 0.75rem;
  border-radius: 6px;
  margin-top: 0.75rem;
  font-size: 0.85rem;
  font-weight: 600;
}

/* Responsive Kategorien */
@media (max-width: 768px) {
  .kategorien-quick-select {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }

  .wettkampfer-kategorie-header {
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
  }

  .header-actions {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
    width: 100%;
  }

  .select-all-kat-btn {
    align-self: flex-end;
  }

  .kategorie-chip {
    padding: 0.4rem 0.7rem;
  }

  .kategorie-name {
    font-size: 0.85rem;
  }

  .division-type-group {
    padding: 0.5rem;
  }
}

@media (max-width: 480px) {
  .kategorien-grid {
    gap: 0.4rem;
  }

  .kategorie-chip {
    padding: 0.35rem 0.6rem;
    gap: 6px;
  }

  .kategorie-checkbox {
    width: 18px;
    height: 18px;
  }

  .kategorie-name {
    font-size: 0.8rem;
  }
}

/* Scrollbar fr Kategorien-Liste */
.wettkampfer-kategorien-list::-webkit-scrollbar {
  width: 6px;
}

.wettkampfer-kategorien-list::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 3px;
}

.wettkampfer-kategorien-list::-webkit-scrollbar-thumb {
  background: rgba(255, 51, 51, 0.5);
  border-radius: 3px;
}

.wettkampfer-kategorien-list::-webkit-scrollbar-thumb:hover {
  background: #ff3333;
}

/* =====================
   SCHRITT 3 - VOLLBILD MIT TABS
   ===================== */

.fullscreen-step {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 500px;
}

/* Wettkmpfer Tabs */
.wettkampfer-tabs {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  padding: 0 0 10px 0;
  border-bottom: 1px solid rgba(255, 51, 51, 0.15);
  margin-bottom: 10px;
}

.wettkampfer-tab {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1px;
  padding: 8px 12px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
  min-width: 120px;
}

.wettkampfer-tab:hover {
  background: rgba(255, 51, 51, 0.1);
  border-color: rgba(255, 51, 51, 0.3);
}

.wettkampfer-tab.active {
  background: rgba(255, 51, 51, 0.2);
  border-color: #ff3333;
  box-shadow: 0 4px 15px rgba(255, 51, 51, 0.2);
}

.wettkampfer-tab.has-selection {
  border-color: rgba(34, 197, 94, 0.5);
}

.wettkampfer-tab.has-selection.active {
  border-color: #22c55e;
}

.tab-name {
  color: #fff;
  font-weight: 600;
  font-size: 0.85rem;
}

.tab-meta {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.7rem;
}

.tab-badge {
  position: absolute;
  top: -6px;
  right: -6px;
  background: #22c55e;
  color: #000;
  font-size: 0.75rem;
  font-weight: 700;
  min-width: 20px;
  height: 20px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 6px;
}

.tab-warning {
  position: absolute;
  top: -6px;
  right: -6px;
  background: #ef4444;
  color: #fff;
  font-size: 0.85rem;
  font-weight: 700;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Division Content Area */
.division-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.active-wettkampfer-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 16px;
  background: rgba(255, 51, 51, 0.08);
  border: 1px solid rgba(255, 51, 51, 0.2);
  border-radius: 8px;
  margin-bottom: 12px;
  gap: 16px;
}

.wettkampfer-details-full {
  display: flex;
  align-items: center;
  gap: 16px;
  flex-wrap: wrap;
}

.wettkampfer-details-full h3 {
  margin: 0;
  color: #fff;
  font-size: 1.1rem;
  font-weight: 600;
}

.detail-badges {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.detail-badges .badge {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 3px 8px;
  border-radius: 4px;
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.7);
}

.selection-actions {
  display: flex;
  align-items: center;
  gap: 12px;
}

.division-stats {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.8rem;
  font-weight: 500;
}

.btn-select-all {
  background: rgba(255, 51, 51, 0.15);
  color: #ff3333;
  border: 1px solid rgba(255, 51, 51, 0.3);
  padding: 5px 12px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: 600;
  transition: all 0.2s ease;
}

.btn-select-all:hover {
  background: rgba(255, 51, 51, 0.25);
  border-color: #ff3333;
}

/* Divisions Container */
.divisions-container {
  flex: 1;
  overflow-y: auto;
  padding-right: 8px;
}

.division-type-section {
  margin-bottom: 16px;
}

.type-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 8px;
  padding-bottom: 6px;
  border-bottom: 1px solid rgba(255, 51, 51, 0.15);
}

.type-header h4 {
  margin: 0;
  color: #ff3333;
  font-size: 0.95rem;
  font-weight: 600;
}

.type-count {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.85rem;
}

/* Formen Filter Tabs */
.formen-filter-tabs {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin-bottom: 12px;
  padding: 8px 12px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 6px;
}

.formen-filter-tab {
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 5px 10px;
  background: rgba(255, 255, 255, 0.06);
  border: 1px solid rgba(255, 255, 255, 0.12);
  border-radius: 15px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.75rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.formen-filter-tab:hover {
  background: rgba(255, 51, 51, 0.15);
  border-color: rgba(255, 51, 51, 0.4);
  color: #fff;
}

.formen-filter-tab.active {
  background: rgba(255, 51, 51, 0.25);
  border-color: #ff3333;
  color: #fff;
}

.filter-count {
  background: rgba(0, 0, 0, 0.3);
  padding: 2px 8px;
  border-radius: 10px;
  font-size: 0.75rem;
  font-weight: 600;
}

.formen-filter-tab.active .filter-count {
  background: rgba(255, 51, 51, 0.4);
}

.no-filter-results {
  text-align: center;
  padding: 24px;
  color: rgba(255, 255, 255, 0.5);
  font-style: italic;
}

/* Division Cards Grid */
.divisions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 8px;
}

.division-card {
  display: flex;
  gap: 10px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.division-card:hover {
  background: rgba(255, 51, 51, 0.1);
  border-color: rgba(255, 51, 51, 0.4);
  transform: translateY(-2px);
}

.division-card.selected {
  background: rgba(34, 197, 94, 0.15);
  border-color: #22c55e;
}

.division-checkbox {
  width: 20px;
  height: 20px;
  border-radius: 4px;
  background: rgba(0, 0, 0, 0.4);
  border: 2px solid rgba(255, 51, 51, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: bold;
  color: #22c55e;
  flex-shrink: 0;
  transition: all 0.2s ease;
  margin-top: 2px;
}

.division-card.selected .division-checkbox {
  background: #22c55e;
  border-color: #22c55e;
  color: #000;
}

.division-info {
  flex: 1;
  min-width: 0;
}

.division-code {
  color: #ff3333;
  font-weight: 700;
  font-size: 0.85rem;
  margin-bottom: 1px;
}

.division-name {
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.8rem;
  margin-bottom: 4px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.division-details {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
}

.division-details span {
  background: rgba(255, 255, 255, 0.06);
  padding: 1px 6px;
  border-radius: 3px;
  font-size: 0.65rem;
  color: rgba(255, 255, 255, 0.6);
}

/* Warnings */
.no-divisions-warning,
.selection-warning {
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  color: #ef4444;
  padding: 16px;
  border-radius: 8px;
  text-align: center;
  font-size: 0.95rem;
}

.selection-warning {
  margin-top: 16px;
}

/* Responsive fr Tabs */
@media (max-width: 768px) {
  .wettkampfer-tabs {
    overflow-x: auto;
    flex-wrap: nowrap;
    padding-bottom: 12px;
  }

  .wettkampfer-tab {
    min-width: 120px;
    flex-shrink: 0;
  }

  .active-wettkampfer-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .selection-actions {
    width: 100%;
    justify-content: space-between;
  }

  .divisions-grid {
    grid-template-columns: 1fr;
  }
}

/* =====================
   DIVISION TOOLBAR - Suche & Sortierung
   ===================== */
.division-toolbar {
  display: flex;
  gap: 16px;
  margin-bottom: 16px;
  padding: 12px 16px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  border: 1px solid rgba(255, 51, 51, 0.15);
  align-items: center;
  flex-wrap: wrap;
}

.division-search {
  flex: 0 0 auto;
  width: 150px;
  position: relative;
}

.division-search-input {
  width: 100%;
  padding: 8px 30px 8px 10px;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  color: white;
  font-size: 0.8rem;
}

.division-search-input:focus {
  outline: none;
  border-color: #ff3333;
  box-shadow: 0 0 0 2px rgba(255, 51, 51, 0.2);
}

.division-search .clear-search {
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  font-size: 1rem;
  padding: 4px;
}

.division-search .clear-search:hover {
  color: #ff3333;
}

.division-sort {
  display: flex;
  align-items: center;
  gap: 8px;
}

.division-sort label {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
  white-space: nowrap;
}

.sort-select {
  padding: 8px 12px;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: white;
  font-size: 0.85rem;
  cursor: pointer;
  min-width: 140px;
}

.sort-select:focus {
  outline: none;
  border-color: #ff3333;
}

.sort-select option {
  background: #1a1a1a;
  color: white;
}

/* =====================
   FAVORITEN BUTTON
   ===================== */
.favorit-btn {
  position: absolute;
  top: 8px;
  right: 8px;
  background: none;
  border: none;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.3);
  cursor: pointer;
  padding: 4px;
  z-index: 2;
  transition: all 0.2s ease;
}

.favorit-btn:hover {
  color: #ffd700;
  transform: scale(1.2);
}

.favorit-btn.active {
  color: #ffd700;
}

.division-card {
  position: relative;
}

.division-card.favorit {
  border-color: rgba(255, 215, 0, 0.3);
}

.division-card.favorit::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, #ffd700, transparent);
}

/* =====================
   BERSICHT BUTTON
   ===================== */
.btn-summary {
  padding: 8px 14px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: white;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn-summary:hover {
  background: rgba(255, 51, 51, 0.2);
  border-color: #ff3333;
}

/* =====================
   ZUSAMMENFASSUNGS-MODAL
   ===================== */
.summary-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1100;
  backdrop-filter: blur(4px);
}

.summary-modal {
  background: #0a0a0a;
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 16px;
  width: 90%;
  max-width: 700px;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 25px 50px rgba(255, 51, 51, 0.2);
  animation: modalSlideIn 0.2s ease-out;
}

.summary-header {
  padding: 16px 20px;
  border-bottom: 1px solid rgba(255, 51, 51, 0.2);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(135deg, rgba(255, 51, 51, 0.15), transparent);
}

.summary-header h3 {
  margin: 0;
  color: white;
  font-size: 1.2rem;
}

.close-summary {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.6);
  font-size: 1.3rem;
  cursor: pointer;
  padding: 4px 8px;
}

.close-summary:hover {
  color: #ff3333;
}

.summary-content {
  flex: 1;
  overflow-y: auto;
  padding: 16px 20px;
}

.summary-wettkampfer {
  margin-bottom: 20px;
  padding: 16px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.summary-wettkampfer:last-child {
  margin-bottom: 0;
}

.summary-wettkampfer-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.summary-wettkampfer-header h4 {
  margin: 0;
  color: white;
  font-size: 1rem;
}

.summary-badge {
  padding: 4px 10px;
  background: rgba(255, 51, 51, 0.2);
  border-radius: 12px;
  color: #ff6666;
  font-size: 0.8rem;
  font-weight: 500;
}

.summary-warning {
  color: rgba(255, 255, 255, 0.5);
  font-style: italic;
  font-size: 0.85rem;
  margin: 0;
}

.summary-divisions {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.summary-divisions li {
  display: flex;
  gap: 12px;
  padding: 8px 12px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.summary-code {
  color: #ff3333;
  font-weight: 600;
  min-width: 80px;
  font-size: 0.85rem;
}

.summary-name {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.85rem;
}

.summary-footer {
  padding: 16px 20px;
  border-top: 1px solid rgba(255, 51, 51, 0.2);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(0, 0, 0, 0.3);
}

.summary-total {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
}

/* Responsive fr Toolbar */
@media (max-width: 600px) {
  .division-toolbar {
    flex-direction: column;
    align-items: stretch;
  }

  .division-search {
    width: 100%;
  }

  .division-sort {
    width: 100%;
    justify-content: space-between;
  }

  .sort-select {
    flex: 1;
  }
}

/* =====================
   NO SEARCH RESULTS
   ===================== */
.no-search-results {
  padding: 20px;
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
  font-style: italic;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  margin: 10px 0;
}

/* =====================
   MAIN CATEGORY TABS
   ===================== */
.main-category-tabs {
  display: flex;
  gap: 8px;
  margin-bottom: 12px;
  padding: 8px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  border: 1px solid rgba(255, 51, 51, 0.2);
  overflow-x: auto;
  flex-wrap: wrap;
}

.main-category-tab {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 10px 16px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s ease;
  white-space: nowrap;
}

.main-category-tab:hover {
  background: rgba(255, 51, 51, 0.15);
  border-color: rgba(255, 51, 51, 0.3);
  color: white;
}

.main-category-tab.active {
  background: linear-gradient(135deg, rgba(255, 51, 51, 0.3), rgba(255, 51, 51, 0.15));
  border-color: #ff3333;
  color: white;
  box-shadow: 0 0 15px rgba(255, 51, 51, 0.2);
}

.main-category-tab .cat-icon {
  font-size: 1.1rem;
}

.main-category-tab .cat-label {
  font-weight: 500;
}

.main-category-tab .cat-count {
  padding: 2px 8px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  font-size: 0.75rem;
  font-weight: 600;
}

.main-category-tab.active .cat-count {
  background: rgba(255, 51, 51, 0.4);
}

@media (max-width: 768px) {
  .main-category-tabs {
    flex-wrap: nowrap;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }

  .main-category-tab {
    flex-shrink: 0;
    padding: 8px 12px;
  }

  .main-category-tab .cat-label {
    display: none;
  }

  .main-category-tab .cat-icon {
    font-size: 1.3rem;
  }
}

/* =====================
   WETTKMPFER BEARBEITEN
   ===================== */
.wettkampfer-card {
  position: relative;
}

.wettkampfer-edit-btn {
  position: absolute;
  top: 8px;
  right: 8px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  padding: 6px 10px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s ease;
  z-index: 2;
}

.wettkampfer-edit-btn:hover {
  background: rgba(255, 51, 51, 0.3);
  border-color: #ff3333;
  transform: scale(1.1);
}

/* Edit Modal */
.edit-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1200;
  backdrop-filter: blur(4px);
}

.edit-modal {
  background: #0a0a0a;
  border: 1px solid rgba(255, 51, 51, 0.3);
  border-radius: 16px;
  width: 90%;
  max-width: 550px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 25px 50px rgba(255, 51, 51, 0.2);
  animation: modalSlideIn 0.2s ease-out;
}

.edit-modal-header {
  padding: 16px 20px;
  border-bottom: 1px solid rgba(255, 51, 51, 0.2);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(135deg, rgba(255, 51, 51, 0.15), transparent);
}

.edit-modal-header h3 {
  margin: 0;
  color: white;
  font-size: 1.2rem;
}

.close-edit {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.6);
  font-size: 1.3rem;
  cursor: pointer;
  padding: 4px 8px;
}

.close-edit:hover {
  color: #ff3333;
}

.edit-modal-body {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

.edit-form-row {
  display: flex;
  gap: 16px;
  margin-bottom: 16px;
}

.edit-form-group {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.edit-form-group.full-width {
  flex: 1 1 100%;
}

.edit-form-group label {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
  font-weight: 500;
}

.edit-form-group input,
.edit-form-group select {
  padding: 10px 12px;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: white;
  font-size: 0.95rem;
}

.edit-form-group input:focus,
.edit-form-group select:focus {
  outline: none;
  border-color: #ff3333;
  box-shadow: 0 0 0 2px rgba(255, 51, 51, 0.2);
}

.edit-form-group select option {
  background: #1a1a1a;
  color: white;
}

.edit-modal-footer {
  padding: 16px 20px;
  border-top: 1px solid rgba(255, 51, 51, 0.2);
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  background: rgba(0, 0, 0, 0.3);
}

@media (max-width: 500px) {
  .edit-form-row {
    flex-direction: column;
    gap: 12px;
  }
}

/* =====================
   SIDEBAR LAYOUT
   ===================== */
.sidebar-layout {
  display: grid;
  grid-template-columns: 220px 1fr;
  gap: 0;
  height: 100%;
  min-height: 0;
}

.wettkampfer-sidebar {
  background: rgba(0, 0, 0, 0.4);
  border-right: 1px solid rgba(255, 51, 51, 0.2);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.sidebar-header {
  padding: 12px 16px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 51, 51, 0.1);
}

.sidebar-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 12px 16px;
  background: transparent;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: left;
  position: relative;
  width: 100%;
}

.sidebar-item:hover {
  background: rgba(255, 51, 51, 0.1);
}

.sidebar-item.active {
  background: rgba(255, 51, 51, 0.2);
  border-left: 3px solid #ff3333;
}

.sidebar-item.has-selection .sidebar-name {
  color: #4ade80;
}

.sidebar-name {
  color: white;
  font-weight: 500;
  font-size: 0.9rem;
  margin-bottom: 2px;
}

.sidebar-meta {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.75rem;
}

.sidebar-status {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.sidebar-badge {
  padding: 2px 8px;
  background: #4ade80;
  color: #000;
  border-radius: 10px;
  font-size: 0.7rem;
  font-weight: 600;
}

.sidebar-warning {
  color: #ff6b6b;
  font-weight: bold;
  font-size: 1rem;
}

.division-main-area {
  overflow-y: auto;
  padding: 0;
  display: flex;
  flex-direction: column;
}

/* Responsive Sidebar */
@media (max-width: 900px) {
  .sidebar-layout {
    grid-template-columns: 160px 1fr;
  }

  .sidebar-name {
    font-size: 0.8rem;
  }
}

@media (max-width: 600px) {
  .sidebar-layout {
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr;
  }

  .wettkampfer-sidebar {
    flex-direction: row;
    overflow-x: auto;
    border-right: none;
    border-bottom: 1px solid rgba(255, 51, 51, 0.2);
  }

  .sidebar-header {
    display: none;
  }

  .sidebar-item {
    flex-direction: row;
    align-items: center;
    gap: 8px;
    min-width: 150px;
    border-bottom: none;
    border-right: 1px solid rgba(255, 255, 255, 0.05);
  }

  .sidebar-item.active {
    border-left: none;
    border-bottom: 3px solid #ff3333;
  }

  .sidebar-status {
    position: static;
    transform: none;
  }
}

/* =====================
   KOMPAKTE ELEMENTE
   ===================== */

/* Sidebar Details */
.sidebar-details {
  display: flex;
  gap: 6px;
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.7rem;
  margin-top: 2px;
}

.sidebar-details span {
  padding: 1px 4px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 3px;
}

.sidebar-item .sidebar-badge {
  position: absolute;
  top: 8px;
  right: 8px;
}

.sidebar-item .sidebar-warning {
  position: absolute;
  top: 8px;
  right: 8px;
}

/* Kompakte Aktionsleiste */
.compact-action-bar {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 16px;
  background: rgba(255, 51, 51, 0.1);
  border-bottom: 1px solid rgba(255, 51, 51, 0.2);
}

.compact-action-bar .division-stats {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
}

.btn-small {
  padding: 5px 10px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 5px;
  color: white;
  font-size: 0.8rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn-small:hover {
  background: rgba(255, 51, 51, 0.2);
  border-color: #ff3333;
}
